.ChooseUs {
  padding: 100px 0;
  position: relative;
  background: #52339b;
  @media screen and (max-width: 767.5px) {
    padding: 50px 0;
  }
  &::after {
    content: "";
    position: absolute;
    top: -14px;
    left: 0;
    width: 100%;
    height: 68px;
    z-index: 0;
    background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/BottomLine_52339b.svg?v=1662028205);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
    transform: rotateX(180deg);
    @media screen and (max-width: 767.5px) {
      content: "";
      position: absolute;
      top: -22px;
      left: 0;
      width: 100%;
      height: 68px;
      z-index: 0;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/Mobile-Border-Illustration.svg?v=1661237073);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center top;
      transform: rotateX(0);
    }
  }
  &::before {
    content: "";
    position: absolute;
    bottom: -14px;
    left: 0;
    width: 100%;
    height: 68px;
    z-index: 0;
    background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/BottomLine_52339b.svg?v=1662028205);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
    transform: rotateX(180deg);
    @media screen and (max-width: 767.5px) {
      bottom: -17px;
      transform: rotateX(0);
      background-position: left;
    }
  }
  &__Container {
    margin: 0 auto;
    @media screen and (max-width: 319px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 320px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 575px) {
      max-width: 480px;
      padding: 0;
    }
    @media screen and (min-width: 600px) {
      max-width: 540px;
    }
    @media screen and (min-width: 768px) {
      max-width: 690px;
    }
    @media screen and (min-width: 992px) {
      max-width: 870px;
    }
    @media screen and (min-width: 1199px) {
      max-width: 1024px;
    }
    @media screen and (min-width: 1300px) {
      max-width: 1199px;
    }
    @media screen and (min-width: 1440px) {
      max-width: 1240px;
    }
  }
  &__Layout-main {
    display: flex;
    align-items: center;
    @media screen and (max-width: 1199px) {
      flex-direction: column;
      row-gap: 30px;
    }
  }
  &__Icons {
    display: block;

    @media screen and (max-width: 767.5px) {
      width: 60px;
      height: 60px;
    }
  }
  &__Details {
    display: flex;
    column-gap: 20px;
    align-items: center;
    margin-bottom: 18px;

    @media (max-width: 1024px) {
      column-gap: 10px; 
    }
  }
  &__Layout-details {
    display: flex;
    flex-direction: column;
    row-gap: 15px;
    flex: 0 0 40%;
    max-width: 40%;
    @media screen and (max-width: 1199px) {
      flex: 0 0 100%;
      max-width: 100%;
    }
  }
  &__Content {
    &:not(:last-child) {
      padding: 0 0 30px 0;
      border-bottom: 1.5px solid #ffffff40;
      @media screen and (max-width: 767.5px) {
        padding: 0 0 20px 0;
      }
    }
  }
  &__Title {
    color: #ffffff;
    font-family: Interstate;
    font-size: 21px;
    letter-spacing: 0.5px;
    line-height: 30px;
    margin: 0;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.3px;
      line-height: 27.2px;
    }
  }
  &__Description {
    color: #ffffff;
    font-family: Interstate Cond;
    font-size: 16px;
    letter-spacing: 0.4px;
    line-height: 30px;
    margin: 0;
    @media screen and (max-width: 767.5px) {
      font-size: 14px;
      letter-spacing: 0.5px;
      line-height: 25.2px;
    }
  }
  &__Images {
    border-radius: 15px;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__Layout-image {
    flex: 0 0 60%;
    max-width: 60%;
    display: flex;
    @media screen and (max-width: 1199px) {
      flex: 0 0 100%;
      max-width: 100%;
      order: -1;
      justify-content: space-between;
    }
    @media screen and (max-width: 767.5px) {
      display: none;
    }
  }
  &__ImgWrap {
    flex: 0 0 50%;
    max-width: calc(50% - 50px);
    margin-left: 50px;
    @media screen and (max-width: 1199px) {
      margin: 0;
      max-width: calc(50% - 25px);
    }
  }
}
