@font-face {
  font-family: "Interstate";
  src: url('{{ "Interstate-light.ttf" | asset_url }}');
  font-weight: 300;
}
@font-face {
  font-family: "Interstate";
  src: url('{{ "Interstate-regular.ttf" | asset_url }}');
  font-weight: 400;
}
@font-face {
  font-family: "Interstate";
  src: url('{{ "Interstate-bold.ttf" | asset_url }}');
  font-weight: 700;
}

@font-face {
  font-family: "Interstate Cond";
  src: url('{{ "interstate-cond-light.ttf" | assets_url }}');
  font-weight: 300;
}
@font-face {
  font-family: "Interstate Cond";
  src: url('{{ "interstate-cond-regular.ttf" | assets_url }}');
  font-weight: 400;
}
@font-face {
  font-family: "Interstate Cond";
  src: url('{{ "interstate-cond-bold.ttf" | assets_url }}');
  font-weight: 700;
}
@font-face {
  font-family: "Gazpacho";
  src: url('{{ "Gazpacho-Black.otf" | assets_url }}');
  font-weight: 400;
}



body {
  color: black;
  font-family: "Interstate";
  font-size: 18px;
  line-height: 1.9;
  letter-spacing: 0.7px;

  @media (max-width: 1024px) {
    font-size: 16px;
    letter-spacing: 0.5px;
  }
}

.h0, h1, h2, h3, h4, h5, h6 {
  font-family: 'Gazpacho';
  font-weight: 400;
}

.h0 {
  font-size: 78px;
  line-height: 1.1;
  letter-spacing: 0px;

  @media (max-width: 1024px) {
    font-size: 48px;
    line-height: 1.1;
    letter-spacing: 1px;
  }
}

h1,
.h1 {
  font-size: 65px;
  line-height: 1.3;
  letter-spacing: 1.8px;

  @media (max-width: 1024px) {
    font-size: 40px;  
    line-height: 1.1;
    letter-spacing: 0.9px;
  }
}

h2,
.h2 {
  font-size: 50px;
  line-height: 1.5;
  letter-spacing: 2.5px;

  @media (max-width: 1024px) {
    font-size: 32px;  
    line-height: 1.3;
    letter-spacing: 0.7px;
  }
}

h3,
.h3 {
  font-size: 38px;
  line-height: 1.3;
  letter-spacing: 1px;

  @media (max-width: 1024px) {
    font-size: 25px;  
    line-height: 1.5;
    letter-spacing: 1px;
  }
}

h4,
.h4 {
  font-size: 23px;
  line-height: 1.4;
  letter-spacing: 0.5px;

  @media (max-width: 1024px) {
    font-size: 18px;  
    line-height: 1.6;
    letter-spacing: 0.6px;
  }
}

h5,
.h5 {
  font-family: 'Interstate';
  font-size: 21px;
  line-height: 1.4;
  letter-spacing: 0.5px;

  @media (max-width: 1024px) {
    font-size: 16px;  
    line-height: 1.7;
    letter-spacing: 0.3px;
  }
}

h6,
.h6 {
  font-size: 18px;
  line-height: 1.7;
  letter-spacing: 0.45px;
}

.text-small {
  font-family: "Interstate Cond";
  font-size: 16px;
  line-height: 1.8;
  letter-spacing: 0.5px;

  @media (max-width: 1024px) {
    font-size: 14px;
  }
}

.heading-stars {
  display: flex;
  justify-content: center;
  position: relative;

  &::before {
    background: url('stars-violet-3.png') center/cover;
    content: "";
    display: block;
    flex: 0 0 50px;
    width: 50px;
    height: 50px;

    @media (max-width: 1024px) {
      flex: 0 0 35px;
      width: 35px;
      height: 35px;
    }
  }

  &::after {
    background: url('stars-violet-3.png') center/cover;
    content: "";
    display: block;
    flex: 0 0 50px;
    margin-left: 5px;
    transform: rotate(180deg);
    width: 50px;
    height: 50px;

    @media (max-width: 1024px) {
      flex: 0 0 35px;
      width: 35px;
      height: 35px;
    }
  }

  &-yellow {
    &::before {
      background: url('stars-yellow-3.png') center/cover;
    }

    &::after {
      background: url('stars-yellow-3.png') center/cover;
    }

    @media (max-width: 599px) {
      max-width: calc(100% - 70px);
      margin-left: auto;
      margin-right: auto;
    }
  }

  &-left {
    &::after {
      opacity: 0;
    }
  }

  &-right {
    &::before {
      opacity: 0;
    }
  }

  &-space {
    &::before {
      margin-right: 60px;
      
      @media (max-width: 599px) {
        margin-right: 10px;  
      }
    }

    &::after {
      margin-left: 60px;

      @media (max-width: 599px) {
        margin-left: 10px;  
      }
    }
  }
}