.FindProduct {
  padding: 120px 100px;

  @media (max-width: 1024px) {
    padding: 70px 0;  
  }
  &__FullContainer {
    width: 100%;
  }
  &__container {
    margin-top: 30px;
  }

  &__Head {
    color: #000000;
    font-family: "Gazpacho";
    font-size: 65px;
    font-weight: bold;
    letter-spacing: 1.8px;
    line-height: 80px;
    text-align: center;
    margin: 0 auto;
    padding: 0 0 20px 0;
    position: relative;
    max-width: fit-content;
    @media screen and (max-width: 640px) {
      max-width: 230px;
    }
    &::before {
      content: "";
      position: absolute;
      width: 60px;
      height: 60px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/a1.png?v=1661325220);
      background-repeat: no-repeat;
      background-size: contain;
      top: 6px;
      left: -60px;
      @media screen and (max-width: 767.5px) {
        top: -24px;
        left: -26px;
      }
      @media screen and (max-width: 640px) {
        top: -15px;
      }
    }
    &::after {
      content: "";
      position: absolute;
      width: 60px;
      height: 60px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/a2.png?v=1661325220);
      background-repeat: no-repeat;
      background-size: contain;
      top: -13px;
      right: -54px;
      @media screen and (max-width: 767.5px) {
        top: -30px;
        right: -42px;
      }
      @media screen and (max-width: 640px) {
        top: -13px;
        right: -35px;
      }
    }
    @media screen and (max-width: 767.5px) {
      font-size: 40px;
      letter-spacing: 0.9px;
      line-height: 44px;
      padding: 0 15px 15px 15px;
    }
  }
  &__Subhead {
    color: #000000;
    font-family: Interstate Cond;
    font-size: 18px;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    text-align: center;
    margin: 0 auto;
    max-width: 505px;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
      max-width: 100%;
      padding: 0 15px;
    }
  }
  &__Layout-main {
    display: flex;
    flex-wrap: wrap;
    padding: 100px 0 0 100px;
    column-gap: 62px;
    justify-content: space-between;
    @media screen and (max-width: 992px) {
      padding: 50px 0 0 50px;
    }
    @media screen and (max-width: 767.5px) {
      flex-direction: column;
      padding: 30px 0 0 0;
    }
  }
  &__Layout-inner {
    flex: 0 0 35%;
    .FindProduct__City {
      border: 2px solid #000000;
      border-radius: 5px;
      background-color: #fefaee;
      max-width: 292px;
      padding: 15px 0 15px 20px;
      display: block;
      height: 60px;
      @media screen and (max-width: 767.5px) {
        max-width: calc(100% - 30px);
        margin: 0 auto;
      }
    }
  }
  &__Map-mobile {
    @media screen and (min-width: 768px) {
      display: none;
    }
    @media screen and (max-width: 767.5px) {
      display: block;
      iframe {
        width: 100%;
        height: 500px;
      }
    }
  }
  &__Map {
    max-width: calc(100% - 35% - 62px);
    flex: 0 0 calc(65% - 62px);
    iframe {
      height: 100%;
      width: 100%;
    }
    @media screen and (max-width: 767.5px) {
      display: none;
    }
  }
  &__Store {
    @media screen and (max-width: 767.5px) {
      padding: 40px 15px 0 15px;
    }
  }
  &__Store-Details {
    border-bottom: 0.5px solid #00000066;
    padding: 30px 0 30px 0;
    @media screen and (max-width: 767.5px) {
      padding: 15px 0;
    }
  }
  &__Current-Location {
    color: #52339b;
    font-family: Interstate;
    font-size: 16px;
    letter-spacing: 0.8px;
    line-height: 30.4px;
    text-decoration: underline;
    text-decoration-color: #52339b;
    text-underline-offset: 3px;
    text-transform: uppercase;
    margin: 40px 0 30px 0;
    display: flex;
    @media screen and (max-width: 767.5px) {
      margin: 45px 0 70px 0;
      justify-content: center;
    }
  }
  &__StoreName {
    color: #000000;
    font-family: Interstate;
    font-size: 21px;
    letter-spacing: 0.3px;
    line-height: 29.4px;
    margin: 0;
    padding: 0 0 10px 0;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.3px;
      line-height: 27.2px;
      padding: 0 0 5px 0;
    }
  }
  &__Address {
    color: #000000;
    font-family: Interstate Cond;
    font-size: 16px;
    letter-spacing: 0.5px;
    line-height: 28.8px;
    margin: 0;
    padding: 0 0 10px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 88px;
    @media screen and (max-width: 1200px) {
      column-gap: 35px;
    }
    @media screen and (max-width: 992px) {
      column-gap: 10px;
    }
    @media screen and (max-width: 767.5px) {
      font-size: 14px;
      letter-spacing: 0.44px;
      line-height: 24px;
      padding: 0 0 5px 0;
    }
    span {
      border-top: 8px solid transparent;
      border-bottom: 8px solid transparent;
      border-left: 8px solid black;
      height: 8px;
    }
  }
  &__Direction {
    text-decoration: underline;
    text-decoration-color: #000000;
    text-underline-offset: 3px;
    color: #000000;
    font-family: Interstate;
    font-size: 14px;
    letter-spacing: 0.5px;
    line-height: 25.2px;
    @media screen and (max-width: 767.5px) {
      font-size: 12px;
      letter-spacing: 0.5px;
      line-height: 22.8px;
    }
  }
}
