.SignIn {
  padding: 105px 0 104px 0;
  background: #FFFAED;
  overflow: hidden;
  @media screen and (max-width: 767.5px) {
    padding: 83px 0 62px 0;
  }
  &__Container {
    max-width: 100%;
  }
  &__Head {
    text-align: center;
    color: #000000;
    font-family: "Gazpacho";
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 2.5px;
    line-height: 75px;
    margin: 0 auto;
    width: fit-content;
    padding: 0 0 20px 0;
    position: relative;
    &::after{
      content: "";
      position: absolute;
      top: -43px;
      right: -101px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/head-bg.png?v=1660660371);
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      width: 121px;
      height: 100px;
      transform: rotate(70deg);
    @media screen and (max-width: 767.5px) {
      width: 47px;
      height: 39px;
      top: -28px;
      right: -24px;
    }
    }
    @media screen and (max-width: 767.5px) {
        font-size: 32px;
        letter-spacing: 0.7px;
        line-height: 41.6px;
        padding:  0 0 27px 0;
    }
  }
  &__Layout-main {
    max-width: 600px;
    width: 100%;
    margin: 0 auto;
    @media screen and (max-width: 767.5px){
      padding: 0 15px;
    }
  }
  &__Layout-inner {
    label {
      color: #000000;
      font-family: Interstate Cond;
      font-size: 18px;
      letter-spacing: 0.7px;
      line-height: 34.2px;
      padding: 0 0 4px 0;
      display: block;
      @media screen and (max-width: 767.5px) {
        font-size: 16px;
        letter-spacing: 0.5px;
        line-height: 30.4px;
      }
    }
  }
  &__Layout-inner {
    input {
      border: 2px solid #000000 !important;
      border-radius: 3px;
      background-color: #ffffff;
      margin: 0 0 20px 0;
      padding: 10px;
      width: 100%;
      outline: none;
      height: 52px;
      @media screen and (max-width: 767.5px) {
        margin: 0 0 14px 0;
        padding: 9px;
        height: 42px;
      }
    }
  }
  &__Forgot-password{
    color: #000000;
    font-family: Interstate Cond;
    font-size: 16px;
    letter-spacing: 0.5px;
    line-height: 28.8px;
  }
  &__Buttons{
    display: flex;
    justify-content: center;
    padding: 40px 0 0 0;
    @media screen and (max-width: 767.5px) {
        padding: 30px 0 0 0;
    }
  }
  &__SignIn-btn{
    margin:  0 59px 0 0;
    width: auto;
    height: 50px;
    padding: 0 58px;
    @media screen and (max-width: 767.5px) {
        font-size: 12px;
        letter-spacing: 0.5px;
        line-height: 22px;
        padding: 9px 42px;
        margin: 0 15px 0 0;
        height: 42px;
    }
  }
  &__SignUp-btn{
    width: auto;
    border-radius: 3px;
    height: 50px;
    padding: 0 58px;
    @media screen and (max-width: 767.5px) {
        font-size: 12px;
        letter-spacing: 0.5px;
        line-height: 22px;
        padding: 9px 40px;
        height: 42px;
    }
  }
}
