.title-grid {
  background: $Color--gray;
  overflow: hidden;
  padding-top: 20px;
  padding-bottom: 40px;

  &__container {
    @include make-container;
  }

  &__list {
    display: flex;
    justify-content: space-around;
    position: relative;

    @media (max-width: 767px) {
      justify-content: flex-start;  
    }

    &-item {
      flex-shrink: 0;

      &-title {
        font-family: 'Interstate Cond';
        text-transform: uppercase;
        white-space: nowrap;
      }
    }
  }
}