.wholesale-signup {
  padding-top: 100px;
  padding-bottom: 100px;

  &__container {

  }

  &__title {
    text-align: center;
  }

  &__subtitle {
    font-family: 'Interstate Cond';
    font-weight: 700;
    max-width: 740px;
    margin: 20px auto 0;
    text-align: center;
  }

  &__copy {
    font-family: 'Interstate Cond';
    font-weight: 300;
    max-width: 740px;
    margin: 20px auto 40px;
    text-align: center;
  }

  form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px 30px;
    max-width: 960px;
    margin: 0 auto;

    @media (max-width: 599px) {
      grid-template-columns: repeat(1, 1fr);  
    }
  }

  input[type=submit] {
    @extend .btn;

    @media (max-width: 1024px) {
      margin-top: 10px;  
    }
  }
}

@media (max-width: 599px) {
  .grecaptcha-badge {
    margin: 0 auto;
  }

  .hs-submit {
    .actions {
      text-align: center;
    }
  }
}