.ContactForm {
  padding: 100px 0;
  background: #fefaee;
  @media screen and (max-width: 767.5px) {
    padding: 180px 0 100px 0;
  }
  @media screen and (max-width: 767.5px) {
    padding: 134px 0 70px 0;
  }
  &__container {
    
  }

  &__content {
    max-width: 760px;
    margin: 0 auto;
  }
  &__Head {
    color: #000000;
    font-family: "Gazpacho";
    font-size: 65px;
    font-weight: bold;
    letter-spacing: 1.8px;
    line-height: 80px;
    text-align: center;
    margin: 0 auto;
    padding: 0 0 20px 0;
    max-width: fit-content;
    position: relative;
    &:after {
      content: "";
      position: absolute;
      width: 45px;
      height: 60px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/a1.png?v=1661325220);
      background-repeat: no-repeat;
      background-size: contain;
      top: -10px;
      right: -44px;
      @media screen and (max-width: 767.5px) {
        top: -28px;
      }
      @media screen and (max-width: 575.5px) {
        top: -22px;
        right: -37px;
        background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/Purple_Star_Group.png?v=1662014428);
        background-repeat: no-repeat;
        background-size: contain;
        transform: rotate(-30deg);
        width: 45px;
        height: 45px;
      }
    }
    &:before {
      content: "";
      position: absolute;
      width: 45px;
      height: 60px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/Purple_Star_Group.png?v=1662014428);
      background-repeat: no-repeat;
      background-size: contain;
      top: -14px;
      left: -35px;
      @media screen and (max-width: 767.5px) {
        top: -35px;
      }
      @media screen and (max-width: 575.5px) {
        top: -25px;
        left: -31px;
      }
    }
    @media screen and (max-width: 767.5px) {
      font-size: 40px;
      letter-spacing: 0.9px;
      line-height: 44px;
      padding: 0 0 16px 0;
    }
  }
  &__Details {
    color: #000000;
    font-family: 'Interstate Cond';
    font-size: 18px;
    font-weight: 300;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    text-align: center;
    margin: 0 auto;
    text-align: center;
    padding: 0 0 40px 0;
    max-width: 420px;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
      padding: 0 0 30px 0;
      max-width: 445px;
    }
  }

  form {
    display: grid;
    gap: 20px;
  }

  .hs-input {
    background: white;
  }

  input[type=submit] {
    @extend .btn;

    @media (max-width: 1024px) {
      margin-top: 10px;  
    }
  }
}
