.RetaileBanner{
    padding:121px 0 116px 0;
    background: #54349a;
    position: relative;
    &::before {
        content: "";
        position: absolute;
        bottom: -66px;
        left: 0;
        width: 100%;
        height: 96px;
        z-index: 0;
        background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/BackgroundLine_52339b.svg?v=1662031835);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center bottom;
    }
    &::after {
        content: "";
        position: absolute;
        top: -14px;
        left: 0;
        width: 100%;
        height: 68px;
        z-index: 0;
        background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/BottomLine_52339b.svg?v=1662028205);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center top;
    }
    @media screen and (max-width: 767.5px) {
        padding: 80px 0 104px 0 !important;
    }
    &__Container{
        margin: 0 auto;
        @media screen and (max-width: 319px) {
            padding: 0 15px;
        }
        @media screen and (min-width: 320px) {
            max-width: 290px;
        }
        @media screen and (min-width: 575px) {
            max-width: 480px;
        }
        @media screen and (min-width: 600px) {
            max-width: 540px;
        }
        @media screen and (min-width: 768px) {
            max-width: 690px;
        }
        @media screen and (min-width: 992px) {
            max-width: 960px;
        }
        @media screen and (min-width: 1280px) {
            max-width: 1140px;
        }
        @media screen and (min-width: 1440px) {
            max-width: 1204px;
        }
    }
    &__Head{
        color: #FFFFFF;
        font-family: "Gazpacho";
        font-size: 50px;
        font-weight: bold;
        letter-spacing: 2.5px;
        line-height: 75px;
        text-align: center;
        margin: 0 auto;
        padding: 0 0 18px 0;
        max-width: fit-content;
        position: relative;
        @media screen and (max-width: 640px) {
            max-width: 290px;
        }
        &::before {
            content: "";
            position: absolute;
            width: 60px;
            height: 60px;
            background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/start_group_yellow.png?v=1661160183);
            background-repeat: no-repeat;
            background-size: contain;
            top: 6px;
            left: -60px;
            @media screen and (max-width: 991.5px) {
                left: 0;
            }
            @media screen and (max-width: 767.5px) {
                top: -28px;
                left: -45px;
                width: 40px;
                height: 45px;
            }
            @media screen and (max-width: 640px) {
                left: -7px;
            }
        }
        &::after {
            content: "";
            position: absolute;
            width: 60px;
            height: 60px;
            background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/star_group.png?v=1661160183);
            background-repeat: no-repeat;
            background-size: contain;
            top: -13px;
            right: -54px;
            @media screen and (max-width: 991.5px) {
                right: 0;
            }
            @media screen and (max-width: 767.5px) {
                top: -13px;
                right: -53px;
                width: 40px;
                height: 45px;
            }
            @media screen and (max-width: 640px) {
                top: -29px;
                right: -14px;
            }
        }
        @media screen and (max-width: 767.5px) {
            font-size: 32px;
            letter-spacing: 0.7px;
            line-height: 41.6px;
            padding: 0 0 15px 0;
        }
    }
    &__Subhead{
        color: #FFFFFF;
        font-family: Interstate Cond;
        font-size: 18px;
        letter-spacing: 0.7px;
        line-height: 34.2px;
        text-align: center;
        margin: 0 auto;
        max-width: 720px;
        @media screen and (max-width: 767.5px) {
            font-size: 16px;
            letter-spacing: 0.5px;
            line-height: 30.4px;
        }
    }
    &__Layout-main{
        display: flex;
        flex-wrap: wrap;
        padding: 42px 0 0 0;
        column-gap: 100px;
        row-gap: 20px;
        justify-content: center; 
        @media screen and (max-width: 1439.5px) and (min-width: 1200px){
            column-gap: 50px;
        }
        @media screen and (max-width: 767.5px) {
            column-gap: 0;
            row-gap: 50px;
            padding: 30px 0 0 0;    
            text-align: center;
        }
    }
    &__Layout-inner{
        flex: 0 0 calc(16.67% - 85px);
        max-width: calc(16.67% - 85px);
        display: flex;
        align-items: center;
        justify-content: center;
        @media screen and (max-width: 1439.5px) {
            flex: 0 0 calc(16.67% - 43px);
            max-width: calc(16.67% - 43px);
        }
        @media screen and (max-width: 1199.5px) {
            flex: 0 0 calc(33.33% - 69px);
            max-width: calc(33.33% - 69px);
        }
        @media screen and (max-width: 767.5px) {
            flex: 0 0 100%;
            max-width: 100%;
        }
    }
    &__Img{
        width: 100%;
        height: auto;
        object-fit: contain;
        @media screen and (max-width: 1199.5px) {
            max-width: 50%;
        }
        @media screen and (max-width: 767.5px) {
            max-width: 50%;
        }
    }
}