.form-confirmation {
  background: url('brush.svg') 0 100%/contain no-repeat, 
  linear-gradient(180deg, 
  $Color--violet 0% calc(100% - 20px), 
  $Color--gray 20px);
  color: white;
  padding: 40px 0;
  position: relative;

  @media (max-width: 700px) {
    padding-top: 127px;  
  }

  @media (max-width: 599px) {
    background: url('brush.svg') 0 100%/contain no-repeat, 
    linear-gradient(180deg, 
    $Color--violet 0% calc(100% - 7px),
    $Color--gray 20px);  
  }
  
  &__container {
    align-items: center;
    display: flex;
    justify-content: space-between;
    min-height: 800px;

    @media (max-width: 700px) {
      flex-wrap: wrap;
      justify-content: center;
      min-height: 0;
    }
  }

  &__content {
    flex: 0 0 60.6%;
    padding-left: 40px;
    padding-right: 40px;

    @media (max-width: 900px) {
      flex: 1;
      padding-left: 10px;
    }

    @media (max-width: 700px) {
      flex: 0 0 100%;
      margin-bottom: 70px;
      padding: 0;
      text-align: center;
    }
  }

  &__title {
    display: inline;
    margin-bottom: 16px;
    position: relative;
    max-width: fit-content;

    p {
      display: inline;
    }

    &:before {
      content: "";
      position: absolute;
      width: 60px;
      height: 60px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/start_group_yellow.png?v=1661160183);
      background-repeat: no-repeat;
      background-size: contain;
      top: -14px;
      left: -42px;

      @media screen and (max-width: 991.5px) {
        width: 50px;
        height: 50px;
        left: -30px;
        top: -18px;
      }
    }

    &:after {
      content: "";
      position: absolute;
      width: 60px;
      height: 60px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/star_group.png?v=1661160183);
      background-repeat: no-repeat;
      background-size: contain;
      top: -13px;
      right: -54px;

      @media screen and (max-width: 992px) {
        width: 50px;
        height: 50px;
        right: -40px;
      }
    }
  }

  &__copy {
    font-family: 'Interstate Cond';
    font-size: 18px;
    line-height: 34px;
    letter-spacing: 0.7px;
    margin-top: 16px;
    margin-bottom: 40px;
    max-width: 80%;

    @media (max-width: 1024px) {
      font-size: 16px;
      line-height: 30px;
      letter-spacing: 0.5px;
      max-width: 100%;
    }
  }

  &__item {
    flex: 0 0 347px;
    padding-right: 40px;

    @media (max-width: 900px) {
      padding-right: 0;
      flex: 0 0 307px;
    }

    @media (max-width: 599px) {
      flex: 0 0 100%;  
    }
  }
}