.ContactBlock {
  padding: 100px 0;
  background: #fefaee;
  &__Container {
    margin: 0 auto;
  }
  &__Layout-main {
    display: grid;
    gap: 50px;
    grid-template-columns: repeat( 3, 1fr );
    justify-content: center;

    @media (max-width: 1024px) {
      gap: 30px;  
    }

    @media (max-width: 767px) {
      grid-template-columns: repeat(2, 1fr);  
    }
    
    @media (max-width: 599px) {
      grid-template-columns: repeat(1, 1fr);  
    }
  }
  &__MainTitle {
    margin-bottom: 40px;
  }
  &__Layout-inner {
    min-width: 0;
  }
  &__Block {
    padding: 37px 23px 24px 38px;
    background: #52339b;
    border-radius: 10px;
    position: relative;
    border: 15px solid transparent;
    height: 100%;
    border-radius: 15px;
    border-top: 0;
    border-left: 0;
    transition: 0.3s all ease-in-out;
    -moz-transition: 0.3s all ease-in-out;
    -webkit-transition: 0.3s all ease-in-out;
    // &:hover {
    //   border: 15px solid #1e8386;
    //   border-top: 0;
    //   border-left: 0;
    //   border-radius: 15px 26px 26px 26px;
    //   transform: scale(1.01);
    // }
    @media screen and (max-width: 1139.5px) {
      padding: 37px 5px 24px 20px;
    }
    @media screen and (max-width: 767.5px) {
      padding: 44px 0 29px 15px;
    }
  }
  &__Title {
    color: #ffffff;
    font-family: "Gazpacho";
    font-size: 23px;
    font-weight: bold;
    letter-spacing: 0.5px;
    line-height: 35px;
    text-align: center;
    margin: 0;
    padding: 0 0 12px 0;
    @media screen and (max-width: 767.5px) {
      font-size: 18px;
      letter-spacing: 0.6px;
      line-height: 28.8px;
      padding: 0 0 15px 0;
    }
  }
  &__Details {
    color: #ffffff;
    font-family: Interstate Cond;
    font-size: 16px;
    letter-spacing: 0.4px;
    line-height: 30px;
    text-align: center;
    margin: 0;
    padding: 0 0 15px 0;
    @media screen and (max-width: 767.5px) {
      font-size: 14px;
      letter-spacing: 0.5px;
      line-height: 25.2px;
      padding: 0 0 10px 0;
    }
  }
  &__ButtonWrap {
    display: flex;
    justify-content: center;
  }
  &__Button {
    height: 47px;
    color: #000000;
    font-family: Interstate;
    font-size: 14px;
    letter-spacing: 0.88px;
    line-height: 30px;
    text-align: center;
    padding: 12px 19px 9px;
    max-width: fit-content;
    @media screen and (max-width: 767.5px) {
      font-size: 12px;
      letter-spacing: 0.5px;
      line-height: 22px;
      height: 42px;
    }
  }
}
