body .swiper-pagination {
  z-index: 2;
}

//Judge me Widget
body .jdgm-rev-widg {
  @include make-container;

  border: 0;
  margin-top: 0;
  margin-bottom: 0;
  padding: 100px 50px;
  position: relative;

  @media (max-width: 1024px) {
    padding: 70px 20px;
  }

  .jdgm-rev-widg__title {
    margin-bottom: 40px;
    text-align: center;

    @media (max-width: 1140px) {
      font-size: 32px; 
    }

    @extend .heading-stars;
    @extend .heading-stars-right;

    @media (max-width: 1024px) {
      margin-bottom: 30px;  
    }
  }

  .jdgm-rev-widg__summary {
    display: none;
  }

  .jdgm-histogram {
    display: none;
  }

  .jdgm-rev-widg__summary-inner {
    display: none;
  }

  .jdgm-rev-widg__sort-wrapper {
    margin-bottom: 30px;
    text-align: center;
  }

  .jdgm-sort-dropdown-arrow {
    display: none !important;
  }

  .jdgm-sort-dropdown-wrapper {
    position: absolute;
    top: 100px;
    right: 50px;

    @media (max-width: 1024px) {
      float: none;
      position: static;
      text-align: center;
    }
  }

  .jdgm-sort-dropdown {
    background: $Color--yellow url('icon-select.svg') calc(100% - 20px) center/10px 6px no-repeat;
    border: 2px solid black;
    border-radius: 3px;
    font-family: 'Interstate';
    font-size: 14px;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 0.5px;
    padding: 0 30px 0 20px;
    text-transform: uppercase;
    height: 52px;
  }

  .jdgm-rev {
    border: 0;
    margin: 0;
    padding: 0 0 43px;

    @media (max-width: 1024px) {
      padding: 0 0 30px;  
    }
  }

  .jdgm-rev__header {
    float: left;
    margin-bottom: 0;
    margin-right: 60px;
    width: 123px;

    @media (max-width: 1024px) {
      margin-right: 25px;  
      width: 76px;
    }

    @media (max-width: 599px) {
      float: none;  
      width: 100%;
    }
  }

  .jdgm-rev__rating {
    position: absolute;
    top: 0;
    left: 183px;

    @media (max-width: 1024px) {
      left: 100px;  
    }

    @media (max-width: 599px) {
      display: block;
      margin-left: 25px;
      overflow: hidden;
      padding-left: 25px;
      padding-top: 10px;
      position: static;
    }

    .jdgm-star {
      color: $Color--yellow;
      font-size: 13px;
      padding-right: 3px;
    }

    .jdgm--off {
      color: #D0C4AB;

      &::before {
        content: "\e000";
      }
    }
  }

  .jdgm-rev__timestamp {
    font-family: 'Interstate Cond';
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.44px;
    position: absolute;
    top: 0;
    right: 0;

    @media (max-width: 599px) {
      top: 11px;  
    }
  }

  .jdgm-rev__author-wrapper {
    clear: both;
    float: left;
    width: 100%;

    @media (max-width: 1024px) {
      clear: none;
    }

    @media (max-width: 599px) {
      float: left;
      width: auto;
      overflow: hidden;
      margin-left: 25px;
      margin-top: 0;  
    }
  }

  .jdgm-rev__author {
    display: block;
    font-family: 'Interstate';
    font-size: 16px;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: 0.5px;
    margin-top: 10px;
    text-transform: uppercase;
    width: 100%;
    text-align: center;

    @media (max-width: 1024px) {
      line-height: 27px;
      letter-spacing: 0.3px;  
      text-transform: none;
    }

    @media (max-width: 599px) {
      margin-top: 0;
    }
  }

  .jdgm-rev__icon {
    align-items: center;
    display: flex;
    font-size: 40px;
    justify-content: center;
    margin-right: 0;
    width: 123px;
    height: 123px;

    @media (max-width: 1024px) {
      font-size: 24px;
      width: 76px;
      height: 76px;
    }
  }

  .jdgm-rev__content {
    font-family: 'Interstate Cond';
    margin-top: 35px;
    overflow: hidden;

    @media (max-width: 1024px) {
      margin-top: 30px;  
    }

    @media (max-width: 599px) {
      margin-top: 15px;  
    }
  }

  .jdgm-rev__title {
    @extend h4;

    margin-bottom: 10px;
  }

  .jdgm-rev__body p {
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.44px;
  }
}

// Wholesale app
body#raw-rev {
  .site-header {
    @extend .site-header--yellow;
  }

  .MainContent {
    padding-top: 70px;
    padding-bottom: 70px;
  }

  .wpd_headermsg {
    margin-bottom: 30px;
  }

  .wpd_headermsg .title {
    font-family: 'Interstate Cond';
  }

  .wpd_create_account .title {
    display: none;
  }
  
  .wpd_company_info,
  .wpd_pri_cont {
    margin-bottom: 20px;
  }

  .wpd_full_with {
    .wpd_half.Second {
      @media (max-width: 599px) {
        margin-top: 30px;  
      }
    }

    .wpd_clearfix {
      & + * {
        margin-top: 20px;
      }
    }

    input {
      background-color: #fdd17426;
      border: 2px solid black;
      border-radius: 3px;
      outline: none;
      padding: 0 20px;
      transition: all .3s cubic-bezier(.42,0,.58,1);
      width: calc(100% - 40px);
      height: 52px;

      @media (max-width: 599px) {
        width: 100%;  
      }
    }

    select {
      -webkit-appearance: none;
      -moz-appearance: none;

      background: rgba($Color--yellow, 0.15) url(data:image/png;base64,) calc(100% - 5px) center no-repeat;
      border: 2px solid black;
      border-radius: 5px;
      font-family: 'Interstate Cond';
      font-size: 16px;
      font-weight: 300;
      letter-spacing: 0.5px;
      line-height: 29px;
      outline: none;
      padding: 0 15px;
      transition: all 0.3s $cubic;
      width: calc(100% - 40px);
      height: 52px;

      @media (max-width: 599px) {
        width: 100%;  
      }
    }

    .wpd_submit {
      @extend .btn;

      margin-top: 26px;
      width: calc(100% - 20px);
      height: 52px;

      @media (max-width: 599px) {
        width: 100%;  
      }
    }
  }

  #wpd-create-customer {
    @media (max-width: 599px) {
      width: calc(100% - 40px);  
    }
  }
}