@mixin input-placeholder {
  &.placeholder {
    @content;
  }

  &:-moz-placeholder {
    @content;
  }

  &::-moz-placeholder {
    @content;
  }

  &:-ms-input-placeholder {
    @content;
  }

  &::-webkit-input-placeholder {
    @content;
  }
}

@mixin transition-delay {
  @for $i from 1 through 10 {
    &:nth-child(#{$i}) {
      transition-delay: #{$i * .1 + 0.2}s;
    }
  }
}
  
@mixin remove-number-style {
  -moz-appearance: textfield;
  
  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
}

@media (min-width: 1025px) {
  .mobile-only {
    display: none !important;
  }
}

@media (max-width: 1024px) {
  .desktop-only {
    display: none !important;
  }
}

/////////////////////////////////
////////Bootstrap 5 grid/////////
/////////////////////////////////
$grid-columns: 12 !default;

:root {
  --grid-gutter-width: 0;

  @media (max-width: 1024px) {
    --grid-gutter-width: 20px;
  }
}

@mixin make-container() {
  margin-right: auto;
  margin-left: auto;
  padding-right: var(--grid-gutter-width);
  padding-left: var(--grid-gutter-width);
  width: 100%;

  @media (min-width: 576px) {
    max-width: calc(100% - 20px);
  }

  @media (min-width: 768px) {
    max-width: calc(100% - 40px);
  }

  @media (min-width: 1025px) {
    max-width: calc(100% - 80px);
  }

  @media (min-width: 1320px) {
    max-width: 1240px;
  }

  // @media (min-width: 1400px) {
  //   max-width: 1320px;
  // }
}

@mixin make-container-small() {
  margin-right: auto;
  margin-left: auto;
  padding-right: var(--grid-gutter-width);
  padding-left: var(--grid-gutter-width);
  width: 100%;

  @media (min-width: 576px) {
    max-width: calc(100% - 20px);
  }

  @media (min-width: 768px) {
    max-width: calc(100% - 40px);
  }

  @media (min-width: 1025px) {
    max-width: calc(100% - 80px);
  }

  @media (min-width: 1320px) {
    max-width: 1240px;
  }
}

@mixin make-container-edge() {
  margin-left: auto;
  padding-right: var(--grid-gutter-width);
  padding-left: var(--grid-gutter-width);
  width: 100%;

  @media (min-width: 576px) {
    max-width: calc(100%);
  }

  @media (min-width: 1025px) {
    max-width: calc(100% - 80px);
  }

  @media (min-width: 1420px) {
    max-width: 1370px;
  }
}

@mixin make-row() {
  display: flex;
  flex-wrap: wrap;
  // margin-right: calc(var(--grid-gutter-width) / -1);
  // margin-left: calc(var(--grid-gutter-width) / -1);
}

@mixin make-col-ready() {
  // Prevent columns from becoming too narrow when at smaller grid tiers by
  // always setting `width: 100%;`. This works because we use `flex` values
  // later on to override this initial width.
  position: relative;
  min-height: 1px; // Prevent collapsing
  padding-right: (var(--grid-gutter-width) / 2);
  padding-left: (var(--grid-gutter-width) / 2);
  width: 100%;
}

@mixin make-col($size, $columns: $grid-columns) {
  flex: 0 0 percentage($size / $columns);
  // Add a `max-width` to ensure content within each column does not blow out
  // the width of the column. Applies to IE10+ and Firefox. Chrome and Safari
  // do not appear to require this.
  max-width: percentage($size / $columns);
}

@mixin make-col-offset($size, $columns: $grid-columns) {
  $num: $size / $columns;
  margin-left: if($num == 0, 0, percentage($num));
}

@mixin make-col-auto() {
  flex: 0 0 auto;
  width: auto;
}


// Row columns
//
// Specify on a parent element(e.g., .row) to force immediate children into NN
// numberof columns. Supports wrapping to new lines, but does not do a Masonry
// style grid.
@mixin row-cols($count) {
  > * {
    flex: 0 0 auto;
    width: divide(100%, $count);
  }
}

// Cubic
$cubic: cubic-bezier(0.42, 0.0, 0.58, 1.0);

