.space-top {
  padding-top: 100px;

  @media (max-width: 1024px) {
    padding-top: 40px;  
  }
}

.space-bottom {
  padding-bottom: 100px;

  @media (max-width: 1024px) {
    padding-bottom: 40px;
  }
}

.chevron {
  align-items: center;
  cursor: pointer;
  display: flex;
  justify-content: center;
  position: relative;
  width: 24px;
  height: 24px;
  z-index: 2;

  &::before {
    border-color: white;
    border-style: solid;
    border-width: 2px 2px 0 0;
    content: '';
    display: block;
    position: relative;
    transform: rotate(45deg);
    width: 10px;
    height: 10px;
  }

  &--left:before {
    border-width: 0 0 2px 2px;
  }

  &--top:before {
    border-width: 0 2px 2px 0;
  }

  &--bottom:before {
    border-width: 2px 0 0 2px;
  }
}

@mixin hideScrollbar() {
  &::-webkit-scrollbar {
    display: none;
  }
  
  -ms-overflow-style: none;
  scrollbar-width: none;
}