.nav-list {
  $self: &;
  align-items: center;
  display: flex;
  height: 100%;

  &__item {
    align-items: center;
    display: flex;
    padding: 0 17px;
    height: 100%;

    &::marker {
      display: none;
    }

    &:hover {
      #{$self}__link {
        &::after {
          transform: scaleX(1);
        }
      }

      .dd-mega-menu {
        opacity: 1;
        pointer-events: all;
        transform: translateY(0%);
    
        &__container {
          transform: translateY(0%);
        }
    
        &__item {
          opacity: 1;
          top: 0;
          transition-delay: 0.3s;
        }
      }
    
      .dropdown-menu {
        pointer-events: all;
        transform: scaleY(1);
    
        &__item {
          opacity: 1;
          top: 0;
        }
      }
    }

    &:first-child {
      padding-left: 0;
    }

    &:last-child {
      padding-right: 0;
    }
  }

  &__link {
    color: inherit;
    font-family: 'Interstate Cond';
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 0.5px;
    line-height: 100px;
    position: relative;

    @media (max-width: 1100px) {
      font-size: 14px;
    }

    &::after {
      background: #000;
      content: "";
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
      transform: scaleX(0);
      transition: all 0.3s $cubic;
      width: 100%;
      height: 2px;
    }

    &--nohover {
      &::after {
        display: none;
      }
    }
  }

  &--helper {
    #{$self}__item {
      position: relative;

      > * {
        display: flex;
      }

      a {
        padding: 0;
      }

      svg {
        width: 25px;
        height: 25px;

        @media (max-width: 1024px) {
          width: 20px;  
        }
      }
    }

    img {
      display: block;

      @media (max-width: 1024px) {
        transform: scale(0.8);  
      }
    }
  }
}

//Desktop Dropdown
.dd-mega-menu {
  background: $Color--gray;
  display: flex;
  opacity: 0;
  padding: 50px 0;
  position: fixed;
  pointer-events: none;
  top: 100px; 
  left: 0;
  transition: all 0.3s $cubic;
  transform: translateY(20px);
  transform-origin: top;
  width: 100%;

  @media (max-width: 1024px) {
    display: none;  
  }

  &__container {
    @include make-container;

    display: flex;
    position: relative;
    transform: translateY(5px);
    transition: all 0.3s $cubic;

    &--center {
      flex-direction: column;
      text-align: center;
    }
  }

  &__nav {
    align-items: flex-start;
    display: flex;
    flex: 0 0 235px;
    flex-direction: column;
    padding-top: 6px;
    padding-right: 40px;

    &-item {
      cursor: pointer;
      position: relative;
      transition: all 0.3s $cubic;

      &::after {
        background: $Color--black;
        content: "";
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        transform: scaleX(0);
        transform-origin: left;
        transition: all 0.3s $cubic;
        width: 100%;
        height: 2px;
      }

      &--active {
        color: $Color--black;

        &::after {
          transform: scaleX(100%);
        }
      }

      &:hover {
        color: $Color--black;
      }

      & + * {
        margin-top: 35px;
      }
    }
  }

  &__grid {
    display: flex;

    &-item {
      flex: 0 0 200px;
      transition: all 0.3s $cubic;

      & + * {
        margin-left: 50px;
      }

      &:hover {
        .dd-mega-menu__grid-title {
          letter-spacing: 1px;
        }

        .dd-mega-menu__grid-image {
          border-color: black;
        }
      }
    }

    &-image {
      border: 2px solid transparent;
      border-radius: 5px;
      display: block;
      margin-bottom: 18px;
      object-fit: cover;
      transition: all 0.3s $cubic;
      width: 100%;
      height: 200px;
    }

    &-title {
      font-family: 'Interstate';
      font-size: 12px;
      font-weight: 400;
      line-height: 13px;
      letter-spacing: 0.38px;
      transition: all 0.3s $cubic;
    }
  }
}

// Mobile Dropdown
.dropdown-menu {
  background: white;
  box-shadow: 0 0 10px 0 rgba(0,0,0,0.1);
  min-width: 202px;
  padding: 20px 30px;
  pointer-events: none;
  position: absolute;
  top: 100%;
  right: 0;
  transition: all 0.3s $cubic;
  transform: scaleY(0);
  transform-origin: top;

  &__item {
    opacity: 0;
    position: relative;
    top: 15px;
    transition: all 0.3s $cubic;
    transition-delay: 0s;

    a {
      font-size: 12px;
      font-weight: 400;
      line-height: 27px;
      letter-spacing: 1px;
      text-transform: none;
      transition: all 0.3s $cubic;
      white-space: nowrap;

      &:hover {
        opacity: 0.5;
      }
    }

    & + li {
      margin-top: 10px;
    }
  }
}

// Hamburger - mobile opener
.hamburger {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 20px;
  height: 14px;

  @media (min-width: 1025px) {
    display: none;
  }

  &__line {
    background: white;
    display: block;
    transition: all 0.3s $cubic;
    transform-origin: center;
    width: 100%;
    height: 2px;

    .site-header--sticky &,
    .site-header--light &,
    .site-header--yellow & {
      background: black;
    }

    &:nth-child(2) {
      width: 15px;
    }

    &:nth-child(3) {
      width: 10px;
    }
  }

  &--open {
    .line {
      background: black;
      width: 20px;

      &:nth-child(1){
        transform: translateY(6px) rotate(45deg);
        width: 20px;
      }

      &:nth-child(2){
        opacity: 0;
        width: 20px;
      }

      &:nth-child(3){
        transform: translateY(-6px) rotate(-45deg);
        width: 20px;
      }
    }
  }
}

// Navigation slideIn
.nav-mobile {
  background: $Color--gray;
  padding: 52px 0;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  top: 100%;
  left: 0;
  transition: all 0.3s $cubic;
  transform: translateX(calc(-100% - 11px));
  visibility: hidden;
  width: 100%;
  height: 100vh;
  z-index: 1;

  &__container {
    overflow: auto;
    height: 100%;
  }

  &-list__item {
    &--has-dd {
      .nav-mobile-list__item-link {
        pointer-events: none;
      }

      &.nav-mobile-list-item--animate {
        &::after {
          transform: rotate(90deg);
        }
      }
    }
  }
  
  &--active {
    opacity: 1;
    transform: translateX(0);
    visibility: visible;

    .nav-mobile-list {
      opacity: 1;
      transform: translateX(0);
      transition-delay: 0.2s;
    }
  }
}

// Navigation mobile
.nav-mobile-list {
  margin-bottom: 90px;
  opacity: 0;
  transform: translateX(-20px);
  transition: all 0.3s $cubic;

  &__item {
    &-link {
      line-height: 30px;
      letter-spacing: 0.5px;
      transition: all 0.3s $cubic;

      &:hover {
        color: $Color--violet;
      }
    }

    & + * {
      margin-top: 16px;
    }

    &--has-dd {
      &::after {
        content: "";
        border: 0;
        border-left: 4px solid black;
        border-top: 4px solid transparent;
        border-bottom: 4px solid transparent;
        display: block;
        position: absolute;
        top: 10px;
        right: 0;
        transition: all 0.3s $cubic;
        width: 0;
        height: 0;
      }

      li:first-child {
        margin-top: 15px;
      }

      li:last-child {
        margin-bottom: 15px;
      }
    }
  }
}

// Submenu Big
.dd-mega-menu-mobile {
  background: $Color--gray;
  overflow: hidden;
  transition: all 0.3s $cubic;
  height: 0;
  z-index: 1;

  &::-webkit-scrollbar {
    display: none;
  }
}

// Submenu small
.dd-mega-menu-small-mobile__grid {
  border-bottom: 1px solid rgba(black, 0.4);
  padding-top: 20px;
  padding-bottom: 25px;

  &-item {
    align-items: center;
    display: flex;
    flex-direction: column;

    & + * {
      margin-top: 26px;
    }
  }

  &-image {
    border-radius: 5px;
    margin-bottom: 5px;
    object-fit: cover;
    width: 150px;
    height: 150px;
  }

  &-title {
    line-height: 30px;
  }
}
