.site-header {
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  transition: all 0.3s $cubic;
  height: auto;
  z-index: 4;
  width: 100%;

  &::after {
    box-shadow: 0 0 15px 0 transparent;
    content: "";
    display: block;
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    transition: all 0.3s $cubic;
    width: 100%;
    height: 100%;
    z-index: 1;
  }

  @media (max-width: 1024px) {
    //height: 40px;  
  }

  &__container {
    @include make-container;
    
    height: 100%;
    z-index: 2;
  }

  &__row {
    align-items: center;
    display: flex;
    justify-content: space-between;
    position: relative;
    height: 100%;

    @media (max-width: 1024px) {
      padding-top: 18px;
      padding-bottom: 18px;
    }

    &--dd {
      display: none;
    }
  }

  &__navigation {
    @media (max-width: 1024px) {
      display: none;  
    }
  }

  &--light {
    background: none;
    color: black;
    position: sticky;
  }

  &--yellow {
    background: $Color--gray;
    color: black;
    position: sticky;
  }

  &--sticky,
  &--open {
    background: $Color--gray;
    color: black;
  }
  
  &:hover {
    @media (min-width: 1025px) {
      background: $Color--gray;
      color: black;  
    }
  }
}

//Announcement bar
.ann__container {
  @include make-container;
}

.ann__copy {
  line-height: 22px;
  padding: 7px 0;
  text-align: center;
}

@import "./navigation.scss";
@import "./brand.scss";
@import "./search.scss";
@import "./cart.scss";