.BannerVideo{
    position: relative;
    overflow: hidden;
    &__Container{
        max-width: 100%;
    }
    &__Video{
        width: 100vw;
        height: 700px;
    }
    &__Play-button{
        width: 90px;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        background: #000000;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        cursor: pointer;
        &::after{
            content: '';
            width: 100vw;
            height: 100vh;
            position: absolute;
            display: block;
        }
        @media screen and (max-width: 767.5px) {
            width: 50px;
            height: 40px;
        }
        svg{
            width: 24px;
            height: 24px;
            fill: #ffffff;
            @media screen and (max-width: 767.5px) {
                width: 12px;
                height: 12px;
            }
        }
    }
    &__Video[poster]{
        display: block;
        width: 100vw;
        height: 700px;
        object-fit: cover;
    }
}