.user-form {
  padding-top: 100px;
  padding-bottom: 100px;

  @media (max-width: 1024px) {
    padding-top: 50px;
    padding-bottom: 70px;
  }

  &__content {
    max-width: 600px;
    margin: 0 auto;
  }

  &__title {
    margin: 0 auto 20px;
    text-align: center;

    @media (max-width: 1024px) {
      margin: 0 auto 16px;
    }
  }

  &__copy {
    font-family: 'Interstate Cond';
    font-size: 18px;
    font-weight: 300;
    line-height: 34px;
    letter-spacing: 0.7px;
    max-width: 420px;
    margin: 0 auto 40px;
    text-align: center;

    @media (max-width: 1024px) {
      font-size: 16px;
      line-height: 30px;
      letter-spacing: 0.5px;
      margin: 0 auto 30px;
    }
  }
}

.field-wrapper {
  margin-bottom: 20px;

  @media (max-width: 1024px) {
    margin-bottom: 14px;  
  }

  &--password {
    margin-bottom: 20px;
  }

  &--terms {
    margin-bottom: 10px;
  }

  &--accepts-marketing {
    margin-bottom: 25px;
  }
}

.input-label,
 .hbspt-form label {
  color: black;
  font-family: 'Interstate Cond';
  font-size: 18px;
  font-weight: 300;
  display: block;
  letter-spacing: 0.7px;
  line-height: 34px;
  margin-bottom: 4px;

  @media (max-width: 1024px) {
    font-size: 16px;
    line-height: 30px;
    letter-spacing: 0.5px;
    margin-bottom: 0;  
  }
}

.input-field,
[type=text].hs-input,
[type=number].hs-input,
[type=tel].hs-input,
[type=email].hs-input {
  background-color: rgba($Color--yellow, 0.15);
  border: 2px solid black;
  border-radius: 3px;
  outline: none;
  padding: 0 20px;
  transition: all 0.3s $cubic;
  width: 100%;
  height: 52px;
}

textarea.hs-input {
  background-color: rgba($Color--yellow, 0.15);
  border: 2px solid black;
  border-radius: 3px;
  outline: none;
  padding: 0 20px;
  resize: none;
  transition: all 0.3s $cubic;
  width: 100%;
  height: 102px;
}

input[type=number] {
  @include remove-number-style;
}

.input-select,
select.hs-input {
  -webkit-appearance: none;
  -moz-appearance: none;

  background: rgba($Color--yellow, 0.15) url(data:image/png;base64,) calc(100% - 5px) center no-repeat;
  border: 2px solid black;
  border-radius: 3px;
  outline: none;
  padding: 0 20px;
  transition: all 0.3s $cubic;
  width: 100%;
  height: 52px;
}

.input-checkbox,
[type=checkbox].hs-input {
  border-radius: 0;
  flex: 0 0 20px;
  margin-right: 12px;
  position: relative;
  top: 2px;
  width: 20px;
  height: 20px;
}

.form-after {
  margin-top: 10px;
  text-align: center;
}

.form-forgot {
  margin-bottom: 40px;

  @media (max-width: 1024px) {
    margin-bottom: 30px;  
  }
}

.form-link {
  color: black;
  display: table-cell;
  font-family: 'Interstate Cond';
  font-size: 16px;
  font-weight: 300;
  line-height: 29px;
  letter-spacing: 0.5px;

  &--signup,
  &--register {
    display: inline-block;
    font-family: 'Interstate';
    font-size: 14px;
    font-weight: 700;
    line-height: 26px;
    letter-spacing: 0.5px;
    text-transform: uppercase;

    @media (max-width: 1024px) {
      font-size: 12px;
      line-height: 23px;
      letter-spacing: 0.5px;
    }
  }
}

.form-button {
  margin-bottom: 40px;
  text-align: center;

  @media (max-width: 1024px) {
    margin-bottom: 30px;  
  }

  &--reset,
  &--register {
    margin-top: 40px;

    @media (max-width: 1024px) {
      margin-top: 30px;  
    }
  }
}

.form-info {
  
}

.form-message {
  color: $Color--violet;
  font-family: 'Interstate Cond';
  margin-bottom: 20px;
}

.shopify-challenge__container {
  padding-top: 60px;
  padding-bottom: 100px;

  @media (max-width: 1024px) {
    padding-top: 30px;
    padding-bottom: 75px;
  }

  .shopify-challenge__message {
    margin-bottom: 20px;
  }

  .shopify-challenge__button {
    @extend .btn--black;

    margin-top: 20px !important;
  }
}

.submitted-message {
  font-size: 14px;
  margin-top: 20px;

  @media (max-width: 1024px) {
    text-align: center;  
  }
}

.contact__form {
  .hs-form-field {
    margin-bottom: 40px;
  }

  textarea {
    border: 0;
    border-bottom: 1px solid black;
    resize: none;
    width: 100%;
    height: 40px;
  }

  input[type=submit] {
    @extend .btn;
    @extend .btn--black;
  }
}

// HBS FORMS
.hs-form-booleancheckbox {
  padding-top: 35px;

  @media (max-width: 599px) {
    padding-top: 5px;  
  }
}

.hs_error_rollup {
  display: none;
}

.hs-error-msg {
  font-weight: 700 !important;
}