.ImgLeft {
  padding: 100px 0 100px 0;
  background: #fefaee;
  @media screen and (max-width: 767.5px) {
    padding: 70px 0 70px 0;
  }
  &__container {
    
  }
  &__Layout-main {
    display: flex;
    flex-wrap: wrap;
    padding-left: 80px;
    padding-right: 80px;

    @media (max-width: 1024px) {
      padding-left: 0;
      padding-right: 0;
      text-align: center;
    }

    @media screen and (max-width: 991.5px) {
      
    }
  }
  &__Layout-inner {
    flex: 0 0 36.3%;

    @media screen and (max-width: 991.5px) {
      flex: 0 0 100%;
      max-width: 100%;
      margin-bottom: 30px;
    }
  }
  &__Layout-right {
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: center;
    padding-left: 100px;

    @media screen and (max-width: 991.5px) {
      align-items: center;
      padding-left: 0;
    }
  }
  &__Head {
    padding: 0 0 15px 0;
    color: #000000;
    font-family: "Interstate Cond";
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 2.5px;
    line-height: 75px;
    margin: 0;
    position: relative;
    max-width: max-content;
    @media screen and (max-width: 991.5px) {
      text-align: center;
    }
    
    @media screen and (max-width: 767.5px) {
      font-size: 32px;
      letter-spacing: 0.7px;
      line-height: 41.6px;
      text-align: center;
      margin: 0 auto;
    }
    &::after {
      content: "";
      position: absolute;
      width: 60px;
      height: 60px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/start_group_blue.png?v=1661160183);
      background-repeat: no-repeat;
      background-size: contain;
      top: -30px;
      right: -31px;
      @media screen and (max-width: 767.5px) {
        width: 40px;
        height: 40px;
        top: -20px;
        right: -20px;
      }
    }
  }
  &__Content {
    color: #000000;
    font-family: 'Interstate Cond';
    font-size: 18px;
    font-weight: 300;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    margin: 0;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
      text-align: center;
      margin: 0 auto;
    }
  }
  .Space {
    margin: 0 0 20px 0;
  }
  &__Image {
    width: 100%;
    height: auto;
    object-fit: cover;
  }
}
