.share {
  align-items: center;
  display: flex;
  margin-top: 40px;
  margin-bottom: 40px;
  height: 20px;

  @media (max-width: 767px) {
    margin: 40px auto;  
  }

  &__title {
    font-family: 'Interstate';;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0.8px;
    margin-right: 15px;
    position: relative;
    top: 2px;
  }

  &-item {
    align-items: center;
    display: flex;
    margin: 0 15px;
    width: 20px;
    height: 20px;

    &__image {
      object-fit: contain;
      width: 100%;
      height: 100%;
    }

    &--twitter {
      width: 25px;
      height: 25px;
    }
  }
}