
.QuizResult {
    padding: 200px 0 100px 0;
    @media screen and (max-width: 767.5px) {
      padding: 104px 0 70px 0;
    }
    &__Container {
      margin: 0 auto;
      @media screen and (max-width: 319px) {
        padding: 0 15px;
      }
      @media screen and (min-width: 320px) {
        padding: 0 15px;
      }
      @media screen and (min-width: 575px) {
        max-width: 480px;
        padding: 0;
      }
      @media screen and (min-width: 600px) {
        max-width: 540px;
      }
      @media screen and (min-width: 768px) {
        max-width: 690px;
      }
      @media screen and (min-width: 992px) {
        max-width: 960px;
      }
      @media screen and (min-width: 1080px) {
        max-width: 1040px;
      }
      @media screen and (min-width: 1440px) {
        max-width: 1240px;
      }
    }
    &__Head {
      color: #000000;
      font-family: "Gazpacho";
      font-size: 23px;
      font-weight: bold;
      letter-spacing: 0.5px;
      line-height: 32.2px;
      text-align: center;
      padding: 0 0 22px 0;
      @media screen and (max-width: 767.5px) {
        font-size: 18px;
        letter-spacing: 0.6px;
        line-height: 28.8px;
        padding: 0 0 15px 0;
      }
    }
    &__Details {
      color: #000000;
      font-family: Interstate Cond;
      font-size: 18px;
      letter-spacing: 0.7px;
      line-height: 34.2px;
      text-align: center;
      max-width: 540px;
      padding: 0 0 40px 0;
      margin: 0 auto;
      @media screen and (max-width: 767.5px) {
        font-size: 16px;
        letter-spacing: 0.5px;
        line-height: 30.4px;
        padding: 0 0 30px 0;
      }
    }
    &__ReTake {
      color: #000000;
      font-family: Interstate;
      font-size: 14px;
      letter-spacing: 0.5px;
      line-height: 25.2px;
      text-align: center;
      text-transform: uppercase;
      display: flex;
      column-gap: 5px;
      align-items: center;
      justify-content: center;
      max-width: fit-content;
      margin: 0 auto;
      border-bottom: 2px solid #000000;
      @media screen and (max-width: 767.5px) {
        font-size: 12px;
        letter-spacing: 0.5px;
        line-height: 22.8px;
      }
      svg {
        height: 20px;
        width: 20px;
        @media screen and (max-width: 767.5px) {
          height: 14px;
          width: 14px;
        }
      }
    }
    &__ProductWrap {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      column-gap: 74px;
      padding: 60px 0 0 0;
      @media screen and (max-width: 1280.5px) {
        column-gap: 40px;
      }
      @media screen and (max-width: 1079.5px) {
        column-gap: 0;
        row-gap: 50px;
      }
      @media screen and (max-width: 767.5px) {
        padding: 70px 4px 0 4px;
        max-width: 375px;
        margin: 0 auto;
        row-gap: 30px;
      }
    }
    &__Product {
      flex: 0 0 50%;
      max-width: calc(50% - 37px);
      padding: 40px;
      background-color: #fffaed;
      border-radius: 10px;
      display: flex;
      flex-wrap: wrap;
      row-gap: 20px;
      column-gap: 40px;
      @media screen and (max-width: 1280.5px) {
        max-width: calc(50% - 20px);
      }
      @media screen and (max-width: 1079.5px) {
        flex: 0 0 55%;
        max-width: 55%;
        margin: 0 auto;
      }
      @media screen and (max-width: 991.5px) {
        flex: 0 0 80%;
        max-width: 80%;
      }
      @media screen and (max-width: 767.5px) {
        flex: 0 0 100%;
        max-width: 100%;
        padding: 30px 31px;
        column-gap: 0;
      }
    }
    &__ProductDetails {
      flex: 0 0 50%;
      max-width: calc(50% - 20px);
      @media screen and (max-width: 767.5px) {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }
    &__Selected {
      flex: 0 0 50%;
      max-width: calc(50% - 20px);
      display: flex;
      flex-direction: column;
      @media screen and (max-width: 767.5px) {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }
    &__ProductImageWrap {
      padding: 100% 25px 20px 25px;
      margin: 0 0 17px 0;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      border-radius: 5px;
      background-color: #219ECF;
      @media screen and (max-width: 767.5px) {
        margin: 0 0 15px 0;
      }
    }
    &__ProductImage {
      width: 100%;
      height: auto;
      aspect-ratio: 16/9;
      object-fit: contain;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      padding: 0 25px;
    }
    &__ProductName {
      color: #000000;
      font-family: "Gazpacho";
      font-size: 23px;
      font-weight: bold;
      letter-spacing: 0.5px;
      line-height: 32.2px;
      text-align: center;
      @media screen and (max-width: 767.5px) {
        font-size: 25px;
        letter-spacing: 1px;
        line-height: 37.5px;
        padding: 0 0 10px 0;
      }
    }
    &__AddCartWrap {
      display: flex;
      flex: 0 0 100%;
    }
    &__SelectDetails {
      color: #000000;
      font-family: Interstate ;
      font-size: 16px;
      letter-spacing: 0.8px;
      line-height: 30.4px;
      text-align: center;
      padding: 0 0 15px 0;
      @media screen and (max-width: 767.5px) {
        letter-spacing: 0.3px;
        line-height: 27.2px;
      }
    }
    &__ProductTestWrap {
      display: flex;
      column-gap: 20px;
      @media screen and (max-width: 767.5px) {
        column-gap: 15px;
      }
    }
    &__ProductTest {
      flex: 0 0 50%;
      max-width: calc(50% - 15px);
      @media screen and (max-width: 767.5px) {
        flex: 0 0 50%;
        max-width: 50%;
      }
    }
    &__TestDetails {
      color: #000000;
      font-family: Interstate Cond; 
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 28.8px;
      text-align: center;
      padding: 5px 0 0 0;
      @media screen and (max-width: 767.5px) {
        font-size: 14px;
        letter-spacing: 0.5px;
        line-height: 25.2px;
        padding: 8px 0 0 0;
      }
    }
    &__TestImgWrap {
      padding: 28px 22px;
      border-radius: 15px;
      background-color: #ffffff;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &__DiscountInclude{
      color: #000000;
      font-family: Interstate Cond;
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 28.8px;
      text-align: center;
      margin: auto auto 0 auto;
      @media screen and (max-width: 767.5px) {
        line-height: 30.4px;
        padding: 24px 0 7px 0;  
      }
    }
    &__AddCart{
      color: #000000;
      font-family: Interstate;
      font-size: 14px;
      letter-spacing: 0.88px;
      line-height: 30px;
      text-align: center;
      width: 100%;
      @media screen and (max-width: 767.5px) {
        font-size: 12px;
        letter-spacing: 0.5px;
        line-height: 22px;
        height: 42px;
        padding: 10px 38px;
      }
    }
  }
  