.OurStory {
  padding: 100px 0 100px 0;
  @media screen and (max-width: 767.5px) {
    padding: 70px 0;
  }
  &__Container {
    margin: 0 auto;
    @media screen and (max-width: 319px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 320px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 575px) {
      max-width: 480px;
    }
    @media screen and (min-width: 600px) {
      max-width: 540px;
    }
    @media screen and (min-width: 768px) {
      max-width: 690px;
    }
    @media screen and (min-width: 992px) {
      max-width: 960px;
    }
    @media screen and (min-width: 1280px) {
      max-width: 1140px;
    }
    @media screen and (min-width: 1440px) {
      max-width: 1240px;
    }
  }
  &__Layout-main {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    @media screen and (max-width: 767.5px) {
      flex-direction: column;
      row-gap: 30px;
    }
  }
  &__Layout-inner {
    flex: 0 0 50%;
    max-width: 50%;
    @media screen and (max-width: 767.5px) {
      flex: 0 0 100%;
      max-width: 100%;
    }
  }
  &__Head {
    color: #000000;
    font-family: "Gazpacho";
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 2.5px;
    line-height: 75px;
    margin: 0;
    padding: 0 0 16px 0;
    @media screen and (max-width: 767.5px) {
      font-size: 32px;
      letter-spacing: 0.7px;
      line-height: 41.6px;
      text-align: center;
      padding: 0 0 13px 0;
    }
  }
  &__Details {
    max-width: 420px;
    width: 100%;
    color: #000000;
    font-family: Interstate Cond;
    font-size: 18px;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    margin: 0;
    padding: 0 0 42px 0;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
      padding: 0 0 27px 0;
      text-align: center;
    }
  }
  &__ButtonWrap {
    @media screen and (max-width: 767.5px) {
      text-align: center;
    }
  }
  &__Button {
    width: auto;
    height: 52px;
    @media screen and (max-width: 767.5px) {
      font-size: 12px;
      letter-spacing: 0.5px;
      line-height: 22px;
      height: 42px;
    }
  }
  &__Image {
    object-fit: contain;
    max-width: 100%;
    height: auto;
  }
}
