.site-header__logo {
  color: inherit;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  //transition: all 0.3s $cubic;
  width: 198px;
  height: 30px;

  .site-header--sticky & {
    color: black;
  }

  @media (max-width: 1024px) {
    width: 120px;
    height: 18px;
  }

  img {
    display: block;
    //image-rendering: pixelated;
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}