.btn {
  align-items: center;
  background: $Color--yellow;
  border: 2px solid black;
  border-radius: 5px;
  color: black;
  cursor: pointer;
  display: inline-flex;
  font-size: 14px;
  font-family: 'Interstate';
  font-weight: 700;
  letter-spacing: 0.8px;
  min-width: 177px;
  justify-content: center;
  padding: 0 22px;
  text-transform: uppercase;
  transition: all 0.3s $cubic;
  white-space: nowrap;
  height: 52px;

  @media (max-width: 1024px) {
    font-size: 12px;
    letter-spacing: 0.5px;
    min-width: 152px;
    height: 42px;
  }

  &:hover {
    @media (min-width: 1025px) {
      background: $Color--pink;
      color: white; 
    }
  }

  &--transparent {
    background: transparent;
    border: 1px solid white;
    color: white;
  }

  &--bordered {
    border: 1px solid black;
    cursor: pointer;
  }

  &--black {
    background: black;
    border: 0;
    color: white;

    &:hover {
      @media (min-width: 1025px) {
        background: rgba(black, 0.5);
      }
    }
  }

  &--white {
    background: white;
    color: black;

    &:hover {
      @media (min-width: 1025px) {
        background: $Color--yellow;
        color: black;
      }
    }
  }

  &--disabled {
    opacity: 0.5;
    pointer-events: none;
  }

  &--full-width {
    width: 100%;
  }

  &--selected {
    background: black;
    color: white;
  }
}

.link {
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 1px;
  position: relative;

  &::after {
    content: "";
    border: 0;
    border-left: 3px solid white;
    border-top: 3px solid transparent;
    border-bottom: 3px solid transparent;
    display: block;
    position: absolute;
    top: 8px;
    right: -10px;
    transition: all .3s cubic-bezier(.42,0,.58,1);
    width: 0;
    height: 0;
  }
}

.underline {
  display: inline-block;
  position: relative;

  &::after {
    background: black;
    content: "";
    position: absolute;
    left: 0;
    bottom: -2px;
    width: 100%;
    height: 2px;
  }
}