.pdp {
  &__form {
    width: 100%;
  }

  &__form-bottom {
    display: flex;
    justify-content: space-between;
    height: 60px;

    @media (max-width: 599px) {
      height: 42px;  
    }
  }

  &__quantity-selector {
    flex: 0 0 115px;
    padding-right: 15px;
    height: 60px;

    @media (max-width: 599px) {
      height: 42px;  
    }
  }

  &__quantity-control {
    align-items: center;
    border: 2px solid black;
    border-radius: 5px;
    color: black;
    display: flex;
    height: 100%;
  }

  &__quantity-button {
    align-items: center;
    cursor: pointer;
    display: flex;
    flex: 0 0 30px;
    justify-content: center;
    height: 100%;
  }

  &__quantity-input {
    align-items: center;
    display: flex;
    flex: 0 0 30px;
    font-size: 14px;
    justify-content: center;
    text-align: center;
    height: 100%;
  }

  &__btn {
    border-radius: 5px;
    white-space: auto;
    height: 100%;

    @media (max-width: 599px) {
      line-height: 42px;  
    }
  }
}

.rc-container-wrapper {
  display: none !important;
}

.subs__label {
  align-items: center;
  background: rgba($Color--green, 0.5);
  border-radius: 5px;
  cursor: pointer;
  display: flex;
  flex: 1;
  justify-content: space-between;
  padding: 0 15px 0 46px;
  position: relative;
  width: 100%;
  min-height: 60px;

  &--grid {
    background: white;
    padding: 0 15px 0 38px;
    min-height: 30px;
  }

  &::before {
    background: white;
    border-radius: 50%;
    border: 1px solid black;
    content: "";
    display: inline-block;
    position: absolute;
    left: 15px;
    top: 50%;
    transform: translateY(-50%);
    width: 16px;
    height: 16px;
  }

  &::after {
    background: white;
    border-radius: 50%;
    content: "";
    display: inline-block;
    position: absolute;
    left: 18px;
    top: 50%;
    transform: translateY(-50%);
    transition: all 0.3s $cubic;
    width: 10px;
    height: 10px;
  }

  &-title {
    font-family: 'Interstate Cond';
    font-size: 16px;
    font-weight: 300;
    letter-spacing: 0.5px;
    line-height: 29px;
    margin-top: 1px;

    &--grid {
      @media (max-width: 599px) {
        font-size: 14px;  
      }
    }
  }

  &-price {
    font-family: 'Interstate Cond';
    font-size: 14px;
    font-weight: 700;
    letter-spacing: 0;
    line-height: 30px;
  }
}

.subs__radio {
  display: none;

  &-wrapper {
    margin-bottom: 15px;

    &--grid {
      background: white;
      border-radius: 5px;
      margin-bottom: 0;
      opacity: 0;
      position: absolute;
      left: 15px;
      bottom: 0;
      visibility: hidden;
      transition: all 0.3s $cubic;
      transition-delay: 0.1s;
      width: calc(100% - 30px);
      padding: 15px 0;
      z-index: 1;
    }
  }

  &:checked + label::after {
    background: black;
  }

  &-item {
    & + * {
      margin-top: 15px;
    }

    &--grid {
      & + * {
        margin-top: 0;
        padding-top: 5px;
      }
    }
  }
}

.subs__select {
  -webkit-appearance: none;
  -moz-appearance: none;

  background: rgba($Color--yellow, 0.15) url(data:image/png;base64,) calc(100% - 5px) center no-repeat;
  border: 2px solid black;
  border-radius: 5px;
  font-family: 'Interstate Cond';
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 0.5px;
  line-height: 29px;
  margin-top: 15px;
  outline: none;
  padding: 0 15px;
  transition: all 0.3s $cubic;
  width: 100%;
  height: 60px;

  &--grid {
    background: white url(data:image/png;base64,) calc(100% - 5px) center no-repeat;
    border: 1px solid black;
    border-radius: 3px;
    display: block;
    margin: 0 auto 5px;
    width: calc(100% - 30px);
    height: 30px;
  }

  &-wrapper {
    max-height: 0;
    overflow: hidden;
    transition: all 0.3s $cubic;

    &--active {
      max-height: 75px;
    }
  }
}
