.SupportDetails {
  padding: 116px 0 106px 0;
  background: #fdd174;
  position: relative;
  @media screen and (max-width: 767.5px) {
    padding: 70px 0 70px 0;
  }
  &::after {
    content: "";
    position: absolute;
    top: -14px;
    left: 0;
    width: 100%;
    height: 68px;
    z-index: 0;
    background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/Desktop-Border-Illustration_2.svg?v=1662012676);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
  }
  &::before {
    content: "";
    position: absolute;
    bottom: -46px;
    left: 0;
    width: 100%;
    height: 70px;
    z-index: 0;
    background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/Bottom_Line.svg?v=1662017907);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
    @media screen and (max-width: 767.5px) {
        height: 50px;
    }
  } 
  &__Container {
    max-width: 740px;
    margin: 0 auto;
    @media screen and (max-width: 991.5px) {
      max-width: 668px;
    }
    @media screen and (max-width: 767.5px) {
      max-width: 100%;
      padding: 0 15px;
    }
  }
  &__Head {
    color: #000000;
    font-family: "Gazpacho";
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 2.5px;
    line-height: 75px;
    text-align: center;
    margin: 0 auto;
    padding: 0 0 20px 0;
    max-width: fit-content;
    position: relative;
    @media screen and (max-width: 767.5px) {
      font-size: 32px;
      letter-spacing: 0.7px;
      line-height: 41.6px;
      max-width: calc(100% - 70px);
      margin-left: auto;
      margin-right: auto;
      padding: 0 0 15px 0;
    }
    &::before {
      content: "";
      position: absolute;
      width: 45px;
      height: 45px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/Purple_Star_Group.png?v=1662014428);
      background-repeat: no-repeat;
      background-size: contain;
      top: -5px;
      left: -46px;
      @media screen and (max-width: 767.5px) {
        top: -14px;
        left: -27px;
      }
      @media screen and (max-width: 375px) {
        top: -17px;
        left: -22px;
        transform: rotate(27deg);
      }
    }
    &::after {
      content: "";
      position: absolute;
      width: 45px;
      height: 60px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/a1.png?v=1661325220);
      background-repeat: no-repeat;
      background-size: contain;
      top: -10px;
      right: -44px;
      z-index: 9;
      @media screen and (max-width: 767.5px) {
        top: -30px;
        right: 0px;
        transform: rotate(70deg);
      }
      @media screen and (max-width: 375px) {
        // top: -46px;
        // right: -16px;
      }
    }
  }
  &__Details {
    color: #000000;
    font-family: Interstate Cond;
    font-size: 18px;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    text-align: center;
    margin: 0;
    padding: 0 0 40px 0;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
      padding: 0 0 30px 0;
    }
  }
  &__ButtonWrap {
    display: flex;
    justify-content: center;
  }
  &__Button {
    color: #ffffff;
    font-family: Interstate;
    font-size: 14px;
    letter-spacing: 0.88px;
    line-height: 30px;
    text-align: center;
    padding: 13px 22px 9px 23px;
    width: auto;
    @media screen and (max-width: 767.5px) {
        padding: 11px 42px;
        font-size: 12px;
        letter-spacing: 1px;
        line-height: 22px;
        height: 44px;
    }
  }
}
