.BannerV2Wrap{
    position: relative;
    z-index: 1;
}
.BannerV2{
    padding: 147px 0 171px 0;
    background-repeat: no-repeat;
    background-size: cover !important;
    background-position: center bottom !important;
    position: relative;
    @media screen and (max-width: 767.5px) {
        background-position: 66% 82% !important;
        padding: 122px 0 136px 0;
    }
    &::after{
        position: absolute;
        content: "";
        bottom: -21px;
        left: 0;
        width: 100%;
        height: 80px;
        background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/Desktop-Border-Illustration.svg?v=1661413420);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center bottom;
        z-index: -1;
        transform: rotateX(180deg);
        display: none;
    }
    &__Container{
        max-width: 100%;
        @media screen and (max-width: 767.5px) {
            padding: 0 15px;
        }
    }
    &__Head{
        color: #FFFFFF;
        font-family: "Gazpacho";
        font-size: 65px;
        font-weight: bold;
        letter-spacing: 1.8px;
        line-height: 84.5px;
        text-align: center;
        margin: 0 auto;
        @media screen and (max-width: 767.5px) {
            font-size: 40px;
            letter-spacing: 0.9px;
            line-height: 45px;
        }
    }
}