.shadow-cards {
  padding-top: 100px;
  padding-bottom: 100px;

  @media (max-width: 1024px) {
    padding-top: 70px;
    padding-bottom: 70px;
  }

  &__container {
    @media (min-width: 1200px) {
      padding-left: 40px;
      padding-right: 40px;
    }
  }

  &__title {
    margin-bottom: 20px;

    @media (max-width: 1024px) {
      margin-bottom: 15px;  
    }
  }

  &__copy {
    color: black;
    font-family: 'Interstate Cond';
    font-size: 18px;
    font-weight: 300;
    line-height: 34px;
    letter-spacing: 0.7px;
    margin: 0 auto 60px;
    max-width: 740px;
    text-align: center;

    @media (max-width: 1024px) {
      font-size: 16px;
      line-height: 30px;
      letter-spacing: 0.5px;
    }

    @media (max-width: 599px) {
      margin-bottom: 70px;  
    }
  }

  &-list {
    display: grid;
    gap: 50px;
    grid-template-columns: repeat(3, 1fr);

    @media (max-width: 1024px) {
      gap: 20px;  
    }

    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);  
    }

    @media (max-width: 599px) {
      gap: 30px;  
    }

    &-item {
      $self: &;
      position: relative;
      text-align: center;
      
      &::before {
        background: $Color--orange;
        border-radius: 15px 26px 26px 26px;
        content: "";
        position: absolute;
        right: 0;
        bottom: 0;
        transition: all 0.3s $cubic;
        width: 100%;
        height: 100%;
      }

      &__content {
        background: $Color--violet;
        border-radius: 10px;
        color: white;
        padding: 43px 40px;
        position: relative;
        transition: all 0.3s $cubic;
        width: 100%;
        height: 100%;

        @media (max-width: 1024px) {
          padding: 44px 15px;  
        }
      }

      &__title {
        margin-bottom: 15px;
      }

      &__copy {
        font-family: 'Interstate Cond';
        font-size: 16px;
        font-weight: 300;
        letter-spacing: 0.4px;
        line-height: 30px;
        margin-bottom: 20px;

        @media (max-width: 1024px) {
          font-size: 14px;
          line-height: 25px;
          letter-spacing: 0.5px;
        }
      }

      &:hover {
        &::before {
          width: calc(100% + 15px);
          height: calc(100% + 15px);
        }

        #{$self}__content {
          transform: translate(-15px, -15px);
        }
      }
    }
  }
}