.SimpleContent{
    padding: 227px 0 100px 0;
    @media screen and (max-width: 767.5px) {
        padding: 134px 0 70px;
    }
    &__Container{
        max-width: 740px;
        margin: 0 auto;
        @media screen and (max-width: 319px) {
            padding: 0 15px;
        }
        @media screen and (min-width: 320px) {
            padding: 0 15px;
        }
        @media screen and (min-width: 575px) {
            max-width: 480px;
            padding: 0;
        }
        @media screen and (min-width: 600px) {
            max-width: 540px;
        }
        @media screen and (min-width: 768px) {
            max-width: 690px;
        }
        @media screen and (min-width: 992px) {
            max-width: 870px;
        }
        @media screen and (min-width: 1024px) {
            max-width: 940px;
        }
    }
    &__Title-inner{
        text-align: center;
        margin: 0 auto 40px;
        position: relative;
        max-width: max-content;
        @media screen and (max-width: 767.5px) {
            margin: 0 auto 30px;
        }
        @media screen and (max-width: 575.5px) {
            max-width: 290px;
        }
        &::after{
            content: "";
            position: absolute;
            width: 60px;
            height: 60px;
            background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/Purple_Star_Group.png?v=1662014428);
            background-repeat: no-repeat;
            background-size: contain;
            top: -25px;
            left: -65px;
            @media screen and (max-width: 767.5px) {
                width: 40px;
                height: 40px;
                top: -12px;
                left: -48px;
            }
            @media screen and (max-width: 575.5px) {
                top: -13px;
                left: 17px;
            }
        }
        &::before {
            content: "";
            position: absolute;
            background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/a1.png?v=1661325220);
            top: -25px;
            right: -60px;
            background-size: contain;
            background-repeat: no-repeat;
            width: 60px;
            height: 60px;
            @media screen and (max-width: 767.5px) {
                width: 40px;
                height: 40px;
                top: -20px;
                right: -50px;
            }
            @media screen and (max-width: 575.5px) {
                right: 10px;
                top: -16px;
            }
        }
    }
    &__Title{
        color: #000000;
        font-family: "Gazpacho";
        font-size: 65px;
        font-weight: bold;
        letter-spacing: 1.8px;
        line-height: 80px;
        text-align: center;
        @media screen and (max-width: 767.5px) {
            font-size: 40px;
            letter-spacing: 0.9px;
            line-height: 44px;
        }
    }
    &__Head{
        color: #000000;
        font-family: Interstate;
        font-size: 21px;
        letter-spacing: 0.3px;
        line-height: 29.4px;
        margin-bottom: 20px;
        @media screen and (max-width: 767.5px) {
            font-size: 16px;
            letter-spacing: 0.3px;
            line-height: 27.2px;
            margin-bottom: 10px;
        }
    }
    &__Details{
        color: #000000;
        font-family: Interstate Cond;
        font-size: 16px;
        letter-spacing: 0.5px;
        line-height: 28.8px;
        @media screen and (max-width: 767.5px) {
            font-size: 14px;
            letter-spacing: 0.5px;
            line-height: 25.2px;
        }
    }
    &__Block-inner{
        margin-bottom: 64px;  
        @media screen and (max-width: 767.5px) {
            margin-bottom: 25px;  
        }
    }
    &__Call-btn{
        margin: 0 auto;
        display: flex;
        justify-content: center;
    }
    &__Button{
        box-sizing: border-box;
        border: 2px solid #000000;
        border-radius: 5px;
        background-color: #FDD174;
        color: #000000;
        font-family: Interstate;
        font-size: 14px;
        letter-spacing: 0.88px;
        line-height: 30px;
        text-align: center;
        padding: 13px 22px;
        @media screen and (max-width: 767.5px) {
            padding: 10px 15px; 
            font-size: 12px;
            line-height: 20px;
            width: 150px;
            height: 45px;
        }
    }
}