.blog {
  background: $Color--gray; 
  padding-top: 100px;
  padding-bottom: 100px;

  @media (max-width: 1024px) {
    padding-top: 70px;
    padding-bottom: 70px;
  }

  &__container {
    display: flex;
    flex-wrap: wrap;
    
    @media (min-width: 1201px) {
      padding-left: 40px;
      padding-right: 40px;
    }
  }

  &__sidebar {
    flex: 0 0 217px;

    @media (max-width: 1024px) {
      flex: 0 0 170px;  
    }

    @media (max-width: 700px) {
      flex: 0 0 100%;
    }
  }

  &__form {
    margin-bottom: 38px;
    position: relative;

    @media (max-width: 700px) {
      margin-bottom: 30px;
    }

    &-text {
      border: 2px solid black;
      border-radius: 5px;
      font-family: 'Interstate Cond';
      font-size: 16px;
      font-weight: 300;
      letter-spacing: 0.5px;
      outline: none;
      padding: 0 51px 0 16px;
      width: 100%;
      height: 51px;

      @include input-placeholder {
        color: black;
      }
    }

    &-submit {
      background: url('icon-search.png') center/18px 18px no-repeat;
      border: 0;
      position: absolute;
      top: 0;
      right: 0;
      text-indent: -9999px;
      width: 51px;
      height: 51px;
    }
  }

  &__title {
    margin-bottom: 15px;

    @media (max-width: 700px) {
      display: none;
    }
  }

  &__content {
    flex: 1;

    @media (max-width: 700px) {
      flex: 0 0 100%;
    }
  }

  .article__list {
    margin: 0 auto 0 38px;
    max-width: none;

    @media (max-width: 700px) {
      margin: 0 auto;
    }
  }

  &__pagination {
    margin-left: auto;
    max-width: 980px;
    padding-top: 33px;
    text-align: center;

    > * {
      display: inline-block;
      font-family: 'Interstate Cond';
      font-size: 16px;
      font-weight: 300;
      line-height: 26px;
      letter-spacing: 0.5px;
      margin: 0 6px;
      position: relative;
      width: 13px;

      &.current {
        border-bottom: 2px solid black;
        font-weight: 700;
      }
    }
  }

  .article__list {
    @media (max-width: 1124px) {
      grid-template-columns: repeat(2, 1fr);  
    }

    @media (max-width: 499px) {
      grid-template-columns: repeat(1, 1fr); 
    }
  }
}

.nav-category {
  @media (max-width: 700px) {
    display: none;
  }

  &__item {
    margin-bottom: 5px;

    &-name {
      font-family: 'Interstate Cond';
      font-size: 18px;
      font-weight: 300;
      line-height: 34px;
      letter-spacing: 0.7px;
      text-transform: capitalize;

      &--selected {
        color: $Color--violet;
        font-weight: 700;
      }
    }
  }
}

.mobile-nav-categories {
  -webkit-appearance: none;
  -moz-appearance: none;

  background: $Color--yellow url('icon-select.svg') calc(100% - 20px) center/10px 6px no-repeat;
  border: 2px solid black;
  border-radius: 3px;
  font-family: 'Interstate';
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 0.5px;
  margin-bottom: 50px;
  outline: none;
  padding: 0 30px 0 20px;
  text-indent: 1px;
  text-overflow: '';
  text-transform: uppercase;
  width: 100%;
  height: 51px;

  @media (min-width: 701px) {
    display: none;
  }
}