#ingredients {
  scroll-margin-top: 130px;
}

.ingredients {
  padding: 100px 0;

  #gift-card & {
    display: none;
  }
  

  @media (max-width: 1024px) {
    padding: 70px 0;  
  }
  
  &__container {
    text-align: center;
  }

  &__title {
    margin-bottom: 35px;
  }

  &__content {
    align-items: center;
    display: flex;
    justify-content: space-between;

    @media (max-width: 599px) {
      flex-direction: column;  
    }
  }

  &__list {
    flex: 0 0 calc(30% - 40px);

    @media (max-width: 599px) {
      order: 2;  
    }

    &-item {
      display: flex;
      text-align: left;

      @media (max-width: 1024px) {
        flex-direction: column;  
      }

      &-image-title {
        align-items: flex-start;
        display: flex;
        flex: 0 0 75px;

        @media (max-width: 1024px) {
          align-items: center;  
        }
      }

      &-img-wrapper {
        align-items: center;
        background: $Color--green;
        border-radius: 15px;
        display: flex;
        flex-shrink: 0;
        justify-content: center;
        margin-right: 20px;
        width: 75px;
        height: 75px;

        @media (max-width: 1024px) {
          margin-right: 15px;
          width: 50px;
          height: 50px;
        }
      }

      &-img {
        object-fit: contain;
        width: 53px;
        height: 53px;

        @media (max-width: 1024px) {
          width: 34px;
          height: 34px;
        }
      }

      &-title {
        margin-bottom: 5px;
      }

      &-copy {
        font-family: 'Interstate Cond';
        font-size: 16px;
        font-weight: 300;
        line-height: 1.8;
        letter-spacing: 0.5px;

        @media (max-width: 1024px) {
          font-size: 14px;  
        }
      }

      & + * {
        margin-top: 100px;

        @media (max-width: 599px) {
          margin-top: 30px;  
        }
      }
    }

    &--first {
      @media (max-width: 599px) {
        margin-bottom: 30px;   
      }
    }
  }

  &__main-image-wrapper {
    flex: 0 0 40%;

    @media (max-width: 599px) {
      margin-bottom: 30px;
      order: 1;  
    }
  }
}
