.nav-inner {
  background: $Color--green;
  padding: 25px 50px;
  position: sticky;
  top: 100px;
  z-index: 2;

  @media (max-width: 1024px) {
    padding: 20px 0;
    top: 60px;
  }

  &__list {
    display: flex;
    justify-content: space-between;

    &-item {
      font-family: 'Interstate';
      font-size: 16px;
      font-weight: 700;
      line-height: 30px;
      letter-spacing: 0.8px;
      position: relative;
      text-transform: uppercase;

      @media (max-width: 1100px) {
        font-size: 14px;
        line-height: 21px;
      }

      &::after {
        background: black;
        content: "";
        display: block;
        position: absolute;
        left: 0;
        bottom: -2px;
        transition: all 0.3s $cubic;
        transform: scaleX(0);
        width: 100%;
        height: 2px;
      }

      &--active {
        &::after {
          transform: scaleX(1);
        }
      }
    }
  }

  &__select {
    background: $Color--green url('icon-select.svg') 100% center/10px 6px no-repeat;
    border: 0;
    font-size: 14px;
    line-height: 22px; 
    letter-spacing: 0.8px;
    text-align: center;
    text-transform: uppercase;
    width: 100%;
    height: 22px;
    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 1px;
    text-overflow: '';
  }
}