.FaqsSupport {
  background: $Color--gray;
  padding: 0px 0 94px 0;
  @media screen and (max-width: 768px) {
    padding: 55px 0 70px 0;
  }
  &__Menu-block {
    max-width: 100%;
    padding: 94px 40px 20px;
    position: sticky;
    top: 0px;
    width: 100%;
    background: $Color--gray;
    z-index: 1;
    margin-bottom: 60px;
    @media screen and (max-width: 1366px) {
      padding: 94px 30px 50px;
    }
    @media screen and (max-width: 1280px) {
      padding: 94px 15px 50px;
    }
    @media screen and (max-width: 991px) {
      padding: 30px 20px 30px;
      margin-bottom: 0;
    }

    @media (max-width: 768px) {
      top: 40px;  
    }
  }
  &__Row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    @media screen and (max-width: 768px) {
      flex-direction: column;
      display: none;
    }
  }
  &__Menu-item {
    padding: 10px 40px;
    text-align: center;
    @media screen and (max-width: 1500px) {
      padding: 10px 30px;
    }
    @media screen and (max-width: 768px) {
      padding: 0px 22px;
    }
  }
  &__Menu {
    color: #000000;
    font-family: Interstate;
    font-size: 16px;
    font-weight: 700;
    display: block;
    letter-spacing: 0.8px;
    line-height: 30.4px;
    text-align: center;
    text-transform: uppercase;
    opacity: 0.4;
    padding: 10px 0;
    transition: all 0.3s ease-in-out;
    border-bottom: 2px solid transparent;
    &:hover {
      color: #000000;
    }
    
    @media screen and (max-width: 1080px) {
      font-size: 15px;
      line-height: 25px;
    }
    @media screen and (max-width: 768px) {
      border-bottom: 0px;
    }
  }
  &__Menu-active {
    color: #000000;
    opacity: 1;
    border-color: #000000;
  }
  &__Dropdown {
    display: none;
    margin: 0 auto;
    max-width: 100%;
    @media screen and (max-width: 768px) {
      display: block;
      padding: 0;
    }
  }
  &__Dropdown-btn {
    position: relative;
    max-width: 100%;
    width: 100%;
    text-transform: uppercase;
    border: 2px solid #000000;
    border-radius: 5px;
    background-color: #fdd174;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    font: -apple-system-title2;
    &::before {
      content: "";
      position: absolute;
      right: 9px;
      top: 24px;
      width: 0;
      height: 0;
      border: 6px solid;
      border-color: #000000 transparent transparent;
    }
  }
  &__Container {
    max-width: 768px;
    margin: 0 auto;
    @media screen and (max-width: 991.5px) {
      max-width: 668px;
    }
    @media screen and (max-width: 768px) {
      max-width: 100%;
      padding: 0 20px;
    }
  }
  &__Head {
    color: #000000;
    font-family: "Gazpacho";
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 2.5px;
    line-height: 75px;
    text-align: center;
    margin: 0 auto;
    padding: 0 0 24px 0;
    max-width: fit-content;
    position: relative;
    @media screen and (max-width: 768px) {
      font-size: 32px;
      letter-spacing: 0.7px;
      line-height: 41.6px;
      padding: 0 0 15px 0;
    }
    &::after {
      position: absolute;
      content: "";
      width: 60px;
      height: 60px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/start_group_blue.png?v=1661160183);
      background-repeat: no-repeat;
      background-size: contain;
      top: -18px;
      right: -40px;
      transform: rotate(164deg);
      @media screen and (max-width: 768px) {
        width: 45px;
        height: 45px;
      }
    }
    &::before {
      position: absolute;
      content: "";
      width: 40px;
      height: 40px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/Star_-_Violet.png?v=1661161098);
      background-repeat: no-repeat;
      background-size: contain;
      bottom: 39px;
      left: -30px;
      @media screen and (max-width: 768px) {
        width: 30px;
        height: 30px;
        bottom: 18px;
        left: -22px;
      }
    }
  }
  &__Main {
    border-bottom: 0.5px solid #00000066;
    &:last-child {
      border-bottom: 0px;
    }
  }
  &__FaqInner__content {
    position: relative;
  }

  &__FAQ-tabcontent {
    border-bottom: 1px solid rgba(black, 0.2);
  }

  &__Question {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    align-items: center;
    padding: 29px 0;
    text-decoration: none;
    -webkit-transition: all 0.2s linear;
    -moz-transition: all 0.2s linear;
    transition: all 0.2s linear;
    color: #000000;
    font-family: 'Interstate Cond';
    font-size: 18px;
    font-weight: 300;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    column-gap: 15px;
    @media screen and (max-width: 768px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
      padding: 15px 0;
    }
  }
  &__Question.active {
    .FaqsSupport__Plus {
      svg {
        opacity: 0;
      }
    }
    .FaqsSupport__Minus {
      svg {
        opacity: 1;
      }
    }
  }
  &__Plus__Minus {
    -webkit-transition: all 0.2s linear;
    -moz-transition: all 0.2s linear;
    transition: all 0.2s linear;
    position: relative;
    min-width: 30px;
    width: 30px;
    height: 30px;
    background: #52339b;
    border-radius: 50%;
  }
  &__Plus {
    svg {
      width: 15px;
      height: auto;
      transition: all 0.2s linear;
      position: absolute;
      top: 7px;
      left: 7px;
      display: flex;
      align-items: center;
      justify-content: center;
      fill: #ffd275;
    }
  }
  &__Minus {
    svg {
      transition: all 0.2s linear;
      width: 15px;
      opacity: 0;
      height: auto;
      display: flex;
      align-items: center;
      justify-content: center;
      fill: #ffd275;
      position: absolute;
      top: 7px;
      left: 7px;
    }
  }
  &__Faq__Content {
    display: none;
  }
  &__Answer {
    color: #000000;
    font-family: Interstate Cond;
    font-size: 16px;
    font-weight: 300;
    letter-spacing: 0.5px;
    line-height: 28.8px;
    margin: 0 0 0 45px;
    padding: 0 0 33px 0;
    @media screen and (max-width: 768px) {
      font-size: 14px;
      letter-spacing: 0.5px;
      line-height: 25.2px;
      //margin: 0;
      padding: 0 0 15px 0;
    }
  }
}
