.collection-main {
  overflow-x: hidden;
  padding-top: 100px;
  padding-bottom: 100px;

  &__container {
    display: flex;

    @media (max-width: 767px) {
      flex-wrap: wrap;
    }
  }


  &__tabs {
    display: flex;
    justify-content: space-between;
    max-width: 1000px;
    margin: 0 auto 50px;
    overflow-x: auto;
    overflow-y: hidden;
    padding-left: 10px;
    padding-right: 10px;

    @media (max-width: 599px) {
      margin-bottom: 30px;
      padding-right: 20px;
      padding-left: 20px;
    }

    &::-webkit-scrollbar {
      display: none;
    }
    
    &::-webkit-scrollbar-track {
      opacity: 0;
    }
    
    &::-webkit-scrollbar-thumb {
      display: none;
    }  
  }

  &__tab {
    flex: 0 0 auto;
    font-size: 14px;
    line-height: 18px;
    opacity: 0.6;
    padding: 0 10px;
    position: relative;

    @media (max-width: 599px) {
      background: #f5f5f5;
      line-height: 32px;
      opacity: 1;
      height: 32px;

      & + * {
        margin-left: 10px;
      }

      a {
        color: rgba(black, 0.6);
      }
    }

    &--active {
      opacity: 0.96;

      @media (max-width: 599px) {
        background: #fad7ec;  
      }

      a {
        white-space: nowrap;

        &::after {
          content: "";
          display: block;
          position: absolute;
          left: 0;
          bottom: -4px;
          width: 100%;
          height: 1px;
        }
      }
    }
  }

  &__filter {
    @media (max-width: 767px) {
      overflow: hidden;
      max-height: 0;
      transition: all 0.4s $cubic;
    }

    &--active {
      max-height: 800px;
      padding-bottom: 30px;
    }
  }

  &__sidebar {
    flex: 0 0 auto;
    min-width: 0;
    position: relative;

    @media (max-width: 767px) {
      flex: 0 0 100%;
      padding-right: 0;
    }

    &-button {
      @media (min-width: 768px) {
        display: none;
      }

      align-items: center;
      background: $Color--yellow;
      border: 2px solid black;
      border-radius: 3px;
      cursor: pointer;
      font-size: 14px;
      font-weight: 700;
      letter-spacing: 0.8px;
      display: flex;
      justify-content: center;
      margin-bottom: 30px;
      position: relative;
      width: 100%;
      height: 60px;

      &::after {
        border-top: 7px solid black;
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        content: "";
        cursor: pointer;
        display: block;
        margin-top: -3px;
        pointer-events: none;
        position: absolute;
        right: 12px;
        top: 50%;
        width: 0;
        height: 0;
      }
    }
  }

  &__sort-item {
    display: flex;
    flex-direction: column;

    & + * {
      margin-top: 50px;

      @media (max-width: 767px) {
        margin-top: 25px;  
      }
    }
  }

  &__sort-title {
    font-family: 'Interstate Cond';
    font-size: 18px;
    line-height: 30px;
    letter-spacing: 0.4px;
    margin-bottom: 10px;
  }

  &__content {
    flex: 1;
    padding-left: 96px;

    @media (max-width: 767px) {
      flex: 0 0 100%;
      padding-left: 0;
    }
  }

  ul.pagination {
    //opacity: 0;
  }
}

.filter-item {
  align-items: center;
  display: flex;
  font-family: 'Interstate Cond';
  font-size: 16px;
  font-weight: 300;
  line-height: 29px;
  letter-spacing: 0.5px;

  i {
    align-items: center;
    border: 1px solid black;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    margin-right: 15px;
    width: 20px;
    height: 20px;

    &::before {
      background: transparent;
      border-radius: 3px;
      content: "";
      display: block;
      width: 12px;
      height: 12px;
    }
  }

  input[type="checkbox"] {
    display: none;

    &:checked + i { 
      &::before {
        background: black;
      }
    }
  }

  & + * {
    margin-top: 5px;
  }
}

.filter-item__list {
  .filter-item:nth-child(n + 3) {
    display: none;
  }
}