.grid-item-slide {
  $self: &;
  display: flex;
  flex-direction: column;

  &__inner {
    color: white;
    display: block;
    position: relative;
    text-align: center;
    height: 100%;

    &::after {
      background: rgba(black, 0.2);
      border-radius: 10px;
      content: "";
      display: block;
      pointer-events: none;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }

  &__image {
    border-radius: 10px;
    display: block;
    object-fit: cover;
    transition: all 0.4s $cubic;
    width: 100%;
    height: 100%;

    &-hover {
      border-radius: 10px;
      object-fit: cover;
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
      transition: all 0.4s $cubic;
      width: 100%;
      height: 100%;
    }
  }

  &__content {
    align-items: center;
    color: white;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    opacity: 0;
    padding: 15px 0 0;
    position: absolute;
    bottom: -100%;
    left: 0;
    text-align: center;
    transition: all 0.4s $cubic;
    width: 100%;
    z-index: 1;
  }

  &__title {
    @extend h4;
    
    font-family: 'Interstate Cond';
    margin-bottom: 8px;
    padding: 0 15px;

    &--main {
      margin-bottom: 30px;
      pointer-events: none;
      position: absolute;
      bottom: 0;
      left: 0;
      transition: all 0.4s $cubic;
      width: 100%;
      z-index: 1;

      @media (max-width: 599px) {
        font-size: 16px;
        line-height: 1.5;
        margin-bottom: 15px;
      }
    }
  }

  &__link {
    color: white;
  }

  &__btn {
    margin-top: auto;
  }

  &__copy {
    margin-bottom: 35px;
    text-transform: capitalize;
  }

  &:hover {
    //Desktop
    @media (min-width: 1025px) {
      #{$self}__image-hover {
        opacity: 1;
      }  

      #{$self}__content {
        bottom: -26px;
        opacity: 1;
      }

      #{$self}__title--main {
        opacity: 0;
        bottom: 100%;
      }
    }
  }
}