.c-bundle {
  padding-top: 100px;

  @media (max-width: 599px) {
    padding-top: 30px;
  }

  &__container {
    border: 2px solid black;
    border-radius: 10px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    overflow: hidden;
    padding: 0;

    @media (max-width: 599px) {
      border: 0;
      border-radius: 0;
    }
  }
  
  &__item {
    $self: &;
    align-items: center;
    background: $Color--yellow;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 190px;
    padding: 0 50px;
    text-align: center;
    transition: all 0.3s $cubic;

    @media (max-width: 1024px) {
      min-height: 0;
      padding: 14px;
    }

    @media (max-width: 700px) {
      background: #643BC3;
      color: white;
      min-width: 0;
    }
    
    &-title {
      margin-bottom: 8px;
      transition: all 0.3s $cubic;

      @media (max-width: 700px) {
        color: $Color--yellow;
      }
    }

    &-copy {
      font-family: 'Interstate Cond';
      font-size: 16px;
      font-weight: 700;
      line-height: 30px;
      letter-spacing: 0.8px;
      margin-bottom: 10px;
      text-transform: uppercase;
      transition: all 0.3s $cubic;

      @media (max-width: 599px) {
        font-size: 14px;
        line-height: 26px;
      }
    }

    &-btn {
      @media (max-width: 700px) {
        background: url('bundle-plus.svg') center/cover no-repeat;
        border: 0;
        border-radius: 50%;
        margin: 10px;
        min-width: 0;
        padding: 0;
        text-indent: -9999px;
        width: 30px;
        height: 30px;
      }

      &--active {
        background: $Color--yellow;
        border-color: transparent;

        @media (max-width: 700px) {
          background: transparent url('bundle-check.svg') center/cover no-repeat;
        }
      }
    }

    &:first-child {
      border-right: 2px solid black;

      @media (max-width: 700px) {
        border-right: 1px solid white;
      }
    }

    &:last-child {
      border-left: 2px solid black;

      @media (max-width: 700px) {
        border-left: 1px solid white;
      }
    }

    &--active {
      background: $Color--violet;

      @media (max-width: 700px) {
        background: #52339B;
      }

      #{$self}-title {
        color: $Color--yellow;
      }

      #{$self}-copy {
        color: white;
      }
    }
  }

  &-footer {
    background: $Color--violet;
    color: white;
    padding: 14px 0;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    z-index: 3;

    &__container {
      align-items: center;
      display: flex;
      justify-content: space-between;
      height: 100%;

      @media (max-width: 700px) {
        flex-direction: column;
      }
    }

    &__content {
      @media (max-width: 1240px) {
        flex: 0 0 200px;
      }

      @media (max-width: 700px) {
        flex: auto;
        text-align: center;
      }
    }

    &__title {
      color: $Color--yellow;
      margin-bottom: 5px;
    }

    &__copy {
      font-family: 'Interstate Cond';
      font-size: 16px;
      font-weight: 700;
      line-height: 30px;
      text-transform: uppercase;
    }

    &__checkmarks {
      align-items: center;
      display: flex;

      @media (max-width: 1240px) {
        flex: 1;
        flex-wrap: wrap;
        justify-content: center;
        padding: 0 20px;
      }
    }

    &__checkmark {
      background: url('bundle-plus.svg') center/cover no-repeat;
      border-radius: 50%;
      margin: 10px;
      width: 30px;
      height: 30px;

      @media (max-width: 1240px) {
        margin: 5px;
      }

      &--active {
        background: url('bundle-check.svg') center/cover no-repeat;
      }
    }

    &__btn {
      min-width: 270px;
      opacity: 0;
      visibility: hidden;

      @media (max-width: 700px) {
        display: none;
        margin-top: 20px;
      }

      &--active {
        opacity: 1;
        visibility: visible;

        @media (max-width: 700px) {
          display: inline-flex;
        }
      }
    }
  }

  & + .collection-main {
    @media (max-width: 599px) {
      padding-top: 70px;  
    }
  }
}