.StoreNear{
    padding: 120px 0 0 0;
    overflow: hidden;
    background-color: #fefaee;
    &__Container{
        margin: 0 auto;
        max-width: 100%;
    }
    &__Head{
        color: #000000;
        font-family: "Gazpacho";
        font-size: 50px;
        font-weight: bold;
        letter-spacing: 2.5px;
        line-height: 75px;
        text-align: center;
        margin: 0 auto;
        padding: 0 20px 10px 20px;
        max-width: fit-content;
        position: relative;
        &::after{
            content: "";
            position: absolute;
            width: 45px;
            height: 60px;
            background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/a2.png?v=1661325220);
            background-repeat: no-repeat;
            background-size: contain;
            top: -10px;
            right: -20px;
            @media screen and (max-width: 767.5px) {
                top: -28px;
            } 
            @media screen and (max-width: 575.5px) {
                top: -13px;
                right: 8px;
            }
        }
        &::before{
            content: "";
            position: absolute;
            width: 45px;
            height: 60px;
            background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/a1.png?v=1661325220);
            background-repeat: no-repeat;
            background-size: contain;
            top: -14px;
            left: -8px; 
            @media screen and (max-width: 767.5px) {
                top: -35px;
            }       
            @media screen and (max-width: 575.5px) {
                top: -11px;
                left: 5px;
            }
        }
        @media screen and (max-width: 991.5px) {
            font-size: 44px;
            padding: 0 15px 11px 15px;
        }
        @media screen and (max-width: 767.5px) {
            font-size: 32px;
            letter-spacing: 0.7px;
            line-height: 41.6px;
        }
        @media screen and (max-width: 575.5px) {
            max-width: 325px;
            top: -11px;
            left: 5px;
        }
    }
    &__Details{
        max-width: 640px;
        margin: 0 auto;
        color: #000000;
        font-family: Interstate Cond;
        font-size: 18px;
        letter-spacing: 0.7px;
        line-height: 34.2px;
        text-align: center;
        padding: 0 0 40px 0;
        @media screen and (max-width: 767.5px) {
            font-size: 16px;
            letter-spacing: 0.5px;
            line-height: 30.4px;
            padding: 0 15px 28px 15px;
        }
    }
    &__Img{
        display: block;
    }
    &__Button{
        text-align: center;
        display: flex;
        width: fit-content;
        justify-content: center;
        margin: 0 auto;
        @media screen and (max-width: 767.5px) {
            font-size: 12px;
            letter-spacing: 0.5px;
            line-height: 22px;
            margin: 0 auto 30px auto;
        }
    }
}