.StorySustainability {
  padding: 90px 0;
  background-color: #fefaee;
  @media screen and (max-width: 767.5px) {
    padding: 50px 15px;
  }
  &__container {
    
  }
  &__Con-inner {
    margin-bottom: 40px;
  }
  &__Heading {
    position: relative;
    max-width: 55%;
    margin: 0 auto;
    @media screen and (max-width: 1280px) {
      max-width: 65%;
    }
    @media screen and (max-width: 1100px) {
      max-width: 75%;
    }
    @media screen and (max-width: 991px) {
      max-width: 88%;
    }
    &::after {
      position: absolute;
      content: "";
      width: 60px;
      height: 60px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/start_group_blue.png?v=1661160183);
      background-repeat: no-repeat;
      background-size: contain;
      top: -18px;
      right: -40px;
      @media screen and (max-width: 767.5px) {
        width: 40px;
        height: 40px;
        right: -20px;
      }
    }
    &::before {
      position: absolute;
      content: "";
      width: 40px;
      height: 40px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/Star_-_Violet.png?v=1661161098);
      background-repeat: no-repeat;
      background-size: contain;
      bottom: 39px;
      left: -30px;
      top: 10px;
      @media screen and (max-width: 767.5px) {
        width: 30px;
        height: 30px;
        left: -18px;
      }
    }
  }
  &__Title {
    color: #000000;
    font-family: "Gazpacho";
    font-size: 50px;
    letter-spacing: 2.5px;
    line-height: 75px;
    text-align: center;
    margin-bottom: 15px;
    @media screen and (max-width: 767.5px) {
      font-size: 32px;
      line-height: 41.6px;
    }
  }
  &__Sub-title {
    color: #000000;
    font-family: Interstate Cond;
    font-size: 18px;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    text-align: center;
    max-width: 60%;
    margin: 0 auto;
    @media screen and (max-width: 1280px) {
      max-width: 65%;
    }
    @media screen and (max-width: 1100px) {
      max-width: 75%;
    }
    @media screen and (max-width: 991px) {
      max-width: 100%;
    }
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      line-height: 30.4px;
    }
  }
  &__Layout-main {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 50px;

    @media (max-width: 1024px) {
      grid-template-columns: repeat(2, 1fr);
      gap: 30px;
    }
    
    @media (max-width: 599px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__Layout-inner {
    &:nth-child(3n + 1) {
      .StorySustainability__Box-block:hover {
        border: 15px solid #ec1f22;
        border-top: 0;
        border-left: 0;
        border-radius: 15px 26px 26px 26px;
        transform: scale(1.01);
      }
    }
    &:nth-child(3n + 2) {
      .StorySustainability__Box-block:hover {
        border: 15px solid #dd2990;
        border-top: 0;
        border-left: 0;
        border-radius: 15px 26px 26px 26px;
        transform: scale(1.01);
      }
    }
    &:nth-child(3n + 3) {
      .StorySustainability__Box-block:hover {
        border: 15px solid #219dcf;
        border-top: 0;
        border-left: 0;
        border-radius: 15px 26px 26px 26px;
        transform: scale(1.01);
      }
    }
  }
  &__Box-block {
    background-color: #ffffff;
    padding: 45px 25px 30px 40px;
    border-radius: 15px;
    border: 15px solid transparent;
    border-top: 0;
    border-left: 0;
    transition: all 0.3s ease-in-out;
    @media screen and (max-width: 1280px) {
      padding: 30px 15px 15px 30px;
    }
  }
  &__Box-img {
    margin-bottom: 25px;
    display: flex;
    justify-content: center;
  }
  &__Img {
    width: 45px;
    height: 100%;
  }
  &__Box-title {
    color: #000000;
    font-family: "Gazpacho";
    font-size: 23px;
    letter-spacing: 0.5px;
    line-height: 32.2px;
    text-align: center;
    margin-bottom: 25px;
    @media screen and (max-width: 767.5px) {
      font-size: 18px;
      line-height: 28.8px;
    }
  }
  &__Box-sub-title {
    color: #000000;
    font-family: Interstate Cond;
    font-size: 16px;
    letter-spacing: 0.5px;
    line-height: 28.8px;
    text-align: center;
    @media screen and (max-width: 767.5px) {
      font-size: 14px;
      line-height: 25.2px;
    }
  }
}
