.BannerWrap{
    position: relative;
    z-index: 2;

    &::before {
        content: "";
        display: none;
        position: absolute;
        left: 0;
        width: 100%;
        height: 96px;
        background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/BottomLine_52339b.svg?v=1662028205);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center bottom;
        bottom: -21px;
        z-index: -1;
        transform: rotate(180deg);
        @media screen and (max-width: 767.5px) {
            transform: rotate(0);
            background-size: cover;
            background-position: left;
            transform: rotate(0);
            bottom: -25px;
        }
    }
}
.Banner{
    padding: 158px 0 105px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center bottom;
    position: relative;
    z-index: 1;

    @media screen and (max-width: 767.5px) {
        padding: 97px 15px 88px 15px !important;
        background-position: 66% 50%;
    }
    &__Container{
        max-width: 100%;
    }
    &__Head{
        color: #FFFFFF;
        font-family: "Gazpacho";
        font-size: 60px;
        font-weight: bold;
        letter-spacing: 1.71px;
        line-height: 66px;
        margin: 0;
        padding: 0 0 20px 0;
        text-align: center;
        @media screen and (max-width: 767.5px) {
            font-size: 40px;
            line-height: 45px;
        }
    }
    &__Content{
        color: #FFFFFF;
        font-family: Interstate Cond;
        font-size: 18px;
        letter-spacing: 0.7px;
        line-height: 34.2px;
        margin: 0 auto;
        text-align: center;
        max-width: 505px;
        @media screen and (max-width: 767.5px) {
            font-size: 16px;
            line-height: 30.4px;
            letter-spacing: 0.5px;
        }
        @media screen and (max-width: 575px) {
            max-width: 100%;
            padding: 0 15px;
        }
    }
    &__Button-wrapper {
        display: flex;
        justify-content: center;
        margin-top: 30px;

        .btn {
            margin: 0 10px;
        }
    }
}