/* Cart Sidebar */
.site-header__sidecart-wrapper {
  background: #fff;
  padding: 20px;
  position: fixed;
  right: 0;
  top: 100px;
  transform: translateX(calc(100% + 16px));
  transition: all 0.3s $cubic;
  width: 450px;
  height: calc(100vh - 100px);
  z-index: 6;

  @media (max-width: 1024px) {
    width: 350px;  
  }

  @media (max-width: 599px) {
    top: 61px;
    width: 100%;  
    height: calc(100vh - 61px);
  }

  &.is-Offcanvas--open {
    transform: translateX(0);
  }
}

/* Cart Sidebar content */
.site-header__sidecart {
  opacity: 0;
  margin-left: -20px;
  transition: all 0.3s $cubic;
  transition-delay: 0s;
  width: 100%;
  height: 100%;

  .is-Offcanvas--open & {
    margin-left: 0;
    opacity: 1;
    transition-delay: 0.3s;
  }
}

/* Cart Sidebar content header */
.ajax-cart {
  height: 100%;

  &__header {
    align-items: stretch;
    border-bottom: 1px solid #DBD8D8;
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
    padding-bottom: 15px;
    display: none;

    @media (max-width: 1024px) {
      flex-direction: row-reverse;
      padding-bottom: 10px;
    }
  }

  &__close {
    background: #F2F3F3;
    border: 0;
    font-size: 18px;
    font-weight: 700;
    padding: 0;
    width: 48px;
    height: 48px;

    @media (max-width: 1024px) {
      font-size: 11px;
      width: 24px;
      height: 24px;
    }
  }

  &__title {
    align-items: center;
    display: flex;
    font-size: 24px;
    text-transform: uppercase;

    @media (max-width: 1024px) {
      font-size: 14px;  
    }

    span {
      color: rgba(0, 0, 0, 0.6);
      font-family: Karla;
      font-size: 16px;
      margin-left: 10px;
      position: relative;
      text-transform: capitalize;
      top: -1px;

      @media (max-width: 1024px) {
        font-size: 12px;  
      }
    }
  }

  &__free-shipping {
    font-size: 10px;
    line-height: 18px;
    text-align: center;
  }

  .shipping-bar {
    border-radius: 5px;
    border: 1px solid black;
    display: block;
    margin: 9px auto 0;
    padding: 2px 4px;
    width: 300px;
    height: 10px;

    .bar {
      background: black;
      width: 100%;
      height: 4px;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    height: calc(100% - 136px);

    &::-webkit-scrollbar-thumb {
      display: none;
    }
  }

  &__bottom {
    margin-top: auto;
  }

  &__empty {
    padding-top: 20px;
  }
}


@import "./item.scss";
@import "./totals.scss";