.BestBarQuiz {
  padding: 200px 0 100px 0;
  @media screen and (max-width: 767.5px) {
    padding: 104px 0 90px 0;
  }
  display: block;
  &__Container {
    margin: 0 auto;
    @media screen and (max-width: 319px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 320px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 575px) {
      max-width: 480px;
      padding: 0;
    }
    @media screen and (min-width: 600px) {
      max-width: 540px;
    }
  }
  &__Head {
    color: #000000;
    font-family: "Gazpacho";
    font-size: 65px;
    font-weight: bold;
    letter-spacing: 1.8px;
    line-height: 80px;
    text-align: center;
    margin: 0 auto;
    padding: 0 0 20px 0;
    @media screen and (max-width: 767.5px) {
      color: #000000;
      font-family: "Gazpacho";
      font-size: 40px;
      font-weight: bold;
      letter-spacing: 0.9px;
      line-height: 44px;
      text-align: center;
    }
  }
  &__DetailsWrap {
    display: flex;
    justify-content: center;
    row-gap: 20px;
    flex-wrap: wrap;
  }
  &__Details {
    flex: 0 0 100%;
    max-width: 100%;
    color: #000000;
    font-family: Interstate Cond;
    font-size: 18px;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    text-align: center;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
    }
  }
  &__ButtonWrap {
    display: flex;
    justify-content: center;
    padding: 40px 0 0 0;
  }
  &__Button {
    width: auto;
    padding: 10px;
    @media screen and (max-width: 767.5px) {
      height: 42px;
      font-size: 12px;
      letter-spacing: 0.5px;
      line-height: 22px;
    }
  }
}

// Quiz Step
.QuizStep {
  padding: 200px 0 100px 0;
  display: none;
  @media screen and (max-width: 767.5px) {
    padding: 104px 0 70px 0;
  }
  &__Container {
    max-width: calc(100% - 200px);
    margin: 0 auto;
    @media screen and (max-width: 991px) {
      max-width: calc(100% - 50px);
    }
    @media screen and (max-width: 767.5px) {
      max-width: 575px;
    }
    @media screen and (max-width: 640px) {
      max-width: 475px;
    }
    @media screen and (max-width: 575.5px) {
      padding: 0 20px;
    }
  }
  &__Question {
    color: #000000;
    font-family: Interstate;
    font-size: 21px;
    letter-spacing: 0.3px;
    line-height: 29.4px;
    text-align: center;
    margin: 0 auto;
    padding: 0 0 20px 0;
    @media screen and (max-width: 767.5px) {
      font-size: 18px;
      letter-spacing: 0.6px;
      line-height: 28.8px;
      margin: 0 -15px;
      padding: 0 15px 15px 15px;
    }
  }
  &__AnswerWrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 60px;
    row-gap: 30px;
    @media screen and (max-width: 1130.5px) {
      column-gap: 110px;
    }
    @media screen and (max-width: 800.5px) {
      column-gap: 70px;
    }
    @media screen and (max-width: 767.5px) {
      column-gap: 0;
      row-gap: 30px;
    }
  }
  &__Answer {
    flex: 0 0 140px;
    max-width: 25%;
    @media screen and (max-width: 1100px) {
      flex: 0 0 103px;
    }
    @media screen and (max-width: 767.5px) {
      max-width: 50%;
      flex: 0 0 50%;
    }
  }
  &__ImgWrap {
    border-radius: 15px;
    background-color: rgba(253, 209, 116, 0.2);
    padding: 23px 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid transparent;
    max-width: 105px;
    margin: 0 auto;
    &:hover,
    &.selected {
      border: 2px solid #000000;
      border-radius: 15px;
      background-color: #fdd174;
    }
  }
  &__Img {
    max-width: 60px;
    max-height: 64px;
  }
  &__AnswerDetails {
    color: #000000;
    font-family: Interstate Cond;
    font-size: 18px;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    text-align: center;
    margin: 0;
    font-weight: 400;
    padding: 0 0 5px 0;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
    }
  }
  &__ButtonWrap {
    padding: 60px 0 40px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    @media screen and (max-width: 767.5px) {
      padding: 30px 0 30px 0;
    }
  }
  &__BackButton {
    width: auto;
    padding: 10px 65px;
    color: #000000;
    font-family: Interstate;
    font-size: 14px;
    letter-spacing: 0.88px;
    line-height: 30px;
    text-align: center;
    margin: 0 40px 0 0;
    @media screen and (max-width: 767.5px) {
      padding: 10px 49px;
      font-size: 12px;
      letter-spacing: 0.5px;
      line-height: 22px;
      height: 42px;
      margin: 0 15px 0 0;
    }
  }
  &__NextButton {
    width: auto;
    padding: 10px 65px;
    color: #000000;
    font-family: Interstate;
    font-size: 14px;
    letter-spacing: 0.88px;
    line-height: 30px;
    text-align: center;
    @media screen and (max-width: 767.5px) {
      padding: 10px 49px;
      font-size: 12px;
      letter-spacing: 0.5px;
      line-height: 22px;
      height: 42px;
    }
  }
  &__EatProgress {
    padding: 40px 0 0 0;
  }
  &__EatDetailsWrap {
    display: flex;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    max-width: 915px;
    margin: 0 auto;
    padding: 0px 0 0 0;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      background-color: #000000;
      height: 2px;
      width: 75%;
      left: 50%;
      transform: translateX(-50%);
    }

    @media screen and (max-width: 767.5px) {
      margin-top: 100px;
    }
  }
  &__EatDetails {
    color: #000000;
    font-family: Interstate Cond;
    font-size: 18px;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    text-align: center;
    flex: 0 0 25%;
    max-width: 25%;
    padding: 35px 0px 0px;
    position: relative;
    &:after {
      content: "";
      width: 1.5px;
      height: 10px;
      display: block;
      background: #000;
      position: absolute;
      top: -4px;
      left: 50%;
      transform: translateX(-50%);
      @media screen and (max-width: 767.5px) {
        top: 0px;
        height: 30px;
      }
    }
    &:before {
      content: "";
      width: 26px;
      height: 26px;
      display: block;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/Star_-_Black.svg?v=1662467380)
        no-repeat center center;
      background-size: contain;
      position: absolute;
      top: -12px;
      left: 50%;
      transform: translateX(-50%);
      opacity: 0;
      transition: all 0.3s ease-in-out;
    }
    &.active {
      &:before {
        opacity: 1;
      }
    }
    &:nth-child(odd) {
      @media screen and (max-width: 767.5px) {
        margin-top: -63px;
        padding: 0px 0px 35px;
      }
      &:after {
        @media screen and (max-width: 767.5px) {
          top: unset;
          bottom: 0px;
        }
      }
      &:before {
        @media screen and (max-width: 767.5px) {
          top: unset;
          bottom: -12px;
        }
      }
    }
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      line-height: 30px;
    }
  }
  &__Greetings {
    color: #000000;
    font-family: Interstate Cond;
    font-size: 16px;
    letter-spacing: 0.8px;
    line-height: 30.4px;
    text-align: center;
    padding: 0 0 20px 0;
    text-transform: uppercase;
    @media screen and (max-width: 767.5px) {
      padding: 0 0 15px 0;
      color: #000000;
      font-family: "Gazpacho";
      font-size: 18px;
      font-weight: bold;
      letter-spacing: 0.6px;
      line-height: 28.8px;
      text-transform: unset;
    }
  }
  &__EmailWrap {
    max-width: 350px;
    margin: 10px auto 0 auto;
  }
  &__Email {
    padding: 10px 0 10px 15px;
    background-color: #fffaed;
    border: none !important;
    height: 50px !important;
    @media screen and (max-width: 767.5px) {
      height: 40px !important;
    }
    &__Email:focus {
      border: none;
    }
    &::placeholder {
      color: #000000;
      font-family: Interstate Cond;
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 28.8px;
      font-weight: 400;
      @media screen and (max-width: 767.5px) {
        font-size: 14px;
        letter-spacing: 0.5px;
        line-height: 25.2px;
      }
    }
  }
  &__DiscountButton {
    width: auto;
    padding: 10px 18px;
    color: #000000;
    font-family: Interstate;
    font-size: 14px;
    letter-spacing: 0.88px;
    line-height: 30px;
    text-align: center;
    @media screen and (max-width: 767.5px) {
      padding: 10px 17px;
      font-size: 12px;
      letter-spacing: 0.5px;
      line-height: 22px;
      height: 42px;
    }
  }
  &__SkipResult {
    display: flex;
    justify-content: center;
  }
  &__Skip {
    color: #000000;
    font-family: Interstate;
    font-size: 14px;
    letter-spacing: 0.5px;
    line-height: 25.2px;
    text-align: center;
    border-bottom: 2px solid #000000;
    text-transform: uppercase;
    @media screen and (max-width: 767.5px) {
      font-size: 12px;
      letter-spacing: 0.5px;
      line-height: 22.8px;
    }
  }
  &__ProgressBar {
    height: 4px;
    border-radius: 2px;
    width: 100%;
    max-width: 600px;
    background-color: #fdd174;
    margin: 40px auto 20px auto;
    @media screen and (max-width: 767.5px) {
      margin: 30px auto 14px auto;
    }
  }
  &__Progress {
    height: 4px;
    border-radius: 2px;
    width: 100%;
    max-width: 600px;
    background-color: #000000;
  }
  &__CountWrap {
    display: flex;
    justify-content: center;
  }
  &__Count {
    color: #000000;
    font-family: Interstate Cond;
    font-size: 16px;
    letter-spacing: 0.5px;
    line-height: 28.8px;
    text-align: center;
    @media screen and (max-width: 767.5px) {
      font-size: 14px;
      letter-spacing: 0.5px;
      line-height: 25.2px;
    }
  }
}
.result_area {
  visibility: hidden;
}


.ResultSection{
  .ProductCollection {
    &__Product-inner {
      display: flex;
      flex-wrap: wrap;
      
    }
    &__Product-block-box {
      max-width: 100%;
      padding: 40px;
      background-color: #fffaed;
      border-radius: 10px;

      .row_wrapper {
        column-gap: 40px;
      }

      .left_col {
        flex: 0 0 50%;
        max-width: calc(50% - 20px);
      }
      .right_col {
        flex: 0 0 50%;
        max-width: calc(50% - 20px);
        display: flex;
        flex-direction: column;
        >h4 {
          color: #000;
          font-family: Interstate;
          font-size: 16px;
          letter-spacing: .8px;
          line-height: 30.4px;
          text-align: center;
          padding: 0 0 15px;
        }
      }
    }
    &__Product-img-box {
      padding: 100% 25px 20px;
      margin: 0 0 17px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      border-radius: 5px;
    }
    &__Image {
      width: 100%;
      height: auto;
      aspect-ratio: 16/9;
      object-fit: contain;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      padding: 0 25px;
    }
    &__Product-block-title {
      color: #000;
      font-family: NT Wagner;
      font-size: 23px;
      font-weight: 700;
      letter-spacing: .5px;
      line-height: 32.2px;
      text-align: center;
    }
  }
}