.ajax-cart {
  &__totals {
    display: none;
    width: 100%;
  }

  &__total {
    font-size: 16px;
    line-height: 1.5;

    @media (max-width: 1024px) {
      font-size: 14px;  
    }

    th {
      font-weight: 500;
      padding: 3.5px 0;
      text-align: left;
    }

    td {
      letter-spacing: 1.17px;
      opacity: 0.6;
      padding: 3.5px 0;
      text-align: right;
    }

    &-note {
      font-size: 12px;
      line-height: 14px;
      font-weight: 400;
      margin-left: 5px;
      opacity: 0.47;
      text-transform: none;
    }
  }

  &__footer {
    background: white;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 30px 20px;
    height: 160px;
    width: 100%;

    @media (max-width: 1024px) {
      padding: 16px 23px;
      height: 160px;
    }

    &-copy {
      font-family: "Interstate Cond";
      font-size: 16px;
      font-weight: 300;
      line-height: 28px;
      letter-spacing: 0.5px;
      margin-bottom: 10px;
      text-align: center;
    }

    .btn {
      margin-top: 18px;
    }
  }
}