.article-grid {
  $self: &;
  color: black;
  position: relative;

  &::before {
    background: $Color--pink;
    border-radius: 15px;
    content: "";
    display: block;
    position: absolute;
    right: 0;
    bottom: 0;
    transition: all 0.3s $cubic;
    width: 100%;
    height: 100%;

    @media (max-width: 1024px) {
      display: none;  
    }
  }

  &__wrapper {
    border-radius: 5px;
    background: white;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: relative;
    transition: all 0.3s $cubic;
    transition-delay: 0.1s;
    height: 100%;
  }

  &__image-wrapper {
    position: relative;
  }

  &__image {
    display: block;
    object-fit: cover;
    width: 100%;
    height: auto;

    @media (max-width: 599px) {
      width: 100%;  
    }
  }

  &__tag {
    background: $Color--pink;
    border-radius: 2px;
    border: 1px solid black;
    color: white;
    font-family: 'Interstate Cond';
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0.8px;
    line-height: 27px;
    padding: 0 6px;
    position: absolute;
    top: 18px;
    left: 24px;
    text-transform: uppercase;

    @media (max-width: 1024px) {
      font-size: 14px;  
      top: 18px;
      left: 18px;
    }
  }

  &__content {
    align-items: flex-start;
    background: white;
    display: flex;
    flex-direction: column;
    padding: 18px 26px;
    height: 100%;

    padding: 20px 17px;
  }

  &__meta {
    font-family: 'Interstate Cond';
    font-size: 16px;
    font-weight: 300;
    line-height: 29px;
    letter-spacing: 0.5px;
    margin-bottom: 5px;

    @media (max-width: 1024px) {
      font-size: 14px;
      line-height: 25px;
    }
  }

  &__title {
    margin-bottom: 14px;

    @media (min-width: 600px) and (max-width: 700px) {
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;  
      display: -webkit-box;
      overflow: hidden;
      height: 58px;
    }
  }

  &__link {
    font-size: 14px;
    font-weight: 700;
    line-height: 26px;
    letter-spacing: 0.5px;
    margin-top: auto;

    &::after {
      bottom: 0;
    }
  }

  &:hover {
    @media (min-width: 1025px) {
      &::before {
        transition-delay: 0.05s;
        width: calc(100% + 13px);
        height: calc(100% + 13px);
      }
  
      #{$self}__wrapper {
        transition-delay: 0s;
        transform: translate(-14px, -13px);
      }
    }
  }
}