.hero-links {
  // background: url('brush.svg') 0 100%/contain no-repeat, 
  // linear-gradient(180deg, 
  // $Color--violet 0% calc(100% - 20px), 
  // $Color--gray 20px);
  color: white;
  position: relative;

  @media (max-width: 599px) {
    // background: url('brush.svg') 0 100%/contain no-repeat, 
    // linear-gradient(180deg, 
    // $Color--violet 0% calc(100% - 7px),
    // $Color--gray 20px);  
  }

  &__container {
    padding-top: 200px;
    text-align: center;

    @media (max-width: 599px) {
      padding-top: 127px;  
    }
  }

  &__image-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__image {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  &__title {
    margin-bottom: 16px;
  }

  &__copy {
    font-family: 'Interstate Cond';
    font-size: 18px;
    font-weight: 400;
    line-height: 34px;
    letter-spacing: 0.7px;
    max-width: 420px;
    margin: 0 auto 96px;
    position: relative;

    @media (max-width: 1024px) {
      font-size: 16px;
      line-height: 30px;
    }

    @media (max-width: 599px) {
      margin-bottom: 50px;  
    }
  }

  &__blocks {
    display: flex;
    justify-content: center;
    padding: 0 0 80px;
    position: relative;

    @media (max-width: 767px) {
      flex-wrap: wrap;  
    }

    @media (max-width: 599px) {
      padding-bottom: 70px;  
    }
  }

  &__block {
    flex: 0 0 300px;
    text-align: center;

    @media (max-width: 1024px) {
      flex: 0 0 33.33%;  
    }

    @media (max-width: 767px) {
      flex: 0 0 100%;  
    }

    &-title {
      font-family: 'Interstate Cond';
      font-size: 16px;
      font-weight: 700;
      line-height: 30px;
      margin-bottom: 4px;
      letter-spacing: 0.8px;
      text-transform: uppercase;

      @media (max-width: 1024px) {
        font-size: 14px;
        line-height: 26px;
      }
    }

    &-link {
      color: $Color--yellow;
      font-family: 'Interstate Cond';
      font-size: 18px;
      font-weight: 700;
      line-height: 32px;
      letter-spacing: 1px;
      text-transform: uppercase;

      @media (max-width: 1024px) {
        font-size: 14px;
        line-height: 26px;  
      }
    }

    & + * {
      @media (max-width: 767px) {
        margin-top: 25px;  
      }
    }
  }
}