.IngredientsHero {
  padding: 227px 0 100px 0;
  background: #fefaee;
  @media screen and (max-width: 767.5px) {
    padding: 134px 0 70px 0;
  }
  &__Container {
    max-width: 100%;
    margin: 0 auto;
    @media screen and (max-width: 767.5px) {
      padding: 0 15px;
    }
  }
  &__Head {
    color: #000000;
    font-family: "Gazpacho";
    font-size: 65px;
    font-weight: bold;
    letter-spacing: 1.8px;
    line-height: 80px;
    text-align: center;
    position: relative;
    max-width: fit-content;
    margin: 0 auto;
    padding: 0 0 20px 0;
    @media screen and (max-width: 767.5px) {
      font-size: 40px;
      letter-spacing: 0.9px;
      line-height: 44px;
      padding: 0 0 15px 0;
    }
    &::after {
      content: "";
      position: absolute;
      width: 45px;
      height: 60px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/a1.png?v=1661325220);
      background-repeat: no-repeat;
      background-size: contain;
      top: -10px;
      right: -44px;
      @media screen and (max-width: 767.5px) {
        top: -28px;
      }
      @media screen and (max-width: 575.5px) {
        top: -12px;
        right: -35px;
        background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/Purple_Star_Group.png?v=1662014428);
        background-repeat: no-repeat;
        background-size: contain;
        transform: rotate(-17deg);
        width: 45px;
        height: 45px;
      }
    }
    &::before {
      content: "";
      position: absolute;
      width: 45px;
      height: 45px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/Purple_Star_Group.png?v=1662014428);
      background-repeat: no-repeat;
      background-size: contain;
      top: -14px;
      left: -35px;
      @media screen and (max-width: 575.5px) {
        top: -21px;
        left: -29px;
      }
    }
  }
  &__Details {
    color: #000000;
    font-family: Interstate Cond;
    font-size: 18px;
    font-weight: 300;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    text-align: center;
    margin: 0 auto;
    padding: 0 0 40px 0;
    max-width: 420px;
    width: 100%;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
      padding: 0 0 30px;
    }
  }
  &__ButtonWrap {
    display: flex;
    justify-content: center;
  }
  &__Button{
    width: auto;
    @media screen and (max-width: 767.5px) {
        height: 42px;
        font-size: 12px;
        letter-spacing: 0.5px;
        line-height: 22px;
    } 
  }
}
