.pdp {
  &__inner {
    @include hideScrollbar;
    
    align-items: flex-start;
    display: flex;
    flex-wrap: wrap;
    overflow: auto;
    height: 888px;

    @media (max-width: 767px) {
      height: auto;  
    }

    @media (max-width: 767px) {
      flex-wrap: wrap;  
    }
  }

  &__images {
    flex: 0 0 57.3%;

    @media (max-width: 1260px) {
      flex: 0 0 50%;
    }

    @media (max-width: 767px) {
      flex: 0 0 100%; 
      position: relative;

      &::after {
        background: linear-gradient(180deg, rgba(255,255,255,0) 0, rgba(255,255,255,1) 100%);
        content: "";
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 30px;
      }
    }

    &-wrapper {
      @media (max-width: 767px) {      
        overflow: auto;
        position: relative;
        height: 400px;  
      }
    }

    img {
      display: block;
      width: 100%;
    }
  }

  &__copy {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: center;
    min-width: 0;
    padding: 0 7%;
    align-items: flex-start;
    position: sticky;
    top: 0;
    height: 100%;

    @media (max-width: 1260px) {
      padding: 0 40px;
    }

    @media (max-width: 1024px) {
      padding: 0 20px;  
    }

    @media (max-width: 767px) {
      flex: 0 0 100%; 
      padding: 70px 20px;
      position: static;
      text-align: center;
      height: auto;
    }
  }

  &__title {
    font-family: 'Interstate Cond';
    margin-bottom: 20px;

    span {
      display: block;
    }

    a {
      color: #222;
      font-size: 14px;
      line-height: 18px;
      margin-bottom: 6px;
      opacity: .6;
      text-transform: capitalize;
    }

    &-and-price {
      border-bottom: 1px solid #EAE7E7;
      display: flex;
      justify-content: space-between;
      padding-bottom: 8px;

      @media (max-width: 599px) {
        order: 1;  
      }
    }
  }

  &__description {
    font-size: 16px;
    margin-bottom: 20px;
  }

  &__vendor {
    font-size: 16px;
    margin-bottom: 10px;
  }

  &__price {
    font-size: 16px;
    font-weight: 500;
    line-height: 1.875;

    &-wrapper {
      white-space: nowrap;
    }

    &--red {
      color: $Color--red;
    }
  }

  &__price--sale {
    font-size: 16px;
    font-weight: 500;
    line-height: 1.875;
    opacity: 0.5;
    text-decoration: line-through;
  }

  &__final-sale {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;

    p {
      color: $Color--red;
      font-size: 14px;
      line-height: 18px;
      margin-bottom: 8px;
    }

    a {
      align-items: center;
      color: $Color--red;
      display: flex;
      font-size: 14px;
      line-height: 18px;
      text-decoration: underline;

      img {
        image-rendering: pixelated;
        margin-right: 5px;
        object-fit: cover;
        width: 18px;
        height: 17px;
      }
    }
  }

  .accordion__list {
    @media (max-width: 599px) {
      margin-top: 30px;
      order: 4;  
    }
  }

  .complete-the-look {
    @media (max-width: 599px) {
      order: 3;  
    }
  }
}

.sticky-form {
  background: white;
  border-top: 1px solid $Color--gray;
  padding: 30px 20px;
  position: fixed;
  bottom: 0;
  left: 0;
  transition: all 0.3s $cubic;
  transform: translateY(100%);
  width: 100%;
  height: auto;
  z-index: 3;

  &-close {
    background: none;
    border: 0;
    color: #000;
    font-size: 16px;
    font-weight: 700;
    position: absolute;
    top: 20px;
    right: 20px;
    width: 18px;
    height: 18px;
  }

  .pdp__bottom .btn {
    max-width: 100%;
  }

  &--active {
    transform: translateY(0);
  }
}

#btn-atc {
  border-top: 1px solid white;
  display: none;
  position: fixed;
  bottom: 0;
  left: 0;
  transition: all 0.3s $cubic;
  transform: translateY(0);
  width: 100%;
  height: 60px;
  z-index: 3;

  @media (max-width: 599px) {
    display: inline-flex;
  }

  &.remove {
    transform: translateY(100%);
  }
}