.js-search {
  cursor: pointer;
}

.search-dd {
  background: white;
  border-top: 1px solid rgba(black, 0.3);
  border-bottom: 1px solid rgba(black, 0.3);
  opacity: 0;
  position: absolute;
  top: 100%;
  left: 0;
  transition: all 0.3s $cubic;
  visibility: hidden;
  width: 100%;
  z-index: 1;

  &__form-group {
    align-items: center;
    display: flex;
    padding: 0 20px;

    @media (max-width: 1300px) {
      padding: 0 5px;
    }

    @media (max-width: 1200px) {
      padding: 0;
    }
  }

  &__input[type=text] {
    @include input-placeholder {
      color: black;
      font-weight: 400;
      text-transform: capitalize;
    }

    border: 0;
    color: black;
    font-size: 12px;
    letter-spacing: 1px;
    line-height: 18px;
    outline: none;
    height: 40px;

    &:focus {
      border: 0;
    }

    @media (max-width: 1024px) {
      height: 30px;  
    }
  }

  &__submit {
    background: url('./icon-search.svg') center/15px 15px no-repeat;
    border: 0;
    outline: none;
    text-indent: -9999px;
    width: 40px;
    height: 40px;

    @media (max-width: 1024px) {
      width: 30px;
      height: 30px;
    }
  }

  &--active {
    opacity: 1;
    visibility: visible;
  }
}

/* 
  SEARCH RESULTS ----------------------------
*/

.search-results {
  padding-top: 60px;
  padding-bottom: 60px;

  @media (max-width: 599px) {
    padding-top: 30px;
    padding-bottom: 65px;
  }
}

.sr {
  &__title {
    font-size: 48px;
    line-height: 1.5;
    margin-bottom: 70px;
    text-align: center;
    text-transform: uppercase;
    
    @media (max-width: 1024px) {
      font-size: 36px;
      margin-bottom: 50px;
    }

    &--no-res {
      margin-bottom: 15px;
    }
  }

  &__copy {
    font-size: 20px;
    margin-bottom: 80px;
    text-align: center;
  }

  &__form {
    margin: 0 auto;
    max-width: 360px;
    position: relative;
    width: 100%;
    height: 30px;

    @media (max-width: 1024px) {
      flex: 0 0 57px;  
    }

    @media (max-width: 599px) {
      width: 100%;  
    }

    &-text {
      background: rgba(black, 0.05);
      border: 0;
      font-size: 12px;
      letter-spacing: 1px;
      padding: 0 10px;
      width: 100%;
      height: 100%;
    }

    &-submit {
      background: url('./icon-search.svg') center/15px 15px no-repeat;
      border: 0;
      position: absolute;
      top: 0;
      right: 0;
      text-indent: -9999px;
      outline: none;
      padding: 0;
      transition: all 0.3s $cubic;
      width: 35px;
      height: 30px;

      &:hover {
        opacity: 0.6;
      }
    }
  }

  &__lists {
    .product__list,
    .article__list {
      display: flex;
      flex-wrap: wrap;
      gap: 0;
      margin: 10px -10px;
    }

    .grid-item {
      padding: 10px;
      width: 25%;

      @media (max-width: 1024px) {
        width: 33.33%;  
      }

      @media (max-width: 780px) {
        width: 50%;
      }
    }
    
    &.sr__products--empty {
      display: none;
    }

    &.sr__articles--empty {
      display: none;
    }
  }
}