.NutrionalFacts {
  padding: 100px 0 35px 0;
  background: #52339b;
  position: relative;
  scroll-margin-top: 130px;

  #gift-card & {
    display: none;
  }

  
  &::after {
    content: "";
    position: absolute;
    top: -14px;
    left: 0;
    width: 100%;
    height: 68px;
    z-index: 0;
    background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/BottomLine_52339b.svg?v=1662028205);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
    @media screen and (max-width: 767.5px) {
      top: -36px;
      background-position: left;
    }
  }
  &::before {
    content: "";
    position: absolute;
    bottom: -18px;
    left: 0;
    width: 100%;
    height: 96px;
    z-index: 0;
    background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/BottomLine_52339b.svg?v=1662028205);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center bottom;
    @media screen and (max-width: 991.5px) {
      bottom: -11px;
      height: 60px;
    }
    @media screen and (max-width: 767.5px) {
      right: -34px;
      background-size: cover;
      background-position: left;
      bottom: -21px;
    }
  }
  &__Container {
    margin: 0 auto;
    @media screen and (max-width: 319px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 320px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 575px) {
      max-width: 480px;
      padding: 0;
    }
    @media screen and (min-width: 600px) {
      max-width: 540px;
    }
    @media screen and (min-width: 768px) {
      max-width: 690px;
    }
    @media screen and (min-width: 992px) {
      max-width: 870px;
    }
    @media screen and (min-width: 1199px) {
      max-width: 1024px;
    }
    @media screen and (min-width: 1440px) {
      max-width: 1116px;
    }
  }
  &__Layout-main {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    @media screen and (max-width: 991.5px) {
      flex-direction: column;
      row-gap: 30px;
    }
  }
  &__Layout-inner {
    flex: 0 0 50%;
    max-width: 50%;
    @media screen and (max-width: 991.5px) {
      flex: 0 0 100%;
      max-width: 100%;
      width: 100%;
    }
  }
  &__Layout-Table {
    position: relative;
    &::after {
      position: absolute;
      content: "";
      width: calc(100% + 4px);
      height: 6%;
      bottom: -2px;
      left: -2px;
      background-repeat: no-repeat;
      background: linear-gradient(
        180deg,
        rgba(90, 76, 160, 0) 0%,
        #52339b 100%
      );
    }
  }
  &__NutrionalImg {
    max-width: 80%;
    height: auto;
    object-fit: cover;
    border-radius: 100px 15px 15px 15px;
    @media screen and (max-width: 991.5px) {
      margin: 0 auto;
      display: flex;
    }
  }
  &__Head {
    color: #ffffff;
    font-family: "Gazpacho";
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 2.5px;
    line-height: 75px;
    margin: 0 auto 0 0;
    padding: 0 0 20px 0;
    position: relative;
    max-width: fit-content;
    @media screen and (max-width: 991.5px) {
      display: none;
    }
    &::after {
      content: "";
      position: absolute;
      top: -8px;
      left: -41px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/star_group_green.png?v=1661160183);
      background-repeat: no-repeat;
      background-size: contain;
      width: 60px;
      height: 60px;
      transform: scaleX(-1);
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: -65px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/star_-_green.png?v=1661168356);
      background-repeat: no-repeat;
      background-size: contain;
      width: 60px;
      height: 60px;
      @media screen and (max-width: 767.5px) {
        width: 35px;
        right: -34px;
      }
    }
  }
  &__Head-mobile {
    display: none;
    color: #ffffff;
    font-family: "Gazpacho";
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 2.5px;
    line-height: 75px;
    margin: 0 auto 0 0;
    padding: 0 0 20px 0;
    position: relative;
    max-width: fit-content;
    &::after {
      content: "";
      position: absolute;
      top: -8px;
      left: -41px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/star_group_green.png?v=1661160183);
      background-repeat: no-repeat;
      background-size: contain;
      width: 60px;
      height: 60px;
      transform: scaleX(-1);
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: -65px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/star_-_green.png?v=1661168356);
      background-repeat: no-repeat;
      background-size: contain;
      width: 60px;
      height: 60px;
      @media screen and (max-width: 767.5px) {
        width: 35px;
        right: -34px;
      }
    }
    @media screen and (max-width: 991.5px) {
      display: block;
      margin: 0 auto;
    }
    @media screen and (max-width: 767.5px) {
      font-size: 32px;
      text-align: center;
      letter-spacing: 0.7px;
      line-height: 41.6px;
      padding: 0 0 15px 0;
      margin: 0 auto;
      text-align: center;
    }
  }
  &__Details {
    color: #ffffff;
    font-family: Interstate Cond;
    font-size: 16px;
    letter-spacing: 0.4px;
    line-height: 30px;
    margin: 0;
    padding: 0 0 20px 0;
    @media screen and (max-width: 991.5px) {
      display: none;
    }
  }
  &__Details-mobile {
    color: #ffffff;
    font-family: Interstate;
    font-size: 16px;
    letter-spacing: 0.4px;
    line-height: 30px;
    margin: 0;
    padding: 0 0 20px 0;
    display: none;
    @media screen and (max-width: 991.5px) {
      display: block;
      margin: 0 auto;
      text-align: center;
    }
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
      padding: 0 0 15px 0;
      text-align: center;
    }
  }
  &__Table-main {
    position: relative;
    overflow-y: scroll;
    max-height: 439px;
    border: 2px solid #ffffff;
    &::-webkit-scrollbar {
      display: none;
    }
  }
  table {
    font-family: 'Interstate Cond';
    color: white;
    width: 100% !important;

    td {
      border-color: white !important;
      padding: 7px 10px !important;
    }
  }
}
