.IngredientsGrid {
  background: #fefaee;
  padding: 0 0 0 0;
  @media screen and (max-width: 767.5px) {
    padding: 0 0 40px 0;
  }
  &__Container {
    margin: 0 auto;
    @media screen and (max-width: 319px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 320px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 575px) {
      max-width: 480px;
      padding: 0;
    }
    @media screen and (min-width: 600px) {
      max-width: 540px;
    }
    @media screen and (min-width: 768px) {
      max-width: 690px;
    }
    @media screen and (min-width: 992px) {
      max-width: 960px;
    }
    @media screen and (min-width: 1280px) {
      max-width: 1140px;
    }
    @media screen and (min-width: 1440px) {
      max-width: 1240px;
    }
  }
  &__Layout-main {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    column-gap: 100px;
    @media screen and (max-width: 1279.5px) {
      column-gap: 30px;
    }
    @media screen and (max-width: 991.5px) {
      column-gap: 30px;
    }
    @media screen and (max-width: 767.5px) {
      column-gap: 0;
    }
  }
  &__Layout-inner {
    flex: 0 0 calc(33.33% - 67px);
    max-width: calc(33.33% - 67px);
    margin: 0 0 100px 0;
    &:hover{
        .IngredientsGrid__ImageWrap{
            transform: scale(1);
        }
    }
    @media screen and (max-width: 1279.5px) {
      max-width: calc(33.33% - 33px);
      flex: 0 0 calc(33.33% - 33px);
    }
    @media screen and (max-width: 991.5px) {
      flex: 0 0 calc(50% - 30px);
      max-width: calc(50% - 30px);
      margin: 0 0 50px 0;
    }
    @media screen and (max-width: 767.5px) {
      flex: 0 0 100%;
      max-width: 100%;
      margin: 0 0 30px 0;
    }
  }
  &__ImageWrap {
    max-width: 167px;
    max-height: 167px;
    background: #52339b;
    border-radius: 50%;
    padding: 28px;
    margin: 0 auto;
    transition: 0.3s all ease-in-out;
    -webkit-transition: 0.3s all ease-in-out;
    -moz-transition: 0.3s all ease-in-out;
    transform: scale(0.825);
    @media screen and (max-width: 767.5px) {
      max-width: 135px;
      max-height: 135px;
      padding: 24px;
      transform: scale(0.89);
    }
  }
  &__Image {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  &__Title {
    color: #000000;
    font-family: Interstate;
    font-size: 21px;
    font-weight: 700;
    letter-spacing: 0.3px;
    line-height: 29.4px;
    text-align: center;
    margin: 0 auto;
    padding: 10px 0 20px 0;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.3px;
      line-height: 27.2px;
      padding: 15px 0 15px 0;
    }
  }
  &__Details {
    color: #000000;
    font-family: Interstate Cond;
    font-size: 18px;
    font-weight: 300;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    text-align: center;
    margin: 0 auto;
    text-align: center;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
    }
  }
}
