.delivery {
  width: 100%;

  &__title {
    font-family: 'Interstate';
    font-size: 16px;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: 0.8px;
    margin-bottom: 15px;
    text-transform: uppercase;
  }

  &__logos {
    align-items: center;
    display: flex;
    justify-content: space-between;
  }
}