.key-benefits {
  padding-top: 100px;
  padding-bottom: 110px;

  @media (max-width: 1024px) {
    padding-top: 70px;
    padding-bottom: 100px;
  }

  &__container {
    @include make-container;
  }

  &__title {
    margin-bottom: 40px;
    text-align: center;

    @media (max-width: 1024px) {
      margin-bottom: 30px;  
    }
  }

  &__content {
    display: flex;
    justify-content: space-between;

    @media (max-width: 599px) {
      flex-direction: column;  
    }
  } 

  &__list {
    display: flex;
    flex: 0 0 250px;
    flex-direction: column;
    justify-content: center;

    @media (max-width: 1024px) {
      flex: 0 0 25%;  
    }

    &-item {
      text-align: center;

      & + * {
        margin-top: 40px;

        @media (max-width: 1024px) {
          margin-top: 15px;  
        }
      }
    }

    &-title {
      margin-bottom: 10px;

      @media (max-width: 1024px) {
        margin-bottom: 5px;  
      }
    }

    &-copy {
      font-weight: 300;
    }

    &--first {
      @media (max-width: 599px) {
        margin-bottom: 15px;
        order: 2;  
      }
    }

    &--second {
      @media (max-width: 599px) {
        order: 3;  
      }
    }
  }

  &__image-wrapper {
    align-items: center;
    display: flex;
    flex: 0 0 40.7%;

    @media (max-width: 599px) {
      justify-content: center;
      margin-bottom: 30px;
      order: 1;
    }
  }

  &__image {
    height: auto;
  }
}