.OurMission{
    padding: 100px 0;
    &__Container{
        max-width: 740px;
        margin: 0 auto;
        @media screen and (max-width: 767.5px) {
            max-width: 100%;
            padding: 0 15px;
        }
    }
    &__Title{
        color: #000000;
        font-family: "Gazpacho";
        font-size: 50px;
        font-weight: bold;
        letter-spacing: 2.5px;
        line-height: 75px;
        text-align: center;
        position: relative;
        max-width: max-content;
        margin: 0 auto 20px;
        &::before {
            content: "";
            position: absolute;
            background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/start_group_blue.png?v=1661160183);
            top: -25px;
            right: -60px;
            background-size: contain;
            background-repeat: no-repeat;
            width: 60px;
            height: 60px;
            transform: rotate(160deg);
            @media screen and (max-width: 767.5px){
                width: 40px;
                height: 40px;
            }
            @media screen and (max-width: 400px) {
                top: -10px;
                right: -40px;
                width: 30px;
                height: 30px;
            }
        }
        @media screen and (max-width: 767.5px) {
            font-size: 25px;
            line-height: 35px;
            margin: 0 auto 10px;
        }
    }
    &__Details{
        color: #000000;
        font-family: Interstate Cond;
        font-size: 18px;
        letter-spacing: 0.7px;
        line-height: 34.2px;
        text-align: center;
        @media screen and (max-width: 767.5px) {
            font-size: 15px;
            line-height: 30px;
        }
    }
}