.Sustainability {
  background-color: #52339b;
  padding: 81px 0 89px 0;
  position: relative;
  scroll-margin-top: 130px;
  @media screen and (max-width: 767.5px) {
    padding: 50px 0;
  }
  &::after {
    content: "";
    position: absolute;
    top: -15px;
    left: 0;
    width: 100%;
    height: 68px;
    z-index: 0;
    background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/BottomLine_52339b.svg?v=1662028205);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
    @media screen and (max-width: 767.5px) {
        background-position: left;
        top: -36px;
    }
  }
  &::before {
    content: "";
    position: absolute;
    bottom: -13px;
    left: 0;
    width: 100%;
    height: 96px;
    z-index: 0;
    background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/BottomLine_52339b.svg?v=1662028205);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center bottom;
    @media screen and (max-width: 767.5px) {
      background-size: cover;
      background-position: left;
      bottom: -21px;
      height: 60px;
    }
  }
  &__container {
    
  }
  &__Head {
    color: #ffffff;
    font-family: "Gazpacho";
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 2.5px;
    line-height: 75px;
    text-align: center;
    margin: 0 auto;
    padding: 0 0 20px 0;
    max-width: fit-content;
    position: relative;
    @media screen and (max-width: 767.5px) {
      font-size: 32px;
      letter-spacing: 0.7px;
      line-height: 41.6px;
      padding: 0 0 15px 0;
    }
    @media screen and (max-width: 575.5px) {
      max-width: 290px;
    }
    &::after {
      content: "";
      position: absolute;
      top: 10px;
      right: -42px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/star_group_green.png?v=1661160183);
      background-size: contain;
      background-repeat: no-repeat;
      width: 55px;
      height: 30px;
      @media screen and (max-width: 767.5px) {
        right: -54px;
      }
      @media screen and (max-width: 575.5px) {
        background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/star_-_green.png?v=1661168356);
        width: 25px;
        height: 25px;
        top: -8px;
        right: 14px;
        background-repeat: no-repeat;
        background-size: contain;
      }
    }
    &::before {
      content: "";
      position: absolute;
      top: 10px;
      left: -40px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/star_group_green.png?v=1661160183);
      background-size: contain;
      background-repeat: no-repeat;
      width: 55px;
      height: 30px;
      transform: rotate(282deg);
      @media screen and (max-width: 575.5px) {
        left: -13px;
        transform: rotate(93deg);
      }
    }
  }
  &__Details {
    color: #ffffff;
    font-family: Interstate Cond;
    font-size: 18px;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    text-align: center;
    max-width: 740px;
    margin: 0 auto;
    padding: 0 0 40px 0;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
      padding: 0 0 30px 0;
    }
  }
  &__Layout-main {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    @media screen and (max-width: 991.5px) {
      row-gap: 20px;
    }
  }
  &__Layout-inner {
    flex: 0 0 20%;
    max-width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 43px;
    @media screen and (max-width: 991.5px) {
      flex: 0 0 50%;
      max-width: 50%;
      row-gap: 20px;
    }
    @media screen and (max-width: 767.5px) {
      flex: 0 0 100%;
      max-width: 100%;
      row-gap: 15px;
    }
  }
  &__ImgWrap {

  }
  &__Image {

  }
  &__Title {
    color: #ffffff;
    font-family: "Gazpacho";
    font-size: 23px;
    font-weight: bold;
    letter-spacing: 0.92px;
    line-height: 31px;
    text-align: center;
    margin: 0 auto;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.3px;
      line-height: 27.2px;
    }
  }
}
