// Footer Main

.Footer {
  overflow: hidden;
  padding: 93px 0 17px 0;
  background: #fefaee;
  @media screen and (max-width: 767.5px) {
    padding: 82px 0 48px 0;
  }
  &__container {

  }
  &__Layout {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 0 0 34px 0;
    @media screen and (max-width: 767.5px) {
      flex-wrap: wrap;
      padding: 0 0 19px 0;
    }
  }
  &__Support,
  &__Condition,
  &__CopyRight,
  &__News {
    // flex: 0 0 30%;
    // max-width: 30%;
    @media (min-width: 768px) and (max-width: 1024px) {
      flex: 0 0 20%;
      padding-right: 20px;
    }

    @media screen and (max-width: 767.5px) {
      flex: 0 0 100%;
      max-width: 100%;
    }
  }
  &__News {
    padding-right: 0;
  }
  &__About,
  &__Legal,
  &__Connect,
  &__Pattents {
    // flex: 0 0 20%;
    // max-width: 20%;
    @media (min-width: 768px) and (max-width: 1024px) {
      flex: 0 0 20%;
      padding-right: 20px;
    }

    @media screen and (max-width: 767.5px) {
      flex: 0 0 100%;
      max-width: 100%;
    }
  }
  &__About,
  &__Legal {
    @media screen and (max-width: 991.5px) {
      // flex: 0 0 13%;
      // max-width: 13%;
    }
    @media (min-width: 768px) and (max-width: 1024px) {
      flex: 0 0 20%;
      padding-right: 20px;
    }

    @media screen and (max-width: 767.5px) {
      flex: 0 0 100%;
      max-width: 100%;
    }
  }
  &__News,
  &__CopyRight {
    @media screen and (max-width: 991.5px) {
      // flex: 0 0 37%;
      // max-width: 37%;
    }
    @media (min-width: 768px) and (max-width: 1024px) {
      flex: 0 0 30%;
    }

    @media screen and (max-width: 767.5px) {
      flex: 0 0 100%;
      max-width: 100%;
    }
  }
  &__Support,
  &__About,
  &__Connect,
  &__News {
    @media screen and (max-width: 767.5px) {
      padding: 0 0 28px 0;
    }
  }
  &__Head {
    color: #000000;
    font-family: "Gazpacho";
    font-size: 18px;
    letter-spacing: 0.39px;
    line-height: 30.6px;
    padding: 0 0 10px 0;
    @media screen and (max-width: 767.5px) {
      letter-spacing: 0.6px;
      line-height: 28.8px;
      padding: 0 0 14px 0;
      text-align: center;
    }
  }
  &__ListLink {
    display: flex;
    flex-direction: column;
  }
  &__Links {
    color: #000000;
    font-family: Interstate Cond;
    font-size: 16px;
    font-weight: 300;
    letter-spacing: 0.5px;
    line-height: 28.8px;
    margin: 0 0 11px 0;
    display: flex;
    transition: all 0.3s $cubic;
    @media (min-width: 1025px) {
      &:hover {
        color: $Color--pink;
      }  
    }
    @media screen and (max-width: 767.5px) {
      letter-spacing: 0.5px;
      line-height: 30.4px;
      justify-content: center;
      margin: 0 0 3px 0;
    }
  }
  &__NewsDetails {
    color: #000000;
    font-family: Interstate Cond;
    font-size: 16px;
    font-weight: 300;
    letter-spacing: 0.5px;
    line-height: 28.8px;
    padding: 0 0 10px 0;
    @media screen and (max-width: 767.5px) {
      line-height: 30.4px;
      text-align: center;
      padding: 0 0 13px 0;
    }
  }
  &__GmailWrap {
    display: flex;
    input {
      border: 2px solid #000000 !important;
      border-radius: 3px 0 0 3px;
      font-size: 14px; 
      padding: 0 10px;
      height: 42px;
      border-right: 0px !important;
      min-width: 0;
      flex: 1;
      
      &::placeholder {
        color: #909090;
        font-family: Interstate;
        font-size: 14px;
        letter-spacing: 0;
        line-height: 18px;
        text-transform: unset;
      }
      &:focus {
        border: 2px solid #000000;
        border-right: 0px !important;
      }
    }
  }
  &__Subscribe {
    padding: 12px 16px;
    height: 42px;
    border-radius: 0 3px 3px 0;
    font-family: Interstate;
    font-size: 12px;
    font-weight: bold;
    letter-spacing: 0;
    line-height: 15px;
    min-width: 0;
    width: auto;
  }
  &__SiteDetails {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 25px 40px 0 40px;
    margin: 0 -40px;
    border-top: 0.5px solid #000000;
    @media screen and (max-width: 1199.5px) {
      margin: 0 -20px;
      padding: 25px 20px 0 20px;
    }
    @media screen and (max-width: 991.5px) {
      margin: 0;
      padding: 25px 0 0 0;
    }
    @media screen and (max-width: 767.5px) {
      flex-wrap: wrap;
      padding: 47px 0 0 0;
    }
  }
  &__CopyRight {
    @media screen and (max-width: 767.5px) {
      padding: 47px 0 0 0;
      border-top: 0.5px solid #000000;
    }
  }
  &__Condition,
  &__Legal {
    @media screen and (max-width: 767.5px) {
      padding: 0 0 10px 0;
    }
  }
  &__Pattents {
    @media screen and (max-width: 767.5px) {
      padding: 0 0 47px 0;
    }
  }

  .klaviyo-form {
    .needsclick {
      min-height: 0 !important;
      padding-right: 0 !important;
      padding-left: 0 !important;
    }

    input[type="email"] {
      border: 2px solid black !important;
      border-radius: 5px 0 0 5px !important;
      box-shadow: none !important;
      font-family: 'Interstate';
      font-size: 14px;
      outline: none !important;
      padding: 0 10px !important;
      width: 100% !important;
      height: 40px !important;
    }

    button {
      background: $Color--yellow !important;
      border-radius: 0 5px 5px 0 !important;
      border: 2px solid black !important;
      border-left: 0 !important;
      box-sizing: border-box;
      box-shadow: none !important;
      color: $Color--black !important;
      font-size: 12px !important;
      font-weight: 700;
      outline: none !important;
      text-transform: uppercase !important;
      width: 104px !important;
      height: 40px !important;
    }

    span, p, div {
      color: $Color--black !important;
      font-family: 'Interstate Cond' !important;
      text-align: left !important;

      @media (max-width: 599px) {
        text-align: center !important;  
      }
    }

    span.ql-font-helvetica-neue {
      display: inline-block;
      margin-bottom: 8px !important;
    }

    input[type="text"] {
      border: 2px solid black !important;
      border-radius: 5px !important;
      box-shadow: none !important;
      margin-bottom: 15px !important;
      padding: 0 10px !important;
      width: 300px !important;
      height: 40px !important;
    }

    [data-testid="form-row"] + [data-testid="form-row"] button {
      border-left: 2px solid black !important;
      border-radius: 5px !important;
    }
  }
}
