.ProductDescription{
    padding: 100px 0 101px 0;
    background: #52339B;
    position: relative;
    scroll-margin-top: 130px;
    &::before {
        content: "";
        position: absolute;
        bottom: -10px;
        left: 0;
        width: 100%;
        height: 74px;
        z-index: 0;
        background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/BottomLine_52339b.svg?v=1662028205);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center bottom;
        @media screen and (max-width: 767.5px) {
            background-size: cover;
            background-position: left;
            bottom: -21px;
        }
      }
    &__Container{
        margin: 0 auto;
        @media screen and (max-width: 319px) {
          padding: 0 15px;
        }
        @media screen and (min-width: 320px) {
            padding: 0 15px;
        }
        @media screen and (min-width: 575px) {
          max-width: 480px;
          padding: 0;
        }
        @media screen and (min-width: 600px) {
          max-width: 540px;
        }
        @media screen and (min-width: 768px) {
          max-width: 690px;
        }
        @media screen and (min-width: 992px) {
          max-width: 700px;
        }
        @media screen and (min-width: 1440px) {
          max-width: 740px;
        }
    }
    &__Head{
        color: #FFFFFF;
        font-family: "Gazpacho";
        font-size: 50px;
        font-weight: bold;
        letter-spacing: 2.5px;
        line-height: 75px;
        text-align: center;
        margin: 0 auto;
        max-width: fit-content;
        padding: 0 0 20px 0;
        position: relative;
        @media screen and (max-width: 767.5px) {
            font-size: 32px;
            letter-spacing: 0.7px;
            line-height: 41.6px;
            padding: 0 15px 15px 15px;
        }
        &::after{
            content: "";
            position: absolute;
            top: -10px;
            right: -15px;
            width: 60px;
            height: 60px;
            background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/star_group_green.png?v=1661160183);
            background-size: contain;
            background-repeat: no-repeat;
            @media screen and (max-width: 767.5px) {
                right: -18px;
            }
            @media screen and (max-width: 392.5px) {
                top: -14px;
                right: 36px;
            }
        }
        &::before{
            content: "";
            position: absolute;
            top: -10px;
            left: -36px;
            width: 60px;
            height: 60px;
            background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/star_-_green.png?v=1661168356);
            background-size: contain;
            background-repeat: no-repeat;
            @media screen and (max-width: 392.5px) {
                left: 26px;
            }
        }
    }
    &__Details{
        color: #FFFFFF;
        font-family: Interstate Cond;
        font-size: 18px;
        letter-spacing: 0.7px;
        line-height: 34.2px;
        text-align: center;
        margin: 0 auto;
        max-width: 740px;
        padding: 0 0 40px 0;
        @media screen and (max-width: 767.5px) {
            font-size: 16px;
            letter-spacing: 0.5px;
            line-height: 30.4px;
            padding: 0 0 30px 0;
        }
    }
    &__Product__Main{
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: center;
        @media screen  and (max-width: 767.5px){
            row-gap: 15px;
        }
    }
    &__Product__Inner{
        flex: 0 0 25%;
        max-width: 25%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        @media screen and (max-width: 767.5px) {
            flex: 0 0 100%;
            max-width: 100%;
        }
    }
    &__ProductImg {
        max-width: 50%;
        margin: 0 auto;
        transform: rotate(70deg);
        @media screen and (max-width: 767.5px) {
            max-width: 100px;
        }
        @media screen and (max-width: 575.5px) {
            max-width: 74px;
        }
    }
    &__ProductName{
        color: #FFFFFF;
        font-family: Interstate;
        font-size: 16px;
        letter-spacing: 0.8px;
        line-height: 30.4px;
        text-align: center;
        margin: 0;
        padding: 10px 0 0 0;
    }
}