.related-blogs {
  padding-bottom: 100px;

  @media (max-width: 1024px) {
    padding-bottom: 70px;  
  }
  
  &__title {
    margin-bottom: 40px;
    text-align: center;
  }
}

.article__list {
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(3, 1fr);
  max-width: 980px;
  margin: 0 auto;  

  @media (max-width: 1024px) {
    gap: 18px;  
  }

  @media (max-width: 599px) {
    gap: 30px;
    grid-template-columns: repeat(1, 1fr);  
  }
}