.SupportBanner {
  padding: 202px 0 60px 0;
  background: #52339b;
  position: relative;
  @media screen and (max-width: 991.5px) {
    padding: 150px 0 60px 0;
  }
  @media screen and (max-width: 767.5px) {
    padding: 104px 0 60px 0;
  }
  &::after {
    position: absolute;
    content: "";
    bottom: -21px;
    left: 0;
    width: 100%;
    height: 80px;
    background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/BottomLine_52339b.svg?v=1662028205);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    z-index: -1;
    transform: rotateX(180deg);
  }
  &__Container {
    margin: 0 auto;
    @media screen and (max-width: 319px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 320px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 575px) {
      max-width: 480px;
      padding: 0;
    }
    @media screen and (min-width: 600px) {
      max-width: 540px;
    }
    @media screen and (min-width: 768px) {
      max-width: 730px;
    }
    @media screen and (min-width: 992px) {
      max-width: 780px;
    }
    @media screen and (min-width: 1024px) {
      max-width: 800px;
    }
    @media screen and (min-width: 1200px) {
      max-width: 1024px;
    }
    @media screen and (min-width: 1440px) {
      max-width: 1240px;
    }
  }
  &__Head {
    color: #ffffff;
    font-family: "Gazpacho";
    font-size: 65px;
    font-weight: bold;
    letter-spacing: 1.8px;
    line-height: 84.5px;
    text-align: center;
    margin: 0 auto;
    padding: 0 0 16px 0;
    position: relative;
    width: fit-content;
    @media screen and (max-width: 767.5px) {
        font-size: 40px;
        letter-spacing: 0.9px;
        line-height: 45px;
        padding: 0 0 15px 0;
    }
    @media screen and (max-width: 475.5px) {
        max-width: 172px;
    }
    &::after {
      content: "";
      position: absolute;
      width: 45px;
      height: 45px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/start_group_yellow.png?v=1661160183);
      background-repeat: no-repeat;
      background-size: contain;
      top: -5px;
      left: -46px;
      @media screen and (max-width: 767.5px) {
        top: -14px;
        left: -27px;
      }
      @media screen and (max-width: 475.5px) {
        left: -44px;
      }
    }
    &::before {
      content: "";
      position: absolute;
      width: 45px;
      height: 60px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/star_group.png?v=1661160183);
      background-repeat: no-repeat;
      background-size: contain;
      top: -10px;
      right: -44px;
      z-index: 9;
      @media screen and (max-width: 767.5px) {
        top: -33px;
        right: -24px;
      }
      @media screen and (max-width: 475.5px) {
        right: -40px;
      }
    }
  }
  &__Details {
    color: #ffffff;
    font-family: Interstate Cond;
    font-size: 18px;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    text-align: center;
    max-width: 420px;
    margin: 0 auto;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
    }
  }
  &__Details-main {
    display: flex;
    justify-content: space-between;
    padding: 96px 0 0 0;
    row-gap: 32px;
    @media screen and (max-width: 767.5px) {
      padding: 73px 0 0 0;
      flex-wrap: wrap;
    }
  }
  &__Details-inner {
    max-width: 250px;
    flex: 0 0 33.33%;
    @media screen and (max-width: 767.5px) {
      flex: 0 0 100%;
      max-width: 100%;
    }
  }
  &__Title {
    color: #ffffff;
    font-family: Interstate Cond;
    font-size: 16px;
    letter-spacing: 0.8px;
    line-height: 30.4px;
    text-align: center;
    margin: 0;
    text-transform: uppercase;
    @media screen and (max-width: 767.5px) {
        font-size: 16px;
        letter-spacing: 1px;
        line-height: 24px;
        padding: 0 0 10px 0;
    }
  }
  &__Link {
    color: #fdd174;
    font-family: Interstate Cond;
    font-size: 18px;
    letter-spacing: 1px;
    line-height: 32px;
    text-align: center;
    display: flex;
    justify-content: center;
    text-transform: uppercase;
    @media screen and (max-width: 767.5px) {
        font-size: 14px;
        letter-spacing: 0.9px;
        line-height: 25.2px;
    }
  }
}
