.hero {
  background: radial-gradient(#5f3ab8, #34157d);
  position: relative;

  @media (max-width: 599px) {
    background: radial-gradient(#5f3ab8, #34157d); 
  }

  &__container {
    @include make-container;
    
    align-items: center;
    display: flex;
    height: 790px;

    @media (max-width: 1024px) {
      align-items: flex-start;  
      flex-wrap: wrap;
      height: auto;  
    }

    @media (max-width: 599px) {
      justify-content: center;
    }
  }

  &__content {
    color: white;
    max-width: 490px;
    position: relative;
    z-index: 1;

    @media (max-width: 1024px) {
      flex: 0 0 100%;
      max-width: 100%;
      padding-top: 130px;
      text-align: center;
    }

    @media (max-width: 599px) {
      padding-top: 100px;  
    }
  }

  &__title {
    margin-bottom: 33px;

    @media (max-width: 599px) {
      margin-bottom: 25px;
    }
  }

  &__copy {
    margin-bottom: 25px;
    opacity: 0.6;

    @media (max-width: 599px) {
      margin-bottom: 16px;  
    }
  }

  &__image {
    position: absolute;
    right: 50px;
    bottom: 0;
    width: 70%;
    height: 100%;

    &-part-1 {
      background: url('part1v2.webp?v=3') center/contain no-repeat;
      position: absolute;
      top: 25%;
      left: 21%;
      width: 50%;
      height: 54%;
    }

    &-part-2 {
      background: url('part2.webp?v=3') center/contain no-repeat;
      position: absolute;
      top: 5%;
      left: 0%;
      width: 81%;
      height: 100%;
    }

    &-part-3 {
      background: url('part3.webp?v=3') center/contain no-repeat;
      position: absolute;
      top: 19%;
      left: 0%;
      width: 67.5%;
      height: 42.5%;
    }

    &-part-4 {
      background: url('part4.webp?v=3') center/contain no-repeat;
      position: absolute;
      top: 21%;
      left: 24%;
      width: 39%;
      height: 46%;
    }

    &-part-5 {
      background: url('part5.webp?v=3') center/contain no-repeat;
      position: absolute;
      top: 9%;
      left: 11%;
      width: 60.8%;
      height: 59%;
    }

    &-part-6 {
      background: url('part6.webp?v=3') center/contain no-repeat;
      position: absolute;
      top: 35%;
      left: 28%;
      width: 23.1%;
      height: 24%;
    }

    &-part-7 {
      background: url('px-bp-7.webp') center/contain no-repeat;
      position: absolute;
      top: 12%;
      left: 27%;
      width: 42.1%;
      height: 70.8%;
    }

    &-part-8 {
      background: url('px-bp-8.webp') center/contain no-repeat;
      position: absolute;
      bottom: 13%;
      left: 45%;
      width: 67px;
      height: 83px;
    }

    &-part-9 {
      background: url('px-bp-9.webp') center/contain no-repeat;
      position: absolute;
      top: 39%;
      left: 56%;
      width: 13.6%;
      height: 19.8%;
    }

    &-part-10 {
      background: url('px-bp-10.webp') center/contain no-repeat;
      position: absolute;
      top: 56%;
      right: 26%;
      width: 67px;
      height: 55px;
    }

    &-part-11 {
      background: url('px-bp-11.webp') center/contain no-repeat;
      position: absolute;
      top: 47%;
      left: 31%;
      width: 50px;
      height: 42px;
    }

    &-part-12 {
      background: url('px-bp-12.webp') center/contain no-repeat;
      position: absolute;
      top: 27%;
      right: 37%;
      width: 57px;
      height: 58px;
    }

    &-mobile {
      position: relative;
      bottom: -15px;
    }
  }
}