.tablet-display-true {
  @media (min-width: 1025px) {
    display: none !important;  
  }
}

.tablet-display-none {
  @media (max-width: 1024px) {
    display: none !important;  
  }
}

.mobile-display-true {
  @media (min-width: 600px) {
    display: none !important;
  }
}

.mobile-display-none {
  @media (max-width: 599px) {
    display: none !important;
  }
}

#your-shopping-cart {
  .site-header {
    @extend .site-header--yellow;
  }
}

.cart-page {
  padding-top: 60px;
  padding-bottom: 100px;

  @media (max-width: 1024px) {
    padding-top: 30px;
    padding-bottom: 75px;
  }

  &__container {
    @include make-container;
  }

  &__title {
    margin-bottom: 50px;
    text-align: center;

    @media (max-width: 1024px) {
      margin-bottom: 30px;  
    }
  }

  &__form {
    max-width: 1140px;
    margin: 0 auto;
    width: 100%;
  }

  .afterpay-paragraph {
    margin-bottom: 20px;
  }
}

.cart-header {
  align-items: center;
  display: flex;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0;
  line-height: 1.77;
  height: 60px;
  text-transform: uppercase;

  @media (max-width: 599px) {
    padding: 0;  
  }

  &__product {
    flex: 0 0 78.5%;

    @media (max-width: 1024px) {
      flex: 0 0 68%;  
    }
  }

  &__qty {
    flex: 0 0 80px;
  }

  &__price {
    flex: 0 0 60px;
    margin-left: auto;
    text-align: right;
  }
}

.line__item {
  border-top: 1px solid rgba(black, 0.3);
  display: flex;
  padding: 25px 0;

  @media (max-width: 599px) {
    padding: 23px 0;  
  }

  &-product {
    display: flex;
    flex: 0 0 78.5%;

    @media (max-width: 1024px) {
      flex: 0 0 68%;  
    }

    @media (max-width: 599px) {
      flex: 0 0 100%;  
    }
  }

  &-image {
    flex: 0 0 135px;
    object-fit: cover;
    width: 135px;
    height: 187px;

    @media (max-width: 1024px) {
      flex: 0 0 117px;
      width: 117px;
      height: 160px;
    }
  }

  &-content {
    flex: 1;
    padding-left: 30px;
  }

  &-title {
    display: block;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.77;
    margin-bottom: 13px;
    text-transform: uppercase;
  }

  &-options {
    margin-bottom: 13px;
    opacity: 0.6;
  }

  &-variant {
    font-size: 14px;
    line-height: 1.77;
  }

  &-removelink {
    display: block;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.77;
    text-transform: uppercase;

    @media (max-width: 1024px) {
      display: none;  
    }
  }

  &__qty {
    border: 1px solid rgba(black, 0.6);
    display: flex;
    flex: 0 0 88px;
    opacity: 0.6;
    width: 88px;
    height: 40px;

    &-selector {
      align-items: center;
      display: flex;
      flex: 0 0 24px;
      font-size: 14px;
      font-weight: 500;
      justify-content: center;

      &-minus {
        font-family: monospace;
      }
    } 

    &-input {
      background: transparent !important;
      border: 0 !important;
      flex: 0 0 23px;
      font-size: 12px;
      font-weight: 500;
      letter-spacing: 1px;
      outline: none;
      padding: 0 !important;
      text-align: center;
      width: 40px !important;
      height: auto !important;
      -moz-appearance: textfield;

      &::-webkit-outer-spin-button,
      &::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }
    }
  }

  &-price {
    flex: 0 0 60px;
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0.4px;
    margin-left: auto;
    text-align: right;

    @media (max-width: 599px) {
      flex: initial;
      font-size: 14px;
      margin-left: 0;
      margin-right: 10px;
      text-align: left;
    }
  }

  &:last-of-type {
    border-bottom: 1px solid rgba(black, 0.3);
  }
}

.cart-actions {
  padding-top: 50px;
  margin-left: auto;
  max-width: 355px;
  width: 100%;

  @media (max-width: 599px) {
    max-width: 100%;
    margin-left: 0;
  }
}

.cart-subtotal {
  display: flex;
  font-size: 14px;
  font-weight: 500;
  justify-content: space-between;
  line-height: 1.77;
  margin-bottom: 6px;
  text-transform: uppercase;

  &-text {
    align-items: center;
    display: flex;
  }

  i {
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    margin-left: 7px;
  }

  &-unit {
    font-weight: 400;
  }
}

.cart-subtotal--final {
  margin-top: 15px;
  margin-bottom: 30px;
}

.cart-actionslist__submit {
  margin-bottom: 20px;
}

.sales-note {
  font-size: 14px;
  line-height: 1.77;
}