.HighlightedBenefits{
    background: #54349a;
    padding: 126px 0 100px 0;
    position: relative;
    &::after {
        content: "";
        position: absolute;
        top: -14px;
        left: 0;
        width: 100%;
        height: 68px;
        z-index: 0;
        background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/BottomLine_52339b.svg?v=1662028205);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center top;
        @media screen and (max-width: 767.5px) {
            content: "";
            position: absolute;
            top: -22px;
            left: 0;
            width: 100%;
            height: 68px;
            z-index: 0;
            background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/Mobile-Border-Illustration.svg?v=1661237073);
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center top;
          }
    }
    &::before{
        content: "";
        position: absolute;
        bottom: -60px;
        left: 0;
        width: 100%;
        height: 96px;
        z-index: 0;
        background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/Mobile-Border-Illustration.svg?v=1661237073);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center bottom;
        transform: rotateY(180deg);
        @media screen and (max-width: 767.5px) {
        transform: rotateY(0deg);
        }
    }
    @media screen and (max-width: 767.5px) {
        padding: 48px 0 42px 0 !important;
    }
    &__Container{
        margin: 0 auto;
        @media screen and (max-width: 319px) {
            padding: 0 15px;
        }
        @media screen and (min-width: 320px) {
            max-width: 290px;
        }
        @media screen and (min-width: 575px) {
            max-width: 480px;
        }
        @media screen and (min-width: 600px) {
            max-width: 540px;
        }
        @media screen and (min-width: 768px) {
            max-width: 690px;
        }
        @media screen and (min-width: 992px) {
            max-width: 960px;
        }
        @media screen and (min-width: 1280px) {
            max-width: 1140px;
        }
        @media screen and (min-width: 1440px) {
            max-width: 1240px;
        }
    }
    &__Head{
        color: #FFFFFF;
        font-family: "Gazpacho";
        font-size: 50px;
        font-weight: bold;
        letter-spacing: 2.5px;
        line-height: 75px;
        text-align: center;
        margin: 0 auto;
        padding: 0 0 40px 0;
        position: relative;
        max-width: fit-content;
        @media screen and (max-width: 767.5px) {
            font-size: 25px;
            letter-spacing: 1px;
            line-height: 37.5px;
            padding: 0 0 15px 0;
        }
        &::after{
            content: "";
            position: absolute;
            width: 60px;
            height: 60px;
            background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/start_group_yellow.png?v=1661160183);
            background-repeat: no-repeat;
            background-size: contain;
            top: -13px;
            right: -54px;
            @media screen and (max-width: 575.5px) {
                width: 32px;
                height: 32px;
                right: -22px;
            }
        }
        &::before{
            content: "";
            position: absolute;
            width: 60px;
            height: 60px;
            background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/star_group.png?v=1661160183);
            background-repeat: no-repeat;
            background-size: contain;
            top: 6px;
            left: -60px;
            @media screen and (max-width: 575.5px) {
                width: 32px;
                height: 32px;
                left: -21px;
                top: -12px;
                transform: rotate(35deg);
            }
        }
    }
    &__Layout-main{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        @media screen and (max-width: 991.5px) {
            row-gap: 50px;
        }
        @media screen and (max-width: 767.5px) {
            row-gap: 30px;
        }
    }
    &__Layout-inner{
        flex: 0 0 25%;
        max-width: 25%;
        @media screen and (max-width: 991.5px) {
            flex: 0 0 50%;
            max-width: 50%;
        }
        @media screen and (max-width: 767.5px) {
            flex: 0 0 100%;
            max-width: 100%;
        }
    }
    &__Icon{
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 0 20px 0;
        @media screen and (max-width: 767.5px) {
           padding: 0 0 18px 0;
        }
    }
    &__Dollor{
        width: 80px;
        height: 80px;
        object-fit: contain;
        @media screen and (max-width: 767.5px) {
            width: 60px;
            height: 60px;
        }
    }
    &__Details{
        margin: 0 auto;
        max-width: 221px;
        width: 100%;
    }
    &__Title{
        color: #FFFFFF;
        font-family: "Gazpacho";
        font-size: 23px;
        font-weight: bold;
        letter-spacing: 0.92px;
        line-height: 31px;
        text-align: center;
        margin: 0;
        padding: 0 0 20px 0;
        @media screen and (max-width: 767.5px) {
            font-size: 16px;
            letter-spacing: 0.3px;
            line-height: 27.2px;
            padding: 0 0 7px 0;
        }
    }
    &__Content{
        color: #FFFFFF;
        font-family: Interstate Cond;
        font-size: 16px;
        letter-spacing: 0.5px;
        line-height: 30px;
        text-align: center;
        margin: 0;
    }
}