#addresses {
  .site-header {
    @extend .site-header--yellow;
  }
}

.address-book {
  &__link {
    margin-bottom: 30px;
  }

  &__subtitle {
    font-size: 18px;
    font-weight: 700;
    line-height: 1.5;
    margin-bottom: 20px;
    text-transform: uppercase;
  }
}

.add-new-address,
.edit-address {
  display: none;
  margin-top: 30px;

  input[type=submit] {
    margin-top: 30px;
  }

  &--active {
    display: block;
  }
}

.form-fields__checkbox {
  align-items: center;
  display: flex;
}

.form-fields__item {
  margin-bottom: 20px;

  select {
    border: 1px solid #AAAAAA;
    background-color: transparent;
    opacity: 0.72;
    width: 100%;
    height: 48px;
  }
}

.form-fields__group {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 15px;
  margin-bottom: 20px;

  @media (max-width: 599px) {
    display: block;
    margin-bottom: 0;  
  }

  .form-fields__item {
    margin-bottom: 0;

    @media (max-width: 599px) {
      margin-bottom: 20px;  
    }
  }
}

.addressBlocks_single {
  border-bottom: 1px solid rgba(0,0,0,0.3);
  padding: 30px 0;

  &:first-of-type {
    padding-top: 0;
  }

  &:last-of-type {
    border: 0;
  }
}

.addressBlocks_single-action {
  display: flex;
  margin-top: 20px;
}

.address-info {
  button:not(.btn) {
    background: none;
    border: 0;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.3;
    outline: none;
    padding: 0;
    text-transform: uppercase;

    &:first-of-type {
      border-right: 1px solid rgba(black, 0.3);
      margin-right: 14px;
      padding: 0 14px 0 0;
    }
  }

  .btn {
    width: 250px;
  }

  input,
  select {
    text-transform: uppercase;
  }
}

.new-address {
  padding-top: 25px;

  button {
    border-right: 0 !important;
    padding-right: 0;
  }
}

.form-fields__footer {
  input[type=checkbox] {
    border-radius: 0;
    flex: 0 0 20px;
    margin-right: 12px;
    position: relative;
    top: 2px;
    width: 20px;
    height: 20px;
  }
  
  label {
    font-size: 14px;
    line-height: 25px;
  }

  .form-fields__buttonCover {
    align-items: center;
    display: flex;

    @media (max-width: 599px) {
      display: flex;
      justify-content: space-between;
      margin-top: 40px;
    }

    span {
      margin-top: 30px;
      margin-left: 20px;
    }
  }

  .btn {
    @media (max-width: 599px) {
      margin-top: 0 !important;
      width: 170px;
    }
  }
}