.product__list {
  display: grid;
  gap: 100px 28px;
  grid-template-columns: repeat(3, minmax(0, 1fr));

  @media (max-width: 1024px) {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 60px 20px;
  }

  @media (max-width: 599px) {
    grid-template-columns: repeat(1, minmax(0, 1fr));  
    padding: 0 20px;
  }

  &[data-grid="1"] {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }

  &[data-grid="2"] {
    grid-template-columns: repeat(2, minmax(0, 1fr));

    .grid-item__size {
      //padding: 0 10px;

      &-item {
        font-size: 11px;
      }
    }
  }

  &[data-grid="4"] {
    grid-template-columns: repeat(4, minmax(0, 1fr));

    @media (max-width: 1024px) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }

    .grid-item__size {
      //padding: 0 10px;

      &-item {
        font-size: 11px;
      }
    }

    .smartwishlist {
      font-size: 18px !important;
      top: 14px;
      right: 14px;
    }
  }

  &[data-grid="5"] {
    grid-template-columns: repeat(5, minmax(0, 1fr));

    .grid-item__size {
      //padding: 0 10px;

      &-item {
        font-size: 11px;
        padding: 2px 4px;
      }
    }

    .smartwishlist {
      font-size: 18px !important;
      top: 14px;
      right: 14px;
    }
  }
}