.external-news {
  padding-top: 60px;
  padding-bottom: 100px;

  @media (max-width: 1024px) {
    padding-top: 40px;  
  }

  &__container {
    @media (min-width: 1025px) {
      padding: 0 50px;  
    }
  }

  &__list {
    &-item {
      $self: &;
      border-bottom: 1px solid rgba(black, 0.4);
      display: flex;
      flex-wrap: wrap;
      padding: 40px 0;

      @media (max-width: 1024px) {
        padding: 30px 0;  
      }

      &-image-wrapper {
        border-radius: 5px;
        flex: 0 0 310px;
        overflow: hidden;
        position: relative;
        height: 240px;

        @media (max-width: 1024px) {
          flex: 0 0 206px;
          height: 160px;
        }

        @media (max-width: 599px) {
          flex: 0 0 100%;  
          margin-bottom: 15px;
          height: 230px;
        }
      }

      &-image {
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
        transition: all 0.3s $cubic;
        width: 100%;
        height: 100%;
      }

      &-content {
        display: flex;
        flex-direction: column;
        flex: 1;
        justify-content: center;
        padding-left: 40px;

        @media (max-width: 1024px) {
          padding-left: 30px;  
        }

        @media (max-width: 599px) {
          flex: 0 0 100%;  
          padding-left: 0;
        }
      }

      &-date {
        font-family: 'Interstate Cond';
        font-size: 16px;
        font-weight: 300;
        line-height: 29px;
        letter-spacing: 0.5px;
        margin-bottom: 15px;

        @media (max-width: 1024px) {
          font-size: 14px;
          line-height: 26px;
        }
      }

      &-title {
        transition: all 0.3s $cubic;
      }

      &:hover {
        #{$self} {
          &-image {
            transform: scale(1.1);
          }

          &-title {
            color: #7A7A7A;
          }
        }
      }

      &:last-child {
        border-bottom: 0;
      }
    }
  }
}