.external-news-featured {
  padding-top: 100px;

  @media (max-width: 1024px) {
    padding-top: 0;  
  }

  &__container {
    align-items: flex-start;
    display: flex;
    flex-wrap: wrap;

    @media (min-width: 1025px) {
      padding: 0 50px;  
    }

    @media (max-width: 599px) {
      padding: 0;  
    }
  }

  &__image-wrapper {
    border-radius: 10px;
    flex: 0 0 55%;
    overflow: hidden;
    position: relative;

    @media (max-width: 1180px) {
      flex: 0 0 40%;
    }

    @media (max-width: 599px) {
      border-radius: 0;
      flex: 0 0 100%;  
      margin-bottom: 30px;
    }

    &::after {
      content: "";
      display: block;
      padding-bottom: 89.3%;
    }
  }

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  &__content {
    align-items: flex-start;
    align-self: center;
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: center;
    padding-left: 8%;

    @media (max-width: 1024px) {
      padding-left: 30px;  
    }

    @media (max-width: 599px) {
      align-items: center;
      align-self: initial;
      padding: 0 20px;
      text-align: center;
    }
  }

  &__date {
    font-family: 'Interstate Cond';
    font-size: 16px;
    font-weight: 300;
    line-height: 29px;
    letter-spacing: 0.5px;
    margin-bottom: 15px;

    @media (max-width: 1024px) {
      font-size: 14px;
      line-height: 26px;
    }
  }

  &__title {
    margin-bottom: 20px;
    transition: all 0.3s $cubic;

    @media (max-width: 1024px) {
      margin-bottom: 15px;  
    }
  }

  &__copy {
    font-family: 'Interstate Cond';
    font-size: 18px;
    font-weight: 300;
    line-height: 34px;
    letter-spacing: 0.7px;
    margin-bottom: 40px;

    @media (max-width: 1024px) {
      font-size: 16px;
      line-height: 30px;
      letter-spacing: 0.5px;
      margin-bottom: 30px;
    }
  }
}