.AffiliateRight {
  padding: 100px 0 100px 0;
  background: #fffaed;
  @media screen and (max-width: 767.5px) {
    padding: 70px 0 70px 0;
  }
  &__Container {
    @include make-container;
  }
  &__Layout-main {
    display: flex;
    flex-wrap: wrap;
    column-gap: 100px;
    align-items: center;
    @media screen and (max-width: 1279.5px) {
        column-gap: 50px;
    }
    @media screen and (max-width: 991.5px) {
      column-gap: 0;
      row-gap: 50px;
      flex-direction: column-reverse;
    }
  }
  &__Layout-inner {
    flex: 0 0 50%;
    max-width: calc(50% - 50px);
    @media screen and (max-width: 1279.5px) {
        max-width: calc(50% - 25px);
    }
    @media screen and (max-width: 991.5px) {
      flex: 0 0 100%;
      max-width: 100%;
      width: 100%;
    }
  }
  &__Head {
    color: #000000;
    font-family: "Gazpacho";
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 2.5px;
    line-height: 75px;
    margin: 0;
    max-width: fit-content;
    position: relative;
    padding: 0 0 15px 0;
    @media screen and (max-width: 767.5px) {
      margin: 0 auto;
      font-size: 25px;
      letter-spacing: 1px;
      line-height: 37.5px;
      text-align: center;
    }
    &::after {
      position: absolute;
      content: "";
      width: 50px;
      height: 50px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/start_group_blue.png?v=1661160183);
      background-repeat: no-repeat;
      background-size: contain;
      top: -18px;
      right: -29px;
      transform: rotate(160deg);
      @media screen and (max-width: 767.5px) {
        width: 30px;
        right: -16px;
        height: 30px;
        top: -10px;
      }
    }
  }
  &__Content {
    color: #000000;
    font-family: Interstate Cond;
    font-size: 18px;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    margin: 0;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
      text-align: center;
      margin: 0 auto;
    }
  }
  &__Image {
    width: 100%;
    height: auto;
    object-fit: cover;
    max-height: 450px;
    border-radius: 10px;
  }
}
