.AnchorLinks{
    background: $Color--gray;
    position: sticky;
    top: 0;
    padding: 100px 0 5px 0;
    z-index: 1;

    @media screen and (max-width: 767.5px) {
        padding: 70px 0 0 0;
    }
    background-color: #fffaed;
    &__Container {
        @include make-container;
    }
    &__List{
        display: flex;
        justify-content: space-between;
        @media screen and (max-width: 1024px) {
            flex-direction: column;
            display: none;
        }
    }
    &__List-item{
        @media screen and (max-width: 1024px) {
            margin: 20px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    &__List-title{
        color: #000000;
        font-family: Interstate;
        font-size: 16px;
        font-weight: 600;
        display: block;
        letter-spacing: 0.8px;
        line-height: 30.4px;
        text-align: center;
        border-bottom: 1px solid transparent;
        text-transform: uppercase;
        transition: all 0.3s $cubic;
        @media (min-width: 1025px) {
            opacity: 0.4;
        }
        @media screen and (max-width: 991px) {
            font-size: 14px;
            line-height: 20px;
            display: block;
            border-bottom: unset;
            width: 100%;
        }
        @media screen and (max-width: 767.5px) {
            font-size: 13px;
        }

        &--active {
            @media (min-width: 1025px) {
                border-bottom: 1px solid black;
                opacity: 1;
            }
        }
    }
    &__Dropdown{
        display: none;
        margin: 0 auto;
        max-width: 100%;
        @media screen and (max-width: 1024px) {
            display: block;
        }
    }
    &__Dropdown-btn {
        position: relative;
        max-width: 100%;
        width: 100%;
        text-transform: uppercase;
        border: 2px solid #000000;
        border-radius: 5px;
        background-color: #FDD174;
        height: 60px;
        letter-spacing: 0.8px;
        font-weight: 700;

        &::before {
            content: '';
            position: absolute;
            right: 15px;
            top: 50%;
            transform: translateY(-40%);
            width: 0;
            height: 0;
            border: 6px solid;
            border-color: #000000 transparent transparent;
        }
    }
}

