#account,
#create-account {
  .site-header {
    @extend .site-header--yellow;
  }
}

.my-account {
  min-height: 400px;
  padding-top: 100px;
  padding-bottom: 100px;

  @media (max-width: 1024px) {
    padding-bottom: 75px;
  }

  &__container {
    @include make-container;
  }

  &__wrapper {
    max-width: 1140px;
    margin: 0 auto;
  }

  &__title {
    border-bottom: 1px solid rgba(black, 0.3);
    font-size: 18px;
    line-height: 1.5;
    margin-bottom: 50px;
    padding-bottom: 20px;
    text-transform: uppercase;

    @media (max-width: 1024px) {
      margin-bottom: 15px;  
    }
  }

  &__sidebar {
    flex: 0 0 292px;
    padding-right: 80px;

    @media (max-width: 1024px) {
      flex: 0 0 100%;
      padding-right: 0;
    }

    > ul {
      @media (max-width: 1024px) {
        display: none;  
      }
    }

    li {
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    a {
      color: rgba(black, 0.5);
      font-size: 18px;
      font-weight: 700;
      line-height: 1.5;
      letter-spacing: 0;
      text-transform: uppercase;
      transition: all 0.3s $cubic;

      &.active,
      &:hover {
        color: black;
      }
    }

    .accordion__list {
      display: none;

      @media (max-width: 1024px) {
        display: block; 
        margin-bottom: 50px;
      }
    }

    .accordion__item {
      border: 0;
    }

    .accordion__paragraph,
    .accordion__body a {
      font-size: 14px;
      font-weight: 700;
      line-height: 1.77;
      text-transform: uppercase;
    }

    .accordion__body ul {
      margin-top: 10px;
    }
  }

  &__content {
    flex: 1;
    min-width: 0;

    @media (max-width: 1024px) {
      flex: 0 0 100%;  
    }
  }

  &__subtitle {
    border-bottom: 1px solid rgba(black, 0.3);
    font-size: 18px;
    font-weight: 700;
    line-height: 1.5;
    margin-bottom: 50px;
    padding-bottom: 20px;
    text-transform: uppercase;

    @media (max-width: 1024px) {
      margin-bottom: 30px;  
    }
  }

  &__table {
    width: 100%;

    th {
      font-size: 18px;
      font-weight: 700;
      line-height: 1.5;
      text-transform: uppercase;
      text-align: left;
    }
  }

  .accordion__title {
    padding-left: 0;
    padding-right: 0;
  }
}

.order-header {
  font-size: 18px;
  font-weight: 700;
  display: flex;
  justify-content: space-between;
  line-height: 1.5;
  letter-spacing: 0;
  margin-bottom: 8px;
  padding-bottom: 20px;
  padding: 0 90px 0 30px;
  text-transform: uppercase;

  @media (max-width: 1100px) {
    font-size: 16px;
  }

  @media (max-width: 1024px) {
    display: none;  
  }
}

.oh {
  &-date {
    flex: 0 0 150px;
  }

  &-order-number {
    flex: 0 0 142px;

    @media (max-width: 1024px) {
      flex: 1;
      text-align: right;
    }
  }

  &-status {
    flex: 0 0 120px;
  }

  &-tracking {
    flex: 0 0 172px;
  }
}

.order-body {
  border-left: 1px solid rgba(black, 0.3);
  border-right: 1px solid rgba(black, 0.3);

  .accordion__item {
    padding: 0 30px;

    @media (max-width: 1024px) {
      padding: 0 20px;  
    }

    &--active {
      .accordion__paragraph {
        @media (max-width: 1024px) {
          opacity: 0;
        }
      }
    }
  }

  .accordion__paragraph {
    display: flex;
    justify-content: space-between;
    font-family: 'DM Mono';
    font-weight: 400;
    font-size: 16px;
    line-height: 1.77;
    opacity: 1;
    transition: all 0.3s $cubic;
    text-transform: capitalize;

    &.large-screen {
      @media (max-width: 1024px) {
        display: none;  
      }
    }

    &.mobile-screen {
      align-items: center;
      flex: 0 0 calc(100% - 25px);
      font-size: 14px;

      @media (min-width: 1025px) {
        display: none;
      }
    }
  }

  .date-status {
    > span {
      display: block;
    }
  }

  h2 {
    font-size: 18px;
    font-weight: 700;
    line-height: 1.5;
    margin: 30px 0;
    text-transform: uppercase;

    @media (max-width: 1024px) {
      font-size: 16px;
      margin: 50px 0 30px;  
    }
  }

  .accordion__body-text {
    @media (max-width: 1024px) {
      max-width: 100%; 
    }
  }

  .accordion__body-row {
    display: flex;
    max-width: 635px;
    padding-bottom: 45px;

    @media (max-width: 1024px) {
      display: block;
    }

    > * {
      flex: 0 0 33.3%;

      @media (max-width: 1024px) {
        & + * {
          margin-top: 10px;
        }  
      }
    }

    .col-payment,
    .col-address {
      padding-right: 50px;

      @media (max-width: 1024px) {
        padding-right: 0;
      }
    }

    table {
      width: 100%;
    }
  }

  h3 {
    font-size: 14px;
    font-weight: 700;
    line-height: 1.5;
    margin-bottom: 13px;
    text-transform: uppercase;

    @media (max-width: 1024px) {
      margin-bottom: 2px;
    }
  }

  h4 {
    font-size: 14px;
    line-height: 1.5;

    & + p {
      font-size: 14px;
      margin-bottom: 11px;
    }
  }

  .line__item {
    @media (max-width: 1024px) {
      flex-wrap: wrap;  

      &-content {
        padding-left: 10px;
      }
    }

    &:last-child {
      border-bottom: 0;
    }

    &-shipping {
      flex: 0 0 150px;

      @media (max-width: 1024px) {
        flex: 0 0 100%;
        margin-top: 30px;
      }

      p {
        margin-bottom: 12px;

        @media (max-width: 1024px) {
          margin-bottom: 5px;
        }
      }
      
      span {
        font-size: 14px;
        display: block;
        line-height: 1.77;
        text-transform: capitalize;
        margin-bottom: 27px;

        @media (max-width: 1024px) {
          margin-bottom: 10px;
        }
      }
    }
  }
}