.BlogBanner {
  @media screen and (max-width: 767.5px) {
    //padding: 54px 0 0 0;
  }
  &__Container {
    max-width: 100%;
    margin: 0 auto;
  }
  &__Layout-main {
    display: flex;
    flex-wrap: wrap;
  }
  &__Layout-inner {
    max-width: 46.4%;
    width: 46.4%;
    @media screen and (max-width: 991.5px) {
      max-width: 50%;
      width: 50%;
    }
    @media screen and (max-width: 767.5px) {
      max-width: 100%;
      width: 100%;
    }
  }
  &__ImageSide {
    max-width: 53.6%;
    width: 53.6%;
    @media screen and (max-width: 991.5px) {
      max-width: 50%;
      width: 50%;
    }
    @media screen and (max-width: 767.5px) {
      max-width: 100%;
      width: 100%;
      order: -1;
    }
  }
  &__LeftContent {
    padding: 88px 100px;
    background: #52339b;
    @media screen and (max-width: 1199.5px) {
      padding: 50px;
    }
    @media screen and (max-width: 991.5px) {
      padding: 30px;
    }
    @media screen and (max-width: 767.5px) {
      padding: 68px 15px 70px 15px;
    }
  }
  &__DateWrap {
    display: flex;
    align-items: center;
    @media screen and (max-width: 767.5px) {
      justify-content: center;
    }
  }
  &__Date {
    color: #ffffff;
    font-family: Interstate Cond;
    font-size: 16px;
    letter-spacing: 0.4px;
    line-height: 30px;
    margin: 0;
  }
  &__Name {
    color: #ffffff;
    font-family: Interstate Cond;
    font-size: 16px;
    letter-spacing: 0.4px;
    line-height: 30px;
    margin: 0;
    span {
      margin: 0 5px;
    }
  }
  &__Head {
    color: #ffffff;
    font-family: "Gazpacho";
    font-size: 38px;
    font-weight: bold;
    letter-spacing: 1px;
    line-height: 49.4px;
    margin: 0;
    padding: 15px 0 20px 0;
    @media screen and (max-width: 991.5px) {
      font-size: 32px;
      line-height: 40.4px;
      padding: 10px 0;
    }
    @media screen and (max-width: 767.5px) {
      font-size: 25px;
      letter-spacing: 1px;
      line-height: 37.5px;
      padding: 15px 0;
      text-align: center;
      margin: 0 auto;
    }
  }
  &__Details {
    color: #ffffff;
    font-family: Interstate Cond;
    font-size: 18px;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    margin: 0;
    padding: 0 0 40px 0;
    @media screen and (max-width: 991.5px) {
      padding: 0 0 20px 0;
    }
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
      text-align: center;
      padding: 0 0 30px 0;
    }
  }
  &__RightImage {
    width: 100%;
    height: 100%;
    position: relative;
  }
  &__ImageWrap {
    height: 100%;
    width: 100%;
  }
  &__Image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    @media screen and (max-width: 767.5px) {
      position: relative;
      max-height: 450px;
      display: block;
    }
  }
  &__BrandBtnWrap {
    position: absolute;
    top: 16px;
    left: 16px;
    z-index: 9;
    height: 29px;
    width: 69px;
    border: 1px solid #000000;
    border-radius: 2px;
    background-color: #52339b;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__BrandButton {
    color: #ffffff;
    font-family: Interstate;
    font-size: 16px;
    letter-spacing: 0.5px;
    line-height: 30px;
    text-align: center;
    @media screen and (max-width: 767.5px) {
        line-height: 25.2px;
        letter-spacing: 0.9;
    }
  }
  &__ButtonWrap {
    @media screen and (max-width: 767.5px) {
      display: flex;
      justify-content: center;
    }
  }
  &__Button {
    width: 180px;
    border-radius: 5px;
    @media screen and (max-width: 767.5px) {
        font-size: 12px;
        letter-spacing: 0.5px;
        line-height: 22px;
        width: 157px;
        height: 42px;
    }
  }
}
