.grid-item {
  $self: &;

  &__inner {
    display: flex;
    flex-direction: column;
    position: relative;
    height: 100%;
  }

  &__link {
    border-radius: 5px;
    display: block;
    overflow: hidden;
    position: relative;
    width: 100%;

    &::before {
      background: rgba(black, 0.2);
      content: "";
      display: block;
      pointer-events: none;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
    }

    &::after {
      content: "";
      display: block;
      padding-bottom: 112.2%;
    }
  }

  &__top {
    position: relative;
  }

  &__image {
    display: block;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    transition: all 0.4s $cubic;
    width: 100%;
    height: 100%;

    &-hover {
      object-fit: cover;
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
      transition: all 0.4s $cubic;
      width: 100%;
      height: 100%;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding: 15px 0 0;
    text-align: center;
  }

  &__title {
    @extend h4;

    font-family: 'Interstate Cond';
    margin-bottom: 30px;
    padding: 0 15px;
  }

  &__btn {
    margin-top: auto;
    
    &-separator {
      margin: 0 5px;
    }
  }

  &__copy {
    text-transform: capitalize;
  }

  &:hover {
    //Desktop
    @media (min-width: 1025px) {
      #{$self}__image {
        opacity: 0;
      }  

      #{$self}__image-hover {
        opacity: 1;
      }  
    }

    .subs__radio-wrapper--grid {
      bottom: 15px;
      opacity: 1;
      visibility: visible;
    }
  }

  &--gray {
    .grid-item__content {
      background: $Color--gray;
    }
  }
}