.Error404 {
  padding: 226px 0 212px 0;
  display: block;
  position: relative;
  background: #52339b;
  @media screen and (max-width: 767.5px) {
    padding: 128px 0 74px 0;
  }
  &::after {
    content: "";
    position: absolute;
    bottom: -46px;
    left: 0;
    width: 100%;
    height: 70px;
    z-index: 0;
    background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/BackgroundLine_52339b.svg?v=1662031835);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
  }
  &__Container {
    margin: 0 auto;
    @media screen and (max-width: 319px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 320px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 575px) {
      max-width: 480px;
      padding: 0;
    }
    @media screen and (min-width: 600px) {
      max-width: 540px;
    }
  }
  &__Head {
    color: #ffffff;
    font-family: "Gazpacho";
    font-size: 65px;
    font-weight: bold;
    letter-spacing: 1.8px;
    line-height: 84.5px;
    text-align: center;
    margin: 0 auto;
    padding: 0 0 16px 0;
    max-width: fit-content;
    position: relative;
    @media screen and (max-width: 767.5px) {
        font-size: 40px;
        letter-spacing: 0.9px;
        line-height: 44px;
        padding: 0 0 14px 0;
    }
    &::before {
      content: "";
      position: absolute;
      width: 45px;
      height: 45px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/start_group_yellow.png?v=1661160183);
      background-repeat: no-repeat;
      background-size: contain;
      top: -14px;
      left: -35px;
    }
    &::after {
      content: "";
      position: absolute;
      width: 45px;
      height: 60px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/star_group.png?v=1661160183 );
      background-repeat: no-repeat;
      background-size: contain;
      top: -10px;
      right: -44px;
      @media screen and (max-width: 767.5px) {
        top: -26px;
        right: -37px;
      }
    }
  }
  &__Details {
    color: #ffffff;
    font-family: Interstate Cond;
    font-size: 18px;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    text-align: center;
    @media screen and (max-width: 767.5px) {
        font-size: 16px;
        letter-spacing: 0.5px;
        line-height: 30.4px;
    }
  }
  &__ButtonWrap {
    display: flex;
    justify-content: center;
    padding: 40px 0 0 0;
    @media screen and (max-width: 767.5px) {
        display: none;
    }
  }
  &__Button{
    color: #000000;
    font-family: Interstate;
    font-size: 14px;
    letter-spacing: 0.88px;
    line-height: 30px;
    text-align: center;
    padding: 13px 22px 10px;
    width: auto;
    height: 52px;
  }
}
