.hero-s {
  position: relative;

  &__container {
    align-items: center;
    display: flex;
    height: 790px;

    @media (max-width: 1024px) {
      align-items: flex-start;  
      flex-wrap: wrap;
      padding-bottom: 100px;
      height: auto;  
    }

    @media (max-width: 599px) {
      justify-content: center;
    }
  }

  &__content {
    color: white;
    max-width: 490px;
    position: relative;
    z-index: 1; 

    @media (max-width: 1024px) {
      flex: 0 0 100%;
      max-width: 100%;
      padding-top: 150px;
      text-align: center;
    }

    @media (max-width: 599px) {
      padding-top: 130px;  
    }
  }

  &__title {
    margin-bottom: 33px;

    @media (max-width: 599px) {
      margin-bottom: 25px;
    }
  }

  &__copy {
    margin-bottom: 25px;
    opacity: 0.6;

    @media (max-width: 599px) {
      margin-bottom: 16px;  
    }
  }

  &__image-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__image {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}