.GlutenFree {
  padding: 80px 0 110px 0;
  background: #52339b;
  position: relative;
  @media screen and (max-width: 991.5px) {
    padding: 80px 0 60px 0;
  }
  @media screen and (max-width: 767.5px) {
    padding: 40px 0 50px 0;
  }
  &:after {
    content: "";
    position: absolute;
    top: -14px;
    left: 0;
    width: 100%;
    height: 68px;
    z-index: 0;
    background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/BottomLine_52339b.svg?v=1662028205);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
    @media screen and (max-width: 767.5px) {
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/Line_52339b.svg?v=1662033115);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center top;
    }
  }
  &:before {
    content: "";
    position: absolute;
    bottom: -46px;
    left: 0;
    width: 100%;
    height: 70px;
    z-index: 0;
    background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/BackgroundLine_52339b.svg?v=1662031835);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
    @media screen and (max-width: 767.5px) {
      height: 50px;
    }
  }
  &__Container {
    margin: 0 auto;
    @media screen and (max-width: 319px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 320px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 575px) {
      max-width: 480px;
      padding: 0;
    }
    @media screen and (min-width: 600px) {
      max-width: 540px;
    }
    @media screen and (min-width: 768px) {
      max-width: 690px;
    }
    @media screen and (min-width: 992px) {
      max-width: 960px;
    }
    @media screen and (min-width: 1280px) {
      max-width: 1140px;
    }
    @media screen and (min-width: 1440px) {
      max-width: 1240px;
    }
  }
  &__Layout-main {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    column-gap: 40px;
    position: relative;

    @media screen and (max-width: 1279.5px) {
      column-gap: 0px;
    }
    @media screen and (max-width: 991.5px) {
      column-gap: 30px;
    }
    @media screen and (max-width: 767.5px) {
      column-gap: 0;
    }
  }
  &__Layout-inner {
    flex: 0 0 calc(25% - 30px);
    max-width: calc(25% - 30px);
    &:hover {
      .GlutenFree__ImageWrap {
        transform: scale(1);
      }
    }
    @media screen and (max-width: 1279.5px) {
      max-width: calc(25% - 15px);
      flex: 0 0 calc(25% - 15px);
    }
    @media screen and (max-width: 991.5px) {
      flex: 0 0 calc(50% - 30px);
      max-width: calc(50% - 30px);
      margin: 0 0 50px 0;
    }
    @media screen and (max-width: 767.5px) {
      flex: 0 0 100%;
      max-width: 100%;
      margin: 0 0 30px 0;
    }
  }
  &__ImageWrap {
    max-width: 167px;
    max-height: 167px;
    background: #fdd174;
    border-radius: 50%;
    padding: 28px;
    margin: 0 auto;
    transition: 0.3s all ease-in-out;
    -webkit-transition: 0.3s all ease-in-out;
    -moz-transition: 0.3s all ease-in-out;
    transform: scale(0.825);
    @media screen and (max-width: 767.5px) {
      max-width: 135px;
      max-height: 135px;
      padding: 24px;
      transform: scale(0.89);
    }
  }
  &__Image {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  &__Title {
    color: #ffffff;
    font-family: "Gazpacho";
    font-size: 23px;
    font-weight: bold;
    letter-spacing: 0.5px;
    line-height: 35px;
    text-align: center;
    margin: 0 auto;
    padding: 10px 0 18px 0;
    @media screen and (max-width: 767.5px) {
      font-size: 18px;
      letter-spacing: 0.6px;
      line-height: 28.8px;
      padding: 15px 0 15px 0;
    }
  }
  &__Details {
    color: #ffffff;
    font-family: Interstate Cond;
    font-size: 18px;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    text-align: center;
    margin: 0 auto;
    text-align: center;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
    }
  }
}
