.ImgRight {
  padding: 126px 0 98px 0;
  @media screen and (max-width: 767.5px) {
    padding: 70px 0 106px 0;
  }
  &__Container {
    margin: 0 auto;
    @media screen and (max-width: 319px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 320px) {
      padding: 0 15px;
    }
    @media screen and (min-width: 575px) {
      max-width: 480px;
      padding: 0;
    }
    @media screen and (min-width: 600px) {
      max-width: 540px;
    }
    @media screen and (min-width: 768px) {
      max-width: 690px;
    }
    @media screen and (min-width: 992px) {
      max-width: 960px;
    }
    @media screen and (min-width: 1280px) {
      max-width: 1140px;
    }
    @media screen and (min-width: 1440px) {
      max-width: 1240px;
    }
  }
  &__Layout-main {
    display: flex;
    flex-wrap: wrap;
    column-gap: 50px;
    align-items: center;
    @media screen and (max-width: 991.5px) {
      column-gap: 0;
      row-gap: 50px;
    }
  }
  &__Layout-inner {
    flex: 0 0 50%;
    max-width: calc(50% - 25px);
    @media screen and (max-width: 991.5px) {
      flex: 0 0 100%;
      max-width: 100%;
    }
  }
  &__Head {
    padding: 0 0 20px 0;
    color: #000000;
    font-family: "Interstate Cond";
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 2.5px;
    line-height: 75px;
    margin: 0;
    //max-width: fit-content;
    position: relative;
    @media screen and (max-width: 767.5px) {
      padding: 0 0 15px 0;
      font-size: 32px;
      letter-spacing: 0.7px;
      line-height: 41.6px;
      text-align: center;
      margin: 0 auto;
    }
    &::after {
      position: absolute;
      content: "";
      width: 60px;
      height: 60px;
      background: url(https://cdn.shopify.com/s/files/1/1803/1651/files/start_group_blue.png?v=1661160183);
      background-repeat: no-repeat;
      background-size: contain;
      top: -18px;
      right: -40px;
      @media screen and (max-width: 767.5px) {
        width: 45px;
        right: -26px;
        height: 45px;
      }
    }
  }
  &__Content {
    color: #000000;
    font-family: Interstate Cond;
    font-size: 18px;
    letter-spacing: 0.7px;
    line-height: 34.2px;
    margin: 0;
    @media screen and (max-width: 767.5px) {
      font-size: 16px;
      letter-spacing: 0.5px;
      line-height: 30.4px;
      text-align: center;
      margin: 0 auto;
    }
  }
  .Space {
    margin: 0 0 20px 0;
  }
  &__Image {
    width: 100%;
    height: auto;
    object-fit: contain;
  }
}
