.template-article {
  background: $Color--gray;
}

.article {
  @include make-container;

  padding-top: 100px;
  padding-bottom: 100px;

  @media (max-width: 1024px) {
    padding-top: 50px;
    padding-bottom: 70px;
  }

  &__title {
    max-width: 75%;
    margin: 0 auto 20px;
    text-align: center;

    @media (max-width: 599px) {
      max-width: 100%;  
    }
  }

  &__meta {
    align-items: center;
    display: flex;
    font-family: 'Interstate Cond';
    font-size: 16px;
    font-weight: 300;
    line-height: 29px;
    letter-spacing: 0.5px;
    justify-content: center;
    margin-bottom: 40px;
    text-align: center;

    @media (max-width: 1024px) {
      margin-bottom: 30px;  
    }
  }

  &__share {
    background: $Color--yellow url('icon-share.png') center/16px 16px no-repeat;
    border-radius: 50%;
    border: 1px solid black;
    cursor: pointer;
    display: inline-block;
    margin-left: 20px;
    position: relative;
    width: 30px;
    height: 30px;

    &-content {
      display: block;
      pointer-events: none;
      position: absolute;
      left: 100%;
      width: 120px;
      height: 100%;
    }

    &-item {
      align-items: center;
      background: $Color--yellow;
      border-radius: 50%;
      border: 1px solid black;
      cursor: pointer;
      display: flex;
      justify-content: center;
      opacity: 0;
      pointer-events: none;
      position: absolute;
      top: 0;
      left: 0;
      transition: all 0.3s $cubic;
      width: 30px;
      height: 30px;
    }

    &:hover {
      overflow: visible;

      .article__share-content {
        pointer-events: all;
      }

      .article__share-item {
        left: 10px;
        opacity: 1;
        pointer-events: all;
        transition-delay: 0.2s;

        &:nth-child(2) {
          left: 50px;
          transition-delay: 0.1s;
        }

        &:nth-child(3) {
          left: 90px;
          transition-delay: 0s;
        }
      }
    }
  }

  &__image {
    border-radius: 10px;
    display: block;
    margin: 0 auto;
    max-width: 100%;
    height: auto;

    &-wrapper {
      margin: 0 auto 40px;
      max-width: 1024px;
      position: relative;
      text-align: center;

      @media (max-width: 1024px) {
        margin-bottom: 30px;  
      }
    }
  }

  &__tag {
    background: $Color--violet;
    border-radius: 2px;
    color: white;
    font-family: 'Interstate Cond';
    font-size: 16px;
    line-height: 30px;
    letter-spacing: 0.5px;
    padding: 0 14px;
    position: absolute;
    top: 15px;
    left: 15px;
    text-transform: capitalize;
  }

  &__copy {
    max-width: 944px;
    margin: 0 auto;

    iframe,
    img {
      display: block;
      float: none;
      margin-bottom: 20px;
      max-width: 100%;
    }

    h2 {
      margin-top: 30px;
      margin-bottom: 15px;
    }

    h3,
    h4,
    h5,
    h6 {
      margin-top: 30px;
      margin-bottom: 15px;
    }

    p {
      font-family: 'Interstate Cond';
      font-size: 18px;
      font-weight: 300;
      line-height: 34.2px;
      letter-spacing: 0.7px;

      @media (max-width: 1024px) {
        font-size: 16px;
        line-height: 30px;
        letter-spacing: 0.5px;
      }

      span {
        font-weight: 300 !important;
      }

      & + p {
        margin-top: 15px;
      }
    }

    ul {
      font-family: 'Interstate Cond';
      font-size: 18px;
      font-weight: 400;
      list-style-type: disc;
      list-style-position: inside;
      line-height: 34.2px;
      letter-spacing: 0.7px;
      margin: 15px 0;

      @media (max-width: 1024px) {
        font-size: 16px;
        line-height: 30px;
        letter-spacing: 0.5px;
      }

      p {
        display: inline;
      }
    }

    blockquote {
      background: url('icon-quote.png') center 30px/19px 17px no-repeat;
      border-top: 1px solid #DEDEDE;
      border-bottom: 1px solid #DEDEDE;
      margin: 30px 0;
      padding: 71px 0 36px;

      p {
        color: black;
        font-size: 26px;
        font-weight: 500;
        line-height: 38px;
      }

      &::before,
      &::after {
        display: none;
      }
    }
  }
}

.article__related {
  font-size: 32px;
  line-height: 40px;
  padding-top: 100px;
  margin: 0 auto;
  max-width: 610px;
  width: 100%;

  @media (max-width: 599px) {
    padding-top: 50px;  
  }

  &-title {
    margin-bottom: 26px;
    text-align: center;
  }
}


@import "./related-blogs.scss";
@import "./article-grid-item.scss";