.pagination {
  align-items: center;
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;

  @media (max-width: 767px) {
    justify-content: center;
  }

  &__prev,
  &__next {
    position: relative;
    width: 24px;
    height: 24px;

    &::before {
      border-color: black;
    }

    &--disabled {
      opacity: 0.3;
    }
  }

  &__prev {
    margin-right: 13px;
  }

  &__next {
    margin-left: 13px;
  }

  &__nums {
    &-list {
      display: flex;
    }

    &-item {
      color: rgba(black, 0.6);
      font-size: 14px;
      font-weight: 400;
      letter-spacing: 0;
      line-height: 20px;
      margin: 0;
      position: relative;
      text-align: center;
      width: 20px;
      height: 20px;

      & + * {
        margin-left: 3px;
      }

      span,
      a {
        color: inherit;
      }

      &--active {
        background: black;
        border-radius: 50%;
        color: white;
        text-align: center;
      }
    }
  }

  &__link {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all 0.3s $cubic;

    &:hover {
      color: primary;
    }
  }
}