.ajax-cart__items {
  margin-bottom: 20px;
}

.ajax-cart__item {
  display: flex;
  padding: 15px 0;

  &:first-child {
    padding-top: 0;
  }

  &-image {
    flex-shrink: 0;
    width: 200px;
    height: 193px;

    @media (max-width: 1024px) {
      width: 100px;
      height: 95px;
    }

    img {
      display: block;
      //filter: grayscale(1);
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }

  &-details {
    display: flex;
    flex-direction: column;
    flex: 1;
    padding-left: 30px;
  }

  &-title {
    font-family: "Interstate Cond";
    font-size: 18px;
    font-weight: 300;
    line-height: 34px;
    letter-spacing: 0.7px;
    margin-bottom: 10px;
    text-transform: capitalize;

    @media (max-width: 599px) {
      font-size: 16px;
      line-height: 27px;
    }
  }

  &-price {
    font-family: "Interstate Cond";
    font-size: 16px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0.5px;
    margin-bottom: 10px;
  }

  &-options {
    font-family: "Interstate Cond";
    font-size: 14px;
    font-weight: 300;
    line-height: 24px;
    letter-spacing: 0.5px;
  }

  &-option {
    color: #737373;

    & + * {
      margin-top: 4px;

      @media (max-width: 1024px) {
        margin-top: 4px;  
      }
    }
  }

  &-control {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin-top: auto;
    padding-top: 5px;
  }

  &-quantity-selector {
    align-items: center;
    background: #EFEFEF;
    border-radius: 3px;
    display: flex;
    width: 100px;
    height: 30px;
  }

  &-quantity-button {
    background: transparent;
    cursor: pointer;
    font-family: monospace;
    font-size: 15px;
    line-height: 28px;
    text-align: center;
    width: 40px;
    height: 100%;

    &.plus {
      position: relative;
      top: 1px;
    }
  }

  &-quantity-input {
    @include remove-number-style;

    background: transparent;
    border: 0 !important;
    color: black;
    font-size: 14px !important;
    line-height: 18px;
    outline: none;
    padding: 0 !important;
    text-align: center;
    width: 30px !important;
    height: 100% !important;
  }

  &-remove {
    color: #737373;
    cursor: pointer;
    font-family: "Interstate Cond";
    font-size: 14px;
    font-weight: 300;
    line-height: 24px;
    letter-spacing: 0.5px;
    text-decoration: underline;
  }
}