.product-slider {
  background: $Color--gray;
  overflow: hidden; 
  padding-top: 60px;
  padding-bottom: 40px;

  @media (max-width: 1024px) {
    padding-top: 36px;  
    padding-bottom: 0;
  }

  &__row {
    flex-wrap: wrap;
    padding-left: 100px;

    @media (max-width: 1024px) {
      padding-left: 20px;  
    }
  }

  &__info {
    flex: 0 0 100%;
    text-align: center;

    @media (max-width: 1024px) {
      padding-right: 20px;
    }
  }

  &__title {
    margin-bottom: 15px;
  }

  &__copy {
    font-family: 'Interstate Cond';
    margin: 0 auto 45px;
    max-width: 420px;

    @media (max-width: 1024px) {
      margin: 0 auto 30px;  
    }
  }

  &__gallery {
    flex: 0 0 100%;
    min-width: 0;
    overflow: hidden;
    padding-bottom: 40px;
    transition: all 0.3s $cubic;

    @media (max-width: 1024px) {
      flex: 0 0 100%;  
    }
  }

  &__track {
    display: flex;
  }

  &__item {
    flex: 0 0 360px;

    @media (max-width: 599px) {
      flex: 0 0 190px;  
      height: 190px;
    }
  }
}