.testimonial-slide-people {
  background: url(brush-yellow.svg) 0 100%/contain no-repeat,linear-gradient(180deg,$Color--yellow 0% calc(100% - 20px),#FFFAED 20px);
  overflow: hidden;
  position: relative;
  padding: 105px 0;

  @media (max-width: 1024px) {
    padding: 70px 0 105px;  
  }
}

.testimonial-slide-p {
  &__container {
    @include make-container;
  }

  &__inner {
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    padding: 0 50px;

    @media (max-width: 1024px) {
      padding: 0;  
    }
  }

  &__images {
    flex: 0 0 41.6%;
    overflow: hidden;
    position: relative;
    transition: all 0.4s $cubic;

    @media (max-width: 767px) {
      flex: 0 0 100%;  
    }

    &::before {
      content: "";
      display: block;
      padding-bottom: 90.6%;
    }
  }

  &__image {
    border: 4px solid $Color--violet;
    border-radius: 15px;
    object-fit: cover;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    transition: all 0.4s $cubic;
    width: 100%;
    height: 100%;

    &--active {
      opacity: 1;
    }
  }

  &__gallery {
    flex: 1;
    overflow: hidden;
    margin-left: 8.2%;
    position: relative;

    @media (max-width: 767px) {
      flex: 0 0 100%;
      margin-top: 30px;
      margin-left: 0;
    }
  }

  &__track {
    display: flex;
    width: 100%;
  }

  &__item {
    background: $Color--yellow;
    flex-shrink: 0;

    @media (max-width: 767px) {
      text-align: center;  
    }

    &-rating {
      background: url('stars-gray.svg') center/cover no-repeat;
      margin-bottom: 20px;
      overflow: hidden;
      position: relative;
      width: 82px;
      height: 14px;

      @media (max-width: 767px) {
        margin: 0 auto 30px;  
      }

      &::before {
        background: url('stars-black.svg') center/cover no-repeat;
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }

      &[data-rating="3"]::before {
        transform: translateX(-34px);
      }

      &[data-rating="4"]::before {
        transform: translateX(-17px);
      }
    }

    &-copy {
      font-family: 'Interstate Cond';
      font-size: 23px;
      line-height: 35px;
      letter-spacing: 1px;
      margin-bottom: 20px;
      
      @media (max-width: 1024px) {
        font-size: 14px;
        line-height: 25px;
        letter-spacing: 0.5px;
        margin-bottom: 15px;
      }
    }

    &-name {
      color: #333;
      font-family: 'Interstate';
      font-size: 12px;
      font-weight: 700;
      line-height: 22px;
      letter-spacing: 0.5px;
    }

    &-thumbs {
      display: flex;
      margin-top: 40px;

      @media (max-width: 1024px) {
        margin-top: 30px;  
      }

      @media (max-width: 767px) {
        justify-content: center;  
      }
    }

    &-thumb {
      border: 2px solid transparent;
      border-radius: 50%;
      cursor: pointer;
      opacity: 0.5;
      transition: all 0.4s $cubic;

      @media (max-width: 1024px) {
        width: 76px;
        height: 76px;
      }

      & +  * {
        margin-left: 38px;

        @media (max-width: 1024px) {
          margin-left: 30px;  
        }
      }

      &--active {
        border: 2px solid $Color--violet;
        opacity: 1;
      }
    }
  }
}