.testimonial-slider {
  background: $Color--yellow;
  overflow: hidden;
  padding-top: 82px;
  padding-bottom: 82px;

  &__container-custom {
    max-width: 1090px;
    margin: 0 auto;
    text-align: center;

    @media (max-width: 1160px) {
      max-width: calc(100% - 80px);
    }

    @media (max-width: 767px) {
      max-width: 100%;
    }
  }

  &__title {
    margin-bottom: 22px;
  }

  &__copy {
    background: white;
    border-radius: 10px;
    flex-shrink: 0;
    font-family: 'Interstate Cond';
    padding: 65px 150px;
    transition: all 0.4s $cubic;

    @media (max-width: 1024px) {
      font-size: 14px;
      line-height: 26px;
      letter-spacing: 0.5px;
      padding: 11px 15px;  
    }

    @media (max-width: 767px) {
      &::before {
        background: url('icon-pointer.png?v24') center/cover no-repeat;
        content: "";
        display: block;
        opacity: 1;
        position: absolute;
        bottom: -32px;
        left: 50%;
        transform: translateX(-50%);
        transition: all 0.4s $cubic;
        width: 44px;
        height: 32px;
      }
    }

    &-gallery {
      @media (max-width: 767px) {
        max-width: calc(100% - 40px);
        margin: 0 auto;
      }
    }

    &-track {
      display: flex;
      position: relative;
    }

    &-item {
      flex-shrink: 0;
    }
  }

  &__gallery {
    overflow: hidden;
  }

  &__track {
    display: flex;
    justify-content: space-around;
    position: relative;

    @media (max-width: 767px) {
      justify-content: flex-start;  
    }
  }

  &__item {
    align-items: center;
    cursor: pointer;
    display: flex;
    flex: 0 0 120px;
    padding-top: 70px;
    position: relative;
    height: 110px;

    @media (max-width: 1024px) {
      flex: 0 0 100px;  
    }

    &::before {
      background: url('icon-pointer.png?v24') center/cover no-repeat;
      content: "";
      display: block;
      opacity: 0;
      position: absolute;
      top: 0;
      left: 15%;
      transition: all 0.4s $cubic;
      width: 44px;
      height: 32px;

      @media (max-width: 767px) {
        display: none;  
      }
    }

    &.swiper-slide-thumb-active {
      &::before {
        opacity: 1;
      }
    }
  }
}